.spinner {
  composes: spin from '~core/styles/animations/spin.css';

  display: inline-block;
  border: solid 2px transparent;
  border-top-color: $primaryColor;
  border-left-color: $primaryColor;
  border-radius: 50%;
}

/*
 * Sizes
 */

.tiny {
  width: 12px;
  height: 12px;
}

.small {
  width: 16px;
  height: 16px;
}

.medium {
  width: 24px;
  height: 24px;
}

.large {
  width: 30px;
  height: 30px;
}
