.button {
  composes: sidebarMenuButton from '~core/components/sidebar/SidebarMenuButton.css';

  margin-top: 20px;
  margin-bottom: 10px;
  padding-right: $sidebarRightGutterWidth;
  padding-left: $sidebarLeftGutterWidth;
  height: 50px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  font-size: $defaultFontSize;
  font-family: $boldFontFamily;
  line-height: 50px;
}

.secondaryTitle {
  composes: sidebarMenuSecondaryTitle from '~core/components/sidebar/SidebarMenuButton.css';

  font-size: $smallFontSize;
}

.icon {
  top: 0;
  display: flex;
  margin-right: 10px;
  font-size: 13px;
}
