@import '~common/less/bootstrap/variables.less';
@import '~desktop/common/less/core/variables.less';

@lyrics-dropdown-padding: 12px;
@lyrics-dropdown-height: 380px;
@lyrics-controls-width: 400px - @lyrics-dropdown-padding * 2;
@lyrics-controls-height: 54px;
@lyrics-lines-height: @lyrics-dropdown-height - @lyrics-controls-height - @lyrics-dropdown-padding;

.lyrics-dropdown {
  width: 400px;
  height: @lyrics-dropdown-height;
  padding-bottom: @lyrics-dropdown-padding;
}

.lyrics-lines {
  padding: @lyrics-dropdown-padding @lyrics-dropdown-padding 0 @lyrics-dropdown-padding;
  height: @lyrics-lines-height;
  overflow-y: scroll;
}

.lyrics-span {
  &.highlight {
    color: white;
  }
}

.lyrics-line-blank {
  height: 20px
}

.lyrics-streams-region {
  position: absolute;
  bottom: 0;
  height: @lyrics-controls-height;
  width: @lyrics-controls-width;
}

.lyrics-streams {
  position: absolute;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;

  .btn-link {
    color: @gray-lighter;
    &[disabled]:hover {
      color: @gray-lighter;
    }
  }

  .vendor-logo {
    height: 6px;
    position: absolute;
    right: 12px;
    top: 50%;
    transform: translateY(-50%);
  }

  .vendor-logo-lyricfind {
    width: 62px;
    background-image: url(~common/img/vendor-logos/lyricfind.svg);
  }
}

.timed-btn {
  width: 22px;
  height: 22px;
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  padding: 0;
  border-radius: @border-radius-base;

  > i {
    font-size: 24px;
    margin-left: -1px;
    margin-top: 1px;
  }

  &.activated {
    color: @orange-light;
  }
}

.lyrics-error {
  text-align: center;
  width: 100%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.lyrics-loading {
  width: 100%;
  text-align: center;
  height: @lyrics-lines-height;
  transform: translateY(50%);
}
