//
// Setup
// --------------------------------------------------


// Base Styles
// ------------------------

.setup-loading-container {
  position: relative;
  height: 200px;

  .loading-title {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    color: @gray-light;
    text-align: center;
    font-size: @font-size-large + 2;
  }
}

.setup-progress {
  margin-bottom: 40px;
  font-size: 0;
}

.setupLoading > .setup-progress,
.setupLoading > h3,
.setupError > .setup-progress,
.setupError > h3 {
  display: none;
}

.setupBasic .library-segment > a,
.setupBasic .complete-segment > a,
.setupLibrary .basic-segment > a,
.setupLibrary .complete-segment > a,
.setupComplete .basic-segment > a,
.setupComplete .librarySegment > a {
  color: rgba(255, 255, 255, 0.3);
  &:focus {
    color: rgba(255, 255, 255, 0.7);
  }
}
.setupBasic .basic-segment > a,
.setupLibrary .library-segment > a,
.setupComplete .complete-segment > a {
  color: #fff;
  &:focus {
    color: #fff;
  }
}
.setupBasic .basic-segment > a,
.setupBasic .library-segment > a,
.setupBasic .complete-segment > a,
.setupLibrary .basic-segment > a,
.setupLibrary .library-segment > a,
.setupLibrary .complete-segment > a,
.setupComplete .basic-segment > a,
.setupComplete .library-segment > a,
.setupComplete .complete-segment > a {
  &:hover {
    color: rgba(255, 255, 255, 0.7);
  }
}

.setupBasic .basic-segment,
.setupLibrary .basic-segment,
.setupLibrary .library-segment,
.setupComplete .basic-segment,
.setupComplete .library-segment,
.setupComplete .complete-segment {
  border-color: @orange;
}

.setup-progress-segment {
  display: inline-block;
  padding: 5px 0;
  width: 100%;
  border-top: 2px solid rgba(0, 0, 0, 0.3);

  > a {
    font-size: @font-size-base;
  }
}

/* Small devices (tablets, 768px and up) */
@media (min-width: @screen-sm) {
  .setup-progress-segment {
    padding-bottom: 0;
    width: 33.33%;
  }
}

.setup-footer {
  margin-top: 40px;
  padding-top: 20px;
  border-top: 2px solid rgba(0, 0, 0, 0.3);
  text-align: right;

  > .btn {
    margin-left: 10px;
  }
}


// Intro
// ------------------------

.setup-intro-container {
  text-align: center;

  .setup-intro-header {
    margin-top: 0;
  }

  .setup-intro-img {
    margin: 20px 0;
    height: 60px;
  }

  .setup-intro-play-img {
    height: 100px;
  }

  .next-btn {
    margin-top: 20px;
    min-width: 200px;
  }
}


// Basic
// ------------------------

.setup-basic-form-group {
  margin-top: 40px;

  label {
    color: @gray-lightest;
  }
}


// Library
// ------------------------

.setup-add-section-container {
  margin: 40px 0 20px;
  text-align: center;
}

.setup-agent-section {
  .setup-agent-title {
    color: rgba(255, 255, 255, 0.45);
  }
  .setup-agent-disclaimer {
    color: rgba(255, 255, 255, 0.3);
  }
  .setup-agent-settings {
    padding-left: 15px;
  }
}

// Channels
// ------------------------

.setup-channel-list-container {
  margin-top: 40px;
}


// Complete
// ------------------------

.setup-complete-container {
  label {
    color: @gray-lightest;
  }
}

.setup-apps-container {
  margin-bottom: 40px;
  vertical-align: middle;
  text-align: center;
}

.setup-apps-image {
  margin-right: 40px;
  width: 40%
}
