.playButton {
  line-height: 0;

  &:hover .playCircle {
    border-color: $primaryBackgroundColor;
    background-color: $primaryBackgroundColor;
    color: $primaryButtonColor;
  }

  &:global(.isActive) .playCircle {
    background-color: $primaryActiveBackgroundColor;
  }
}

.playCircle {
  position: relative;
  display: inline-block;
  width: 24px;
  height: 24px;
  border: 2px solid $defaultLinkColor;
  border-radius: 50%;
  color: $defaultLinkColor;
  text-align: center;
  font-size: 10px;
  line-height: 24px;
  transition: all $defaultSpeed;
}
