.badge {
  composes: badge from '~core/components/base/Badge.css';

  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  padding: 2px 8px;
  color: $primaryColor;
  text-transform: uppercase;
  font-size: $defaultFontSize;
  font-family: $boldFontFamily;
}

.icon {
  top: 0;
  margin-right: 4px;
  width: 12px;
  height: 12px;
}
