.container {
  display: flex;
  justify-content: space-between;
}

.actionLinksContainer {
  display: inline-block;
}

.actionLinks {
  composes: actionLinks from '~desktop/common/js/views/v3/components/pages/settings/SettingsFormSectionActionLinks.css';

  flex-flow: row wrap;
  padding-bottom: 0;
  padding-left: 20px;
}

.linkSeparator {
  margin: 0 8px;
  color: $dimColor;
}

.linkSeparator:first-of-type {
  display: none;
}

@media only screen and (max-width: $breakpointMedium) {
  .linkSeparator {
    display: none;
  }

  .actionLinks {
    flex-direction: column;
  }
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $dimColor;
}

.badgeContainer {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.plexPassBadge {
  margin-right: 15px;
  height: 30px;
}

.textBadge {
  color: $selectedColor;
  text-transform: uppercase;
  font-size: $defaultFontSize;
  font-family: $boldFontFamily;
}
