//
// Side Bar
// --------------------------------------------------


// Base classes

@sidebar-width: 240px;

.side-bar {
  position: relative;
  width: @sidebar-width;
  padding: 0 20px 20px;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  #gradient > .horizontal-three-colors(@start-color: rgba(26, 26, 26, 0.4); @mid-color: rgba(19, 19, 19, 0.4); @color-stop: 50%; @end-color: rgba(0, 0, 0, 0.4));
  .transition(all 0.2s);

  &.transition-out {
    .translate(-300px, 0);
  }

  h5 {
    margin: 20px 0 5px;
  }
}

.side-bar-actions {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 0;

  > a,
  > .dropdown > a, {
    padding-left: 10px;
    font-size: @font-size-base;
  }

  > .hide-btn {
    margin: 0 6px 0 -20px;
  }
}


// Section

.side-bar .form-group-search {
  margin: 12px 0;
}

.side-bar-page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 0 20px 20px;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  .transition(all 0.2s);
}

.filters-page {
  .translate(@sidebar-width, 0);
}

.filter-options-page {
  .translate(@sidebar-width * 2, 0);
}

.side-bar.show-filters {
  .main-page {
    .translate(-@sidebar-width, 0);
  }

  .filters-page {
    .translate(0, 0);
  }

  .filter-options-page {
    .translate(@sidebar-width, 0);
  }
}

.side-bar.show-filter-options {
  .main-page {
    .translate(-@sidebar-width * 2, 0);
  }

  .filters-page {
    .translate(-@sidebar-width, 0);
  }

  .filter-options-page {
    .translate(0, 0);
  }
}

.side-bar .sort-btn {
  i {
    display: none;
  }

  &.selected {
    i {
      display: inline-block;
    }
  }
}

.section-filters-container,
.section-filter-options-container {
  position: relative;

  > h5 {
    margin-left: 10px;
  }

  > .side-bar-actions {
    right: auto;
    left: 0;
  }
}

.section-current-filters-container {
  position: relative;
}
