.avatarContainer {
  display: flex;
}

.isOverDropTarget {
  box-shadow: 0 0 0 2px $selectedColor;
}

.avatarPane {
  min-width: 180px;

  canvas {
    background-color: $avatarBackgroundColor;
    background-image: url('~common/img/posters/avatar-560.svg');
    background-size: contain;
  }
}

.sliderContainer {
  width: 100%;
}

.sliderEnd {
  display: inline-block;
  color: $dimColor;
}

.sliderWrapper {
  display: inline-block;
  padding: 0 10px;
  width: calc(100% - 26px);
}

.slider {
  composes: slider from '~desktop/common/js/views/v3/components/core/Slider.css';

  margin-bottom: 4px;
  width: 100%;
}

.help {
  width: 75%;
  color: $dimColor;
}

.avatarTextPane {
  padding: 0 30px;
}

.avatarActionButtons {
  margin-right: 24px;
}

.avatarFileInput {
  display: none !important;
}
