$eventStatusSize: 24px;
$eventStatusHoverColor: rgba(0, 0, 0, 0.75);

.errorButton {
  width: $eventStatusSize;
  height: $eventStatusSize;
  border-radius: 50%;
  background-color: $posterCenterCircleBackgroundColor;
  color: $warningColor;
  text-align: center;
  font-size: $defaultIconFontSize;
  line-height: $eventStatusSize;
  transition: all $defaultSpeed;

  &:hover {
    background-color: $warningColor;
    box-shadow: inset 0 0 0 2px $warningColor;
    color: $eventStatusHoverColor;
  }

  &:global(.isActive) {
    background-color: darken($warningColor, 0.15);
  }
}

.errorTooltip {
  composes: bottomCenter from '~core/components/tooltips/Tooltip.css';

  max-width: $recordingPriorityPaneWidth;
}
