.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 900px) {
  /* Add specificity to override modal styles with different dimensions. */
  .container {
    align-items: baseline;
    padding: 20px 0;
  }
}
