.pageHeader {
  /* Necessary for the box-shadow to overlap page content */
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
  padding-right: 25px;
  padding-left: $pageHeaderLeftMargin;
  height: $pageHeaderRowHeight;
  box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.15);
  color: $pageHeaderColor;
  font-size: 16px;
}
