.overlay {
  composes: overlay from './PosterCardOverlay.css';
}

.background {
  composes: background from './PosterCardOverlay.css';
}

.hoverBackground {
  composes: hoverBackground from './PosterCardOverlay.css';
}

.selectedBackground {
  composes: selectedBackground from './PosterCardOverlay.css';
}

.visibleBackground {
  composes: visibleBackground from './PosterCardOverlay.css';
}

.link {
  @add-mixin cover;
}

/*
 * Badges
 */

.badge {
  position: absolute;
  padding: 0 6px;
  min-width: 28px;
  border-radius: 4px;
  text-align: center;
  font-family: $boldFontFamily;
  line-height: 22px;
  opacity: 1;
  transition: all $defaultSpeed;
}

.topLeftBadge {
  composes: badge;

  top: $v4CardBorderRadius;
  left: $v4CardBorderRadius;
  padding: 0 8px 0 6px;
}

.versionBadge {
  composes: topLeftBadge;

  background-color: $posterVersionBadgeColor;
}

.versionBadgeIcon {
  margin-right: 2px;
  font-size: $defaultIconFontSize;
}

.smartBadge {
  composes: topLeftBadge;

  background-color: $posterSmartBadgeColor;
}

.smartBadgeIcon {
  font-size: $defaultIconFontSize;
}

.favoriteBadge {
  composes: topLeftBadge;

  background-color: $posterFavoriteBadgeColor;
}

.favoriteBadgeIcon {
  font-size: $defaultIconFontSize;
}

.unplayedBadge {
  composes: badge;

  top: $v4CardBorderRadius;
  right: $v4CardBorderRadius;
  background-color: $v4AccentLight;
  color: $v4BaseDark;
}

.hasSubscriptionBadge {
  composes: downloadIcon from '~desktop/common/js/views/v3/components/core/icons/DownloadIcon.css';
  composes: topLeftBadge;

  margin: 4px 0 0 4px;
  padding: 0;
  min-width: auto;
  box-shadow: none;
}

/*
 * See: https://github.com/plexinc/plex-web-client/issues/4677
 */
.unplayedTagContainer {
  position: absolute;
  right: 0;
  overflow: hidden;
  width: 32px;
  height: 32px;
}

.unplayedTag {
  composes: unplayedBadge;

  top: -16px;
  right: -16px;
  padding: 0;
  width: 100%;
  height: 100%;
  border-radius: 0;
  transform: rotate(45deg);
}

/*
 * Progress Bar
 */

.progress {
  composes: progress from './PosterCardOverlayProgress.css';
}

/*
 * Buttons
 */

.button {
  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  text-align: center;
  font-size: $defaultIconFontSize;
  line-height: $defaultIconFontSize;
  opacity: 0;
  transition: all $defaultSpeed;
}

.centerCircle {
  @add-mixin absoluteCenter;
  display: block;
  width: $posterCenterCircleSize;
  height: $posterCenterCircleSize;
  border-radius: 50%;
  color: $defaultLinkColor;
  text-align: center;
  font-size: 20px;
  line-height: $posterCenterCircleSize;
  transition: all $defaultSpeed;
}

.playButton {
  @add-mixin absoluteCenter;
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: button;

  transform: translate(-50%, -50%);
}

.playCoverButton {
  composes: playButton;

  width: 100%;
  height: 100%;
}

.playCircle {
  composes: playCircle from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: centerCircle;

  background-color: $posterCenterCircleBackgroundColor;
  font-size: 12px;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';
  composes: button;

  top: 0;
  left: 0;
  transform: translateY(-$posterHoverSlideDistance);
}

.selectCircle {
  composes: selectCircle from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';

  background-color: $posterCenterCircleBackgroundColor;
}

.coverButton {
  @add-mixin cover;
}

.selectCoverButton {
  composes: coverButton;

  cursor: default;

  /* Show the hover color for the select circle button when hovered */
  &:hover ~ .selectButton > .selectCircle {
    border-color: $defaultLinkHoverColor;
  }
}

.deselectCoverButton {
  composes: coverButton;

  cursor: default;
}

.editButton {
  composes: button;

  bottom: 0;
  left: 0;
  transform: translateY($posterHoverSlideDistance);
}

.favoriteButton {
  composes: button;

  bottom: 0;
  left: 50%;
  transform: translate(-50%, $posterHoverSlideDistance);
}

.moreButton,
.removeButton {
  composes: button;

  right: 0;
  bottom: 0;
  transform: translateY($posterHoverSlideDistance);
}

/*
 * Hover and Selected States
 */

.overlay:hover:not(.hasActiveSelection),
.isActionsMenuOpen:not(.hasActiveSelection) {
  .refreshingCircle,
  .trashCircle {
    opacity: 0;
  }
}

.overlay:hover,
.isActionsMenuOpen,
.isSelected {
  .badge {
    opacity: 0;
    transform: translateY(-$posterHoverSlideDistance);
  }

  .progress {
    opacity: 0;
  }

  .unplayedTag {
    transform: translateY(-$posterHoverSlideDistance) rotate(45deg);
  }

  .button {
    opacity: 1;
    transform: translateY(0);
  }

  .playButton {
    transform: translate(-50%, -50%);
  }

  .favoriteButton {
    transform: translate(-50%, 0);
  }
}

.isConflictingTypeSelected {
  &:hover .selectButton {
    opacity: 0.3;
  }

  .selectCoverButton {
    cursor: not-allowed;
  }
}

/* Add specificity to override .overlay:hover */
.overlay.hasActiveSelection,
.overlay.isModifierKeyPressed {
  .playButton,
  .editButton,
  .favoriteButton,
  .moreButton,
  .removeButton {
    opacity: 0;
    pointer-events: none;
  }
}

.overlay.isDragging {
  .button {
    opacity: 0;
    pointer-events: none;
  }
}

/*
 * Refreshing and Trash States
 */

.refreshingSpinnerContainer {
  @add-mixin absoluteCenter;
  width: calc($posterCenterCircleSize + 6px);
  height: calc($posterCenterCircleSize + 6px);
}

.refreshingSpinner {
  width: 100%;
  height: 100%;
}

.refreshingCircle {
  composes: centerCircle;

  background-color: $posterCenterCircleBackgroundColor;
}

.refreshingIcon {
  position: absolute;
}

.trashCircle {
  composes: centerCircle;

  background-color: $dangerBackgroundColor;
}
