.card {
  composes: card from '~desktop/common/js/v4/core/ui/cards/PosterCard.css';

  &:focus-within {
    .moreButton {
      opacity: 1;
    }
  }
}

.image {
  border-radius: $v4CardBorderRadius;
}

.iconContainer {
  composes: container from '~desktop/common/js/v4/core/ui/cards/PosterCardIcon.css';

  color: $posterPlaceholderIconColor;
}

.link {
  composes: link from '~desktop/common/js/v4/core/ui/cards/PosterCardLink.css';

  border: 0;
  background: transparent;
}

.hoveredLink {
  composes: hoveredLink from '~desktop/common/js/v4/core/ui/cards/PosterCardLink.css';

  border: 0;
}

.moreButton {
  composes: moreButton from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardActions.css';
}

.hiddenMoreButton {
  composes: moreButton;
  composes: isHidden from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardActions.css';
}
