.title {
  position: relative;
  display: block;
  padding: 0 $v4CardBorderRadius;
  color: $defaultColor;
  line-height: $v4CardTitleHeight;
  cursor: default;
}

a.title,
.title a {
  cursor: pointer;

  &:hover {
    /* Override global v2 anchor color style */
    color: $defaultColor;
    text-decoration: underline;
  }
}

.isSecondary,
.isSecondary a {
  color: $dimColor;
}

.singleLineTitle {
  @add-mixin truncate;
  composes: title;

  height: $v4CardTitleHeight;
}

.doubleLineTitle {
  @add-mixin doubleLineTruncate;
  composes: title;

  max-height: calc($v4CardTitleHeight * 2);
}
