.row {
  display: flex;
  justify-content: center;
}

.cell {
  @add-mixin truncate;
  flex-grow: 1;
  flex-basis: 130px;
}

.label {
  composes: cell;

  margin-right: 40px;
  color: $dimColor;
  text-align: right;
  text-transform: uppercase;
  font-size: $defaultFontSize;
  font-family: $boldFontFamily;
  line-height: $lineHeightComputed;
}

.isDisabled {
  opacity: 0.2;
  pointer-events: none;
}
