.controlsContainer {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-top: $seekBarHeight;
  min-width: $playerControlsMinWidth;
  height: $playerControlsHeight;
}

@media screen and (max-width: $playerControlsNarrowBreakpoint) {
  .controlsContainer {
    padding-bottom: 5px;
    height: $playerControlsNarrowHeight;
  }
}
