.modalContainer {
  @add-mixin cover;
  @add-mixin resetFont;
  position: fixed;
  z-index: $modalZIndex;
}

.modalBackdrop {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: $modalBackdropBackgroundColor;
}

.modal {
  display: flex;
  max-height: 90%;
  transform: scale(1);
  transform-origin: center center;
}

/*
 * Sizes
 */

.small {
  composes: modal;

  width: 450px;
}

.medium {
  composes: modal;

  /*
   * Most default modals will have multiple states (loading, error, form steps).
   * A fixed height ensures the modal is stable as these states change.
   */
  width: 700px;
  height: 500px;
}

.large {
  composes: modal;

  width: 1080px;
}

/*
 * Breakpoints
 */

@media only screen and (max-width: $breakpointLarge) {
  /* Add specificity to override modal styles with different dimensions. */
  .modal.large {
    width: 90%;
  }
}

@media only screen and (max-width: $breakpointMedium) {
  /* Add specificity to override modal styles with different dimensions. */
  .modal.small,
  .modal.medium {
    width: 90%;
  }
}

@media only screen and (max-width: $breakpointSmall) {
  /* Add specificity to override modal styles with different dimensions. */
  .modal.small,
  .modal.medium,
  .modal.large {
    max-height: 100%;
    width: 100%;
    height: 100%;
  }
}
