.item {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  display: flex;
  align-items: center;
  padding: 0 30px;
}

.imageContainer {
  flex-shrink: 0;
}

.image {
  border-radius: $v4CardBorderRadius;
  background-color: $v4CardBackgroundColor;
  box-shadow: 0 0 $v4CardShadowSize $v4CardShadowColor;
}

.title {
  @add-mixin truncate;

  padding: 15px;
}
