.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 30px;
  padding: 0 20px;
  height: 50px;
  background-color: $blackDarkTranslucentBackgroundColor;
}

.pageHeaderLeft {
  flex-shrink: 0;
  color: $pageHeaderColor;
  text-transform: uppercase;
  font-size: 16px;
  font-family: $boldFontFamily;
}

.pageHeaderRight {
  display: flex;
  /*
  * Flex children containing text won't shrink past their minimum width unless
  * set an explicit minimum width of 0.
  * See: https://css-tricks.com/flexbox-truncated-text/
  */
  min-width: 0;
}
