.row {
  position: relative;
  min-height: 44px;
  color: $defaultLinkColor;

  &:hover {
    color: $defaultColor;
  }
}

.alternateRow {
  composes: row;
}

.underlay {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';
}

.overlay {
  @add-mixin cover;
  display: flex;
  align-items: center;
  pointer-events: none;
}

.iconContainer {
  display: flex;
  align-items: center;
  padding: 0 20px;
}

.icon {
  top: 0;
  width: $defaultIconFontSize;
  height: $defaultIconFontSize;
  font-size: $defaultIconFontSize;
}

.title {
  display: flex;
  align-items: center;
  pointer-events: all;

  &:hover {
    color: $defaultColor;
    text-decoration: underline;
  }
}

.actions {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
  opacity: 0;
}

.action {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  padding: 0 15px;
  pointer-events: all;
}

.visible {
  opacity: 1;
}

.editIcon {
  margin-right: 5px;
}

.header {
  display: flex;
}

.column {
  align-self: flex-end;
  padding: 0 5px;
  width: 100px;
  text-align: center;
}

.headerIcon {
  display: flex;
  justify-content: center;
  width: 20px;
}

.spinner {
  composes: spinner from '~desktop/common/js/v4/core/ui/spinners/components/Spinner.css';
  composes: icon;
}
