$smallArrowBorderWidth: calc($smallArrowWidth / 2);
$mediumArrowBorderWidth: calc($mediumArrowWidth / 2);
$largeArrowBorderWidth: calc($largeArrowWidth / 2);

.input {
  position: relative;
  display: inline-block;
  max-width: $inputMaxWidth;
  width: 100%;
  border: none;
  border-radius: $inputBorderRadius;
  background-color: $inputBackgroundColor;
  color: $inputColor;
  vertical-align: middle;
  font-family: $defaultFontFamily;
  line-height: $lineHeightComputed;
}

.input::after {
  position: absolute;
  top: calc(50% - 2px);
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: $inputColor;
  border-top-width: 4px;
  border-right: 4px solid transparent;
  border-bottom-width: 0;
  border-left: 4px solid transparent;
  content: '';
  pointer-events: none;
}

.select {
  position: absolute;
  padding-right: 25px;
  padding-left: 10px;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  color: $inputColor;
  appearance: none;

  /*
    Firefox and IE will use the color from the select and apply it to the option.
    Both browsers will not use the color property on the `option`, but will use a
    background color property. This makes the `option` readable in Firefox and IE.
  */
  option {
    background-color: #2d2d2d;
  }

  &::-ms-expand {
    display: none;
  }
}

.small {
  height: $smallSize;
  font-size: $defaultFontSize;

  &::after {
    right: 10px;
    border-top-width: 4px;
    border-right-width: $smallArrowBorderWidth;
    border-left-width: $smallArrowBorderWidth;
  }
}

.medium {
  height: $mediumSize;
  font-size: $defaultFontSize;

  &::after {
    right: 10px;
    border-top-width: 5px;
    border-right-width: $mediumArrowBorderWidth;
    border-left-width: $mediumArrowBorderWidth;
  }
}

.large {
  height: $largeSize;
  font-size: $largeFontSize;

  &::after {
    right: 10px;
    border-top-width: 6px;
    border-right-width: $largeArrowBorderWidth;
    border-left-width: $largeArrowBorderWidth;
  }
}

.isDisabled {
  opacity: $inputDisabledOpacity;

  .select {
    cursor: not-allowed;
  }
}

.hasError {
  border: 2px solid $dangerColor;
}
