//
// Navs
// --------------------------------------------------


// Header
// -------------------------

.nav-header {
  margin: 40px 0 20px;

  > li {
    float: left;

    > a {
      padding: 0 20px;
      font-size: @font-size-large + 4;
      line-height: 36px;

      &:hover,
      &:focus {
        background-color: transparent;
      }

      &.selected {
        background-color: transparent;

        .badge {
          background-color: @orange;
        }
      }

      .badge {
        margin: 8px 0 0 8px;
        vertical-align: top;
        background-color: #343434;
        background-color: rgba(255, 255, 255, 0.1);
      }
    }
  }
}


// Cards
// -------------------------

.nav-cards {
  margin-bottom: 20px;
}

.nav-cards > li {
  float: none;
  margin: 0;

  > .card {
    padding: 10px 20px;
    .text-overflow();

    .badge {
      float: right;
      margin-top: 2px;
    }
  }

  > a.card:hover,
  > a.card:focus {
    background-color: @card-hover-bg;
  }
}


// Pills
// -------------------------

.nav-pills {
  > li > a {
    padding: 10px 20px;

    &:hover,
    &:focus {
      background-color: transparent;
    }
  }

  > li.active > a {
    // Adjust for the 1px border on each side of the card.
    .card;
    padding: 9px 20px;
  }
}

.nav-pills-sm {
  > li > a {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  > li.active > a {
    // Adjust for the 1px border on each side of the card.
    padding-top: 4px;
    padding-bottom: 4px;
  }
}


// Dashboard
// -------------------------

.nav-dashboard {
  margin: 0;

  > li {
    float: left;

    > a {
      padding: 0 0 0 10px;
      font-size: @font-size-large + 8;
    }
  }
}
