.container {
  display: flex;
  margin-bottom: 10px;
}

.deviceChannelsContainer,
.guideChannels {
  @add-mixin truncate;
  color: $dimColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
}

.deviceChannelsContainer {
  display: flex;
  flex: 1 0 55%;
  padding-left: 15px;
}

.deviceChannels {
  flex: 1 0 auto;
}

.signalStrength {
  flex: 0 0 100px;
  text-align: center;
}

.deviceChannelsPadding {
  flex: 0 0 20%;
}

.guideChannels {
  flex: 1 0 45%;
}
