.fullPlayer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}

.player {
  position: absolute;
  top: 0;
  bottom: 0;
  display: flex;
  align-items: stretch;
  overflow: hidden;
  width: 100%;
}

.main {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.mainFooter {
  flex-shrink: 0;
  height: 100px;
}

.content {
  position: relative;
  display: flex;
  flex-grow: 1;
  overflow: hidden;
}

.spinner {
  @add-mixin cover;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bar {
  position: absolute;
  right: 0;
  left: 0;
  transition: transform $defaultSpeed, background $defaultSpeed;
}

.bottomBar {
  composes: bar;
}

.seekBar {
  composes: seekBar from '~desktop/common/js/views/v3/components/players/core/SeekBar.css';

  position: absolute;
  top: -$seekBarHeight;
  width: 100%;
}

.aside {
  width: 280px;
}

.hideControls {
  cursor: none;

  /* Hide the cursor on the play / pause overlay button */
  button {
    cursor: none;
  }

  .controls {
    transform: translateY(0);
  }
}

.dockControls {
  .player {
    bottom: $playerControlsHeight;
  }
}

@media screen and (max-width: $playerControlsNarrowBreakpoint) {
  .dockControls {
    .player {
      bottom: $playerControlsNarrowHeight;
    }
  }

  .controls {
    background: $playerBottomControlsBackgroundColor;
  }
}
