.header {
  padding: 2px 12px 6px;
}

.headerTitle {
  display: flex;
}

.backIcon {
  font-size: $defaultIconFontSize;
}

.filterText {
  @add-mixin truncate;
  margin-left: 8px;
  color: $headerColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
}

.searchInput {
  composes: input from '~core/components/forms/TextInput.css';

  margin-top: 8px;
  padding-right: 22px;
  padding-left: 22px;
  background-color: rgba(255, 255, 255, 0.07);

  &:not(:focus)::placeholder {
    color: rgba(255, 255, 255, 0.3);
  }
}

.filterOptionsScroller {
  composes: vertical from '~core/components/base/Scroller.css';

  position: absolute;
  top: 41px;
  right: 0;
  bottom: 0;
  left: 0;
  padding-right: 0;
}

.filterOptionsWithSearchScroller {
  composes: filterOptionsScroller;

  top: 85px;
}

.filterOptionMenuItem {
  composes: menuItem from '~core/components/menus/MenuItem.css';

  padding-left: 34px;
}

.emptyText {
  padding: 20px 10px;
  color: $dimColor;
  text-align: center;
}
