//
// Loading
// --------------------------------------------------


// Spinner
// ------------------------

.loading {
  position: fixed;
  top: 30%;
  left: 50%;
  width: 24px;
  height: 24px;
  margin: -12px 0 0 -12px;
  border: solid 2px transparent;
  border-top-color: @orange;
  border-left-color: @orange;
  border-radius: 50%;
  .animation(loading 0.5s linear infinite);
}

.loading-inline {
  position: absolute;
  top: 50%;
}

span.loading {
  position: static;
  display: inline-block;
  margin: 0 10px;
}

.loading-lg {
  width: 30px;
  height: 30px;
  margin: -12px 0 0 -12px;
  margin-left: -15px;
  border-width: 3px;
}

.loading-xl {
  width: 60px;
  height: 60px;
  margin: -12px 0 0 -12px;
  margin-left: -30px;
  border-width: 3px;
}

.loading-sm {
  width: 16px;
  height: 16px;
  margin: -8px 0 0 -8px;
}

.loading-xs {
  width: 10px;
  height: 10px;
  margin: -5px 0 0 -5px;
  border-width: 1px;
}


// Button
// ------------------------

.btn-loading {
  position: relative;
  overflow: hidden;

  &.active {
    > .loading {
      left: 50%;
      visibility: visible;
    }

    > span.loading {
      left: 0;
    }

    > .btn-label {
      left: 100%;
      .opacity(0);
    }
  }

  > .loading {
    position: absolute;
    top: 50%;
    left: -12px;
    z-index: 1; // Avoids unnecessary repaints.
    border-top-color: @white;
    border-left-color: @white;
    visibility: hidden;
    .transition(left 0.2s);
  }

  > span.loading {
    position: relative;
    top: 1px;
    left: 0;
    display: none;
    margin: 0;
  }

  > .btn-label {
    position: relative;
    left: 0;
    .transition(~"left 0.2s, opacity 0.2s");
  }
}

.btn-loading-replace {
  &.active {
    > .loading {
      display: inline-block;
    }

    > .btn-label {
      display: none;
    }
  }

  &:hover.active {
    > .loading {
      display: none;
    }

    > .btn-label-alt {
      display: inline-block;
      left: auto;
      right: auto;
    }

    > .btn-label {
      display: none;
    }
  }

  > .btn-label-alt {
     position: relative;
     display: none;
  }
}

.btn-loading-static {
  &.active .loading {
    display: block;
  }

  &.active span.loading {
    display: inline-block;
  }

  .loading {
    display: none;
  }

  > .loading,
  > .btn-label,
  > .btn-label-alt {
    .transition(none);
  }
}

.dashboard-section-list {
  .btn-loading.active > .loading {
    left: 8%;
  }
}
