.modalBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  position: relative;
  padding: 0;
}

.innerModalBody {
  padding: 0;
}

.pane {
  composes: innerModalBody from '~core/components/modals/ModalBody.css';

  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity $defaultSpeed;
}

.isHidden {
  opacity: 0;
  pointer-events: none;
}

.editPane {
  display: flex;
  flex-direction: column;

  > p {
    margin-bottom: 25px;
  }
}

.centeredContainer {
  text-align: center;
}

.stackedButtonsContainer {
  margin-top: 25px;
}

.sortableServers {
  composes: container from '~desktop/common/js/views/v3/components/user/modals/home/SortableHomeHubs.css';

  position: relative;
  flex-grow: 1;
  background-color: $sortableHomeHubsContainerBackgroundColor;
}

.reloadIcon {
  margin-right: 10px;
}

.footer {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  justify-content: space-between;
}

.button {
  composes: button from '~core/components/modals/ModalFooter.css';
}

.splitButton {
  composes: splitButton from '~core/components/modals/ModalFooter.css';
}
