.input {
  display: inline-block;
  /* Bootstrap applies margin to all inputs */
  margin: 0 10px 0 0 !important;
  font-size: 18px;
}

.label {
  position: relative;
  display: inline-block;
  /* Bootstrap applies margin to all labels */
  margin: 0 !important;
  color: $inputColor;
  font-family: $defaultFontFamily;
}

.small {
  font-size: $defaultFontSize;
}

.large {
  font-size: $largeFontSize;
}

.isDisabled {
  opacity: $inputDisabledOpacity;
  cursor: not-allowed;
}

.hasError {
  color: $dangerColor;
}
