.overlay {
  @add-mixin linkOverlay;
  display: flex;
  align-items: center;
  padding: $summaryCellPadding;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.titleContainer {
  min-width: calc(100% - 25px);
  max-width: 100%;
}

.primaryTitle {
  @add-mixin truncate;
  font-size: $largeFontSize;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.secondaryTitle {
  @add-mixin truncate;
  color: $defaultLinkColor;
}

.nextAiringDate {
  color: $dimColor;
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $dimColor;
}

.subscriptionIcon {
  margin-right: 8px;
  color: $recordColor;
  font-size: $defaultIconFontSize;
}
