.container {
  composes: container from '~desktop/common/js/v4/features/dashboard/components/ServerDashboardSectionContent.css';

  justify-content: space-between;
  height: 580px;
}

.spinnerContainer {
  composes: container;

  align-items: center;
}

.column {
  flex: 0 1 280px;
  /*
  * Flex children containing text won't shrink past their minimum width unless
  * set an explicit minimum width of 0.
  * See: https://css-tricks.com/flexbox-truncated-text/
  */
  min-width: 0;

  &:not(:last-child) {
    margin-right: 40px;
  }
}
