//
// Conversion
// --------------------------------------------------

.conversion-filter-bar.hide-actions {
  .filter-bar-right {
    display: none;
  }
}

.conversion-status-message,
.conversion-error-message {
  margin-bottom: 40px;
  color: @gray-light;
}

.conversion-container.is-empty .conversion-status-message {
  display: none;
}

.conversion-error-icon {
  margin: 0 10px;
}
