.button {
  @add-mixin truncate;
  composes: button from '~desktop/common/js/v4/core/ui/buttons/components/Button.css';

  margin: 0 $pageHeaderTabButtonLeftRightMargin;
  padding: 5px 20px;
  max-width: 250px;
  background-color: transparent;
  color: $dimColor;
  line-height: 30px;

  &:hover {
    background-color: $blackDarkerTranslucentBackgroundColor;
  }
}

.selectedButton {
  composes: button;

  background-color: $blackDarkTranslucentBackgroundColor;
  color: $selectedColor;
}
