.pageHeader {
  composes: pageHeader from '~desktop/common/js/views/v3/components/header/PageHeader.css';

  padding: 0 20px;
  height: 100px;
  background-color: $blackDarkerTranslucentBackgroundColor;
}

.pageHeaderLeft {
  composes: pageHeaderLeft from '~desktop/common/js/views/v3/components/header/PageHeaderLeft.css';

  max-width: 300px;
}

.pageHeaderRight {
  composes: pageHeaderRight from '~desktop/common/js/views/v3/components/header/PageHeaderRight.css';

  min-width: 140px;
}

.addToHomeIcon {
  composes: button from '~core/components/buttons/IconButton.css';

  font-size: $headerFontSize;
}

.avatar {
  composes: avatar from '~desktop/common/js/views/v3/components/core/images/AvatarImg.css';

  display: flex;
  flex-shrink: 0;
}

.username {
  @add-mixin truncate;
  margin-left: 20px;
  font-size: $largeFontSize;
}

.confirmMessageContainer {
  display: flex;
  flex-direction: column;
}

.confirmWarning {
  align-self: center;
  color: $warningColor;
  font-size: 44px;
}

.confirmMessage {
  margin-top: 25px;
}
