.container {
  display: flex;
  flex-direction: row;
  padding: 7px $pagePadding 3px;
}

.rules {
  display: flex;
  justify-content: flex-start;
  flex: 2 0 0;
  flex-direction: row;
}

.rule {
  flex: 1 0 150px;
  margin-right: 10px;
  min-width: 100px;
  max-width: 200px;
}

.tagsInput {
  composes: tagsInput from '~core/components/forms/AutosuggestTagsInput.css';

  background-color: $altFilterBuilderInputBackgroundColor;
}

.textInput {
  composes: input from '~core/components/forms/TextInput.css';

  background: $altFilterBuilderInputBackgroundColor;
}

.unitTextInput {
  composes: textInput from '~desktop/common/js/views/v3/components/core/forms/UnitInput.css';

  background: $altFilterBuilderInputBackgroundColor;
}

.selectInput {
  composes: input from '~core/components/forms/SelectInput.css';

  background: $filterBuilderInputBackgroundColor;
}

.value {
  flex-grow: 1;
  flex-basis: 250px;
  margin-right: 10px;
  min-width: 200px;
  max-width: 300px;
}

.buttons {
  flex-grow: 0;
  flex-shrink: 0;
  width: 80px;
}

.iconButton {
  margin-top: -4px;
  margin-right: 10px;
  margin-left: 10px;
  font-size: 20px;
}

.iconButtonDisabled {
  composes: iconButton;

  color: rgba(255, 255, 255, 0.3);
}
