.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  max-width: 200px;
}

.containerWithRoute {
  composes: container;

  &:hover {
    .title {
      color: $defaultLinkHoverColor;
    }
  }
}

.title {
  @add-mixin truncate;
  align-self: flex-start;
  color: rgba(255, 255, 255, 0.75);
  font-size: $largeFontSize;
  font-family: $defaultFontFamily;
  line-height: 1.5;
}

.detail {
  @add-mixin truncate;
  align-self: flex-start;
  color: $dimColor;
  font-size: $smallFontSize;
  font-family: $defaultFontFamily;
  line-height: 1.4;
}
