.item {
  &:hover,
  &.isActionsMenuOpen {
    background-color: $stripeListHoverBackgroundColor;

    .button {
      opacity: 1;
    }

    .titleEpisodeIndex {
      opacity: 0;
    }
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: calc($summaryCellPadding / 2);

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.simpleSummaryContainer {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
  padding: calc($summaryCellPadding / 2);
}

.indexPlayContainer {
  position: relative;
  flex: 0 0 65px;
  padding-right: 5px;
}

.titleEpisodeIndex {
  color: $dimColor;
  font-size: $largeFontSize;
  opacity: 1;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 0;
  }
}

.playButtonContainer {
  position: absolute;
  top: 0;
  left: 0;
}

.playButton {
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: button;

  transition: opacity $defaultSpeed;
}

.miniProgressContainer {
  flex: 0 0 20px;
  padding-right: 10px;
}

.titleAndSummaryContainer {
  overflow: hidden;
  width: 100%;
}

.titleContainer {
  display: flex;
}

.title {
  @add-mixin truncate;
  flex-grow: 1;
  padding-right: 20px;
  font-size: $largeFontSize;
}

.summary {
  margin-top: 5px;
  max-width: $prePlaySummaryMaxWidth;
  height: calc($lineHeightComputed * $defaultFontSize * 1);
  color: $defaultLinkColor;
}

.actions {
  flex-shrink: 0;
  padding-left: 10px;
}

.button {
  opacity: 0;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 1;
  }
}

.moreButton {
  composes: button;

  padding: 0 10px;
}
