.playButton {
  margin-right: 10px;
  padding-left: 8px;
  font-size: $smallFontSize;

  &:hover {
    background-color: $primaryColor;
  }
}

.recordButton {
  composes: playButton;

  &:hover {
    background-color: $recordBackgroundColor;

    .recordingIcon {
      color: $defaultLinkHoverColor;
    }
  }

  &:global(.isActive) {
    background-color: darken($recordBackgroundColor, 0.1);
  }
}

.icon {
  top: -1px;
  margin-right: 5px;
}
