.serverMenuItem {
  padding: 8px 10px 8px 25px;
  width: 100%;
  height: $serverMenuItemHeight;

  &:hover {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }
}

.disabledServerMenuItem {
  color: $defaultLinkDisabledLinkColor;
}

.selectedServerMenuItem {
  color: $selectedColor;

  &:hover {
    color: $defaultLinkHoverColor;
  }
}

/* See: https://bugzilla.mozilla.org/show_bug.cgi?id=984869 */
.serverMenuItemContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.serverMenuTitle {
  flex-grow: 1;
  overflow: hidden;
  line-height: 1.5;
}

.serverMenuFriendlyName {
  @add-mixin truncate;
  font-size: $largeFontSize;
}

.serverMenuDetails {
  @add-mixin truncate;
  color: $defaultLinkDisabledLinkColor;
}

.serverMenuIconContainer {
  display: flex;
  flex-shrink: 0;
}

.serverMenuIcon {
  top: 0;
  margin-left: 10px;
  font-size: $defaultIconFontSize;
}

.selectedIcon {
  color: $selectedColor;
}

.secureIcon {
  color: $successColor;
}
