.row {
  display: flex;
  flex: 1;
  padding-left: 40px;
  height: 40px;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.draggingRow {
  composes: row;

  background-color: $stripeListSelectedBackgroundColor;
}

.handle {
  display: flex;
  align-items: center;
  padding: 10px;
}

.title {
  display: flex;
  align-items: center;
  padding: 10px;
}

.inputs {
  display: flex;
  align-items: stretch;
  justify-content: flex-end;
  flex: 1;
  padding-right: 50px;
}

.input {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;

  input[type='checkbox']:focus {
    outline: auto;
  }
}
