/*
 * Careful with using flexbox here. IE10 shows multiple scrollbars when using a
 * flex column layout.
 */

.pageContent {
  position: relative;
  flex-grow: 1;
}

/*
 * Work around bugs / spec disagreements in Safari and Firefox related to
 * padding within a scrollable area.
 *
 * See: https://bugzilla.mozilla.org/show_bug.cgi?id=748518
 * See: https://github.com/plexinc/plex-web-client/issues/4357
 */

.innerPageContent {
  padding: $pagePadding;
  min-width: 100%;
  min-height: 100%;
}
