// Forms
// --------------------------------------------------

.web-settings-container,
.server-settings-container {
  position: relative;

  .version-title {
    margin-bottom: 20px;
  }

  h4 {
    padding-bottom: 10px;

    &.settings-header {
      padding-top: 20px;
      color: @gray-light;
    }

    &.settings-header-first {
      color: @gray-light;
    }
  }

  label:not(.disabled) {
    color: @gray-lightest;
  }

  select {
    margin-left: 10px;
    font-size: @font-size-large;
  }

  input {
    max-width: 400px;
  }
}

.settings-group {
  display: none;

  &.active {
    display: block;
  }
}


// Advanced
// --------------------------------------------------

.advanced-setting,
.nav > li.advanced-setting {
  display: none;
}

.show-advanced .advanced-setting,
.show-advanced .nav > li.advanced-setting {
  display: block;
}

.show-advanced .basic-setting {
  display: none;
}


// Remote Access
// --------------------------------------------------

.remote-access-status-icon,
.remote-access-upload-icon-container {
  float: left;
  margin-right: 20px;
  font-size: 60px;
  color: rgba(255, 255, 255, 0.2);
}

.remote-access-upload-icon-container {
  position: relative;
  font-size: 0;
}

.remote-access-status-message {
  .clearfix();
  padding: 10px 0 0 80px;

  .status-header {
    margin: 0;
    padding: 0;
  }

  .status-header-single {
    margin-top: 10px;
  }

  .status-description {
    margin: 0;
  }

  .status-details {
    margin: 10px 0 0 0;
  }
}

.remote-access-mapping-container {
  .mapping-header {
    padding: 0;
  }

  .server-logo {
    float: left;
    margin-right: 20px;
  }

  .remote-access-mapping {
    .clearfix();
    padding-top: 18px;

    .mapping-label {
      margin-right: 5px;
    }

    .mapping-address {
      font-size: 0; // Remove spaces between elements.
    }

    .mapping-value,
    .mapping-empty-value,
    .mapping-separator {
      font-size: @font-size-base;
    }

    .mapping-empty-value {
      color: @gray;
    }

    .mapping-separator {
      margin: 0 4px;
    }

    .mapping-icon {
      margin: 0 15px;
    }

    .mapping-internet-icon {
      margin-right: 2px;
    }

    .mapping-internet.unreachable {
      color: @gray;
    }
  }
}

.remote-access-error-container {
  margin-left: 80px;

  .form-inline {
    margin-top: 40px;

    .form-control {
      margin-left: 10px;
    }

    .btn {
      margin-left: 20px;
    }
  }
}

.remote-access-disable-btn,
.remote-access-enable-btn {
  margin-top: 40px;
}

.remote-access-disable-message {
  margin-top: 10px;
}

.remote-access-disable-warning-icon {
  float: left;
  margin-top: 10px;
  margin-right: 15px;
  font-size: 24px;
  color: @gray;
}

.remote-access-upload-container {
  .remote-access-upload-speed-header {
    display: none;
  }

  .remote-access-upload-icon {
    font-size: 60px;
  }

  .remote-access-upload-warning-icon {
    display: none;
    position: absolute;
    top: 6px;
    right: 0;
    font-size: 12px;
    color: @orange-light;
  }

  &.show-warning {
    .remote-access-upload-speed-header {
      display: block;
    }

    .remote-access-upload-warning-icon {
      display: inline-block;
    }
  }
}

.remote-access-upload-form {
  overflow: hidden;
  padding-top: 15px;

  .remote-access-upload-speed-header {
    font-size: 15px;
    color: @orange-light;
    margin-bottom: 15px;
  }

  .remote-access-upload-speed-control {
    max-width: 60px;
    margin: 0 5px;
    text-align: center;
  }
}


// Versions
// --------------------------------------------------

.versions-container {
  .background-processing-list-region > .loading {
    margin: 60px 0;
  }
}

.versions-status-message {
  margin: 0 0 40px 55px;
  color: @gray-light;
}


// Agents
// --------------------------------------------------

.agent-types-nav {
  margin-bottom: 10px;
  font-size: @font-size-large;
}

.agents-nav {
  min-height: 35px;
}

.sortable-help-block {
  margin: 20px 15px;
}


// Updates
// --------------------------------------------------

.check-for-updates-container {
  margin-left: 10px;

  > .install-info-label {
    margin-left: 10px;
    font-size: @font-size-base;

    .success-icon {
      margin-right: 2px;
      color: @brand-success;
    }

    .failure-icon {
      margin-right: 2px;
      color: @brand-warning;
    }

    .install-manual-link {
      margin-left: 5px;
    }
  }

  > .release-notes-btn {
    margin-left: 10px;
  }

  > .checking-for-updates-btn,
  > .available-updates-btn,
  > .downloading-updates-container,
  > .downloading-updates-btn,
  > .refresh-updates-btn,
  > .install-updates-btn,
  > .installing-updates-btn,
  > .up-to-date-label,
  > .install-error-label,
  > .release-notes-btn {
    display: none;
  }


  // States

  &.checking > .checking-for-updates-btn {
    display: inline;
  }

  &.available > .available-updates-btn {
    display: inline;
  }

  &.downloading > .downloading-updates-container,
  &.downloading > .downloading-updates-btn {
    display: inline;
  }

  &.downloaded > .install-updates-btn {
    display: inline;
  }

  &.installing > .installing-updates-btn {
    display: inline;
  }

  &.latest > .up-to-date-label,
  &.done > .up-to-date-label {
    display: inline;
  }

  &.error > .install-error-label,
  &.notify > .install-error-label {
    display: inline;
  }

  &.has-release-notes > .release-notes-btn {
    display: inline;
  }

  &.checking,
  &.available,
  &.downloading,
  &.reload,
  &.downloaded,
  &.installing,
  &.error,
  &.notify {
    .check-for-updates-btn {
      display: none;
    }
  }
}

.save-btn {
  .save-btn-label-save { display: none; }
  &.next {
    .save-btn-label-next { display: inline; }
    .save-btn-label-save { display: none; }
  }
  &.save {
    .save-btn-label-next { display: none; }
    .save-btn-label-save { display: inline; }
  }
}


// Help
// --------------------------------------------------

.help-container {
  .help-btn-container {
    margin-top: 40px;
  }

  .btn {
    margin-right: 10px;
  }
}
