.starRating {
  position: relative;
  display: inline-block;
  white-space: nowrap;
  line-height: 1;
  cursor: pointer;
  /* Allow the star rating to be interacted with in the metadata table */
  pointer-events: all;
  user-select: none;
}

.stars {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
}

.dot,
.star {
  margin-left: 2px;
}

.dot {
  opacity: 0;
  transition: opacity $starSpeed;
}

.star {
  top: 0;
  flex-shrink: 0;
  transform: scale(1);

  &.hidden {
    opacity: 0;
  }
}

.emptyStar {
  composes: star;

  opacity: 0.6;
  transition: opacity $starSpeed ease, transform $starSpeed ease;
}

.fullStar {
  composes: star;

  opacity: 1;
  transition: opacity $starSpeed ease, transform $starSpeed ease;

  &.pulsing {
    transition: opacity $starSpeed ease,
      transform $starPulseSpeed $starPulseTimingFunction;
    transform: scale($starPulseScale);
  }
}

.isReadOnly {
  cursor: default;
}

.isUnrated {
  .dot {
    opacity: 1;
  }

  .star {
    opacity: 0;
    transform: scale(0);
  }
}

.slider {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
  user-select: none;
}

.track {
  width: 100%;
  height: 100%;
  /* Absolute position and no content makes clicks not register in IE10 */
  background-color: rgba(0, 0, 0, 0);
}
