.item {
  &:hover,
  &.isActionsMenuOpen {
    background-color: $stripeListHoverBackgroundColor;

    .transientTitle {
      opacity: 0;
    }

    .button {
      opacity: 1;
    }
  }

  &.hasActiveSelection,
  &.isForcingSelection {
    .underlay {
      display: none;
    }

    .overlay a,
    .overlay button {
      pointer-events: none;
    }

    .transientTitleLeft {
      opacity: 1;
    }

    .transientTitleRight,
    .playButton,
    .editButton,
    .moreButton,
    .downloadIcon {
      opacity: 0;
    }
  }

  &.isSelected {
    background-color: $stripeListSelectedBackgroundColor;

    .transientTitleLeft {
      opacity: 1;
    }

    .selectButton {
      opacity: 1;
    }
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  display: flex;
  justify-content: flex-start;
  color: $dimColor;
  line-height: $tableRowHeight;

  a {
    color: $dimColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.playCell {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0 0 $playCellWidth;
  flex-shrink: 0;
  overflow: hidden;
}

.actionsCell {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0 0 $actionsCellWidth;
  overflow: hidden;
  padding: 0 10px;

  .button {
    padding: 0 10px;
  }
}

.libraryActionsCell {
  composes: actionsCell;

  justify-content: flex-start;
  flex: 0 0 calc($actionsCellWidth + 40px);
  padding-right: 50px;
}

.transientTitle {
  position: absolute;
  top: 0;
  opacity: 1;
  transition: opacity $defaultSpeed;
}

.transientTitleLeft {
  composes: transientTitle;

  left: $tableCellPadding;
}

.transientTitleRight {
  composes: transientTitle;

  right: $tableCellPadding;
}

.transientTitleRightText {
  margin-left: $tableCellPadding;
}

.button {
  opacity: 0;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 1;
  }
}

.playButton {
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: button;

  transition: opacity $defaultSpeed;
}

.selectCoverButton {
  @add-mixin cover;
  cursor: default;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';
  composes: button;

  height: $tableRowHeight;
  transition: opacity $defaultSpeed;
}

.editButton,
.moreButton {
  composes: button;

  transition: opacity $defaultSpeed;
}

.equalizer {
  composes: equalizer from '~common/js/views/v3/components/core/icons/EqualizerIcon.css';

  position: absolute;
  top: 50%;
  left: $tableCellPadding;
  transform: translateY(-50%);
}
