.deviceInfoBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
}

.deviceInfoForm {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.channelInfo {
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
  margin-bottom: 30px;
  min-height: 42px;
}

.channelDetails {
  display: flex;
  flex-grow: 1;
  overflow-y: hidden;
}

.deviceDetails {
  margin-right: 30px;
  width: $dvrSetupImageWidth;
}

.countryInputGroup {
  margin-bottom: 0;
}

.channelScanSourceSelect {
  composes: input from '~core/components/forms/SelectInput.css';

  margin-top: 10px;
  width: 100%;
}

.deviceChannelsContainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.deviceChannels {
  composes: vertical from '~core/components/base/Scroller.css';

  /*
    flex-basis set to 1px allows the flexbox to grow properly in Edge/IE 11,
    otherwise IE11/Edge overflow and don't scroll properly.
  */
  flex: 1 0 1px;
  background-color: rgba(255, 255, 255, 0.03);
}

.deviceChannelsInfo {
  composes: deviceChannels;

  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
}
