/*
 * Panes
 */

.panesContainer {
  position: relative;
  max-height: inherit;
  width: calc($menuLargeWidth * 2);
}

.pane {
  display: inline-block;
  max-height: inherit;
  width: $menuLargeWidth;
  vertical-align: top;
  transition: transform $defaultSpeed, opacity $defaultSpeed;
}

.filtersPane {
  composes: pane;
}

.filterOptionsPane {
  composes: pane;
}

/*
 * States
 */

.showFilterOptions {
  .filtersPane,
  .filterOptionsPane {
    transform: translateX(-$menuLargeWidth);
  }

  .filtersPane {
    opacity: 0;
  }
}
