.troubleshootingSection {
  margin-bottom: 40px;
}

.buttonContainer {
  display: flex;
  margin-bottom: 10px;
}

.buttonStatus {
  display: flex;
  align-items: center;
  padding-left: 10px;

  i {
    padding-top: 4px;
    padding-right: 10px;
  }
}

.buttonStatusSuccess {
  composes: buttonStatus;

  color: $successColor;
}

.buttonStatusError {
  composes: buttonStatus;

  color: $dangerColor;
}

.button {
  margin-right: 10px;
}

.description {
  color: $dimColor;
}
