.status {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -$v4CardBorderRadius;
  padding: calc(10px + $v4CardBorderRadius) 10px 10px;
  border-radius: 0 0 $v4CardBorderRadius $v4CardBorderRadius;
  background-color: rgba(0, 0, 0, 0.15);
}

.label {
  color: rgba(250, 250, 250, 0.75);
}

.playedBadge {
  composes: playedBadge from '~desktop/common/js/v4/core/ui/badges/components/PlayedBadge.css';

  margin-right: 8px;
}

.watchlistBadge {
  composes: watchlistBadge from '~desktop/common/js/v4/core/ui/badges/components/WatchlistBadge.css';

  margin-right: 8px;
}

.recordingBadge {
  composes: recordingBadge from '~desktop/common/js/v4/core/ui/badges/components/RecordingBadge.css';

  margin-right: 8px;
  margin-bottom: -4px;
}
