.container {
  display: flex;
  margin: 15px 0;
}

.step {
  margin-right: 10px;
  width: 20px;
  height: 20px;
}

.done {
  composes: badge from '~app/desktop/common/js/v4/core/ui/badges/components/Badge.css';
  composes: step;

  background-color: #75ae94;
}

.active {
  composes: spinner from '~app/desktop/common/js/v4/core/ui/spinners/components/Spinner.css';
  composes: step;
}

.waiting {
  composes: badge from '~app/desktop/common/js/v4/core/ui/badges/components/Badge.css';
  composes: step;

  background-color: $v4BaseMedium;
}
