.container {
  position: relative;
}

.scrollContainer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  visibility: hidden;
  overflow: hidden;
}

.expandContent {
  position: absolute;
  top: 0;
  left: 0;
  width: 100000px;
  height: 100000px;
}

.shrinkContent {
  composes: expandContent;

  width: 200%;
  height: 200%;
}
