.photoInformation {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  background: $playerPanelColor;
}

.scroller {
  composes: vertical from '~core/components/base/Scroller.css';

  flex: 1;
  padding-right: 0;
}

.exif {
  padding: 10px 20px;
  background: $playerSecondaryOverlayColor;
}

.line {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 5px 0;
}

.split {
  composes: line;

  border-top: 1px solid $defaultColor;
}

.format {
  padding: 0 4px;
  height: $largeFontSize;
  border-radius: 2px;
  background: $quietColor;
  color: $inverseColor;
  text-transform: uppercase;
  font-size: $smallFontSize;
  line-height: $largeFontSize;
}

.map {
  display: flex;
  align-items: stretch;
  height: 280px;
}
