.progress {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: $posterProgressHeight;
  opacity: 1;
  transition: opacity $defaultSpeed;
}

.progressTrack {
  @add-mixin cover;
  background-color: $posterProgressTrackBackgroundColor;
}

.progressBar {
  @add-mixin cover;
  width: 100%;
  background-color: $v4AccentLight;
  transition: transform 1s ease-in-out;
}
