$playPauseButtonSize: 100px;

.overlay {
  @add-mixin cover;

  cursor: pointer;
}

.playButton {
  composes: overlay;
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';

  transition: opacity $defaultSpeed, transform $defaultSpeed;

  &:global(.isFocused:not(.isDisabled)) {
    box-shadow: none;
  }
}

.pauseButton {
  composes: playButton;

  opacity: 0;
  transform: scale(1.2);
}

.playCircle {
  @add-mixin absoluteCenter;
  composes: playCircle from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';

  width: $playPauseButtonSize;
  height: $playPauseButtonSize;
  border-color: $primaryBackgroundColor;
  background-color: $primaryBackgroundColor;
  box-shadow: 0 0 4px $playerShadowColor;
  color: $defaultLinkHoverColor;
  font-size: 28px;
  line-height: $playPauseButtonSize;
}

.hiddenCursor {
  cursor: none;
}
