.container {
  flex-shrink: 0;
  margin-right: 40px;
  padding: 10px;
  width: 220px;
  border-radius: 6px;
  background-color: $v4BaseMediumLight;
}

.section {
  padding: 10px 0;
  border-bottom: 1px solid $v4BaseLight;
}

.make {
  color: $v4DefaultColor;
  font-size: 11px;
  line-height: 15px;
}

.model {
  color: $v4DefaultColor;
  font-size: 13px;
  line-height: 24px;
}

.moreInfo {
  color: $v4AltMedium;
  font-size: 10px;
  line-height: 14px;
}

.channelsMessage {
  margin-top: 10px;
}

.button {
  composes: button from '~desktop/common/js/v4/core/ui/buttons/components/SpinnerButton.css';

  margin-top: 25px;
  width: 100%;
}

.disabledText {
  margin: 25px 0;
  color: $v4AltMediumDark;
  text-align: center;
}
