.overlay {
  @add-mixin cover;
  overflow: hidden;
  user-select: none;
}

.background {
  @add-mixin cover;
  background: $posterHoverBackgroundColor;
  opacity: 0;
  transition: opacity $defaultSpeed;
}

.link {
  @add-mixin cover;
}

.subscriptionBadge {
  composes: badge from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';

  top: 0;
  right: 0;
  background-color: $recordBackgroundColor;
}

.subscriptionBadgeIcon {
  margin-right: 2px;
  font-size: 14px;
}

/*
 * Progress Bar
 */

.progress {
  composes: progress from './PosterCardOverlayProgress.css';
}

/*
 * Buttons
 */

.button {
  composes: button from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';
}

.playButton,
.recordButton {
  @add-mixin absoluteCenter;
  composes: recordButton from '~desktop/common/js/views/v3/components/core/buttons/RecordButton.css';
  composes: button;
}

.playCoverButton {
  composes: playButton;

  width: 100%;
  height: 100%;
}

.playCircle {
  composes: playCircle from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';
}

.recordCoverButton {
  composes: recordButton;

  width: 100%;
  height: 100%;
}

.recordCircle {
  composes: recordCircle from '~desktop/common/js/views/v3/components/core/icons/RecordIcon.css';
  composes: centerCircle from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';
}

.recordIcon {
  composes: recordIcon from '~desktop/common/js/views/v3/components/core/icons/RecordIcon.css';

  width: 12px;
  height: 12px;
}

.recordButtonBottom {
  composes: recordButton;

  top: auto;
  bottom: 0;
  width: 20px;
  height: 20px;
}

.recordCircleBottom {
  composes: recordCircle;

  width: 20px;
  height: 20px;
}

.recordIconBottom {
  composes: recordIcon from '~desktop/common/js/views/v3/components/core/icons/RecordIcon.css';

  width: 6px;
  height: 6px;
}

/*
 * Hover and Selected States
 */

.overlay:hover,
.isActionsMenuOpen {
  .background {
    background: $posterHoverBackgroundColor;
    opacity: 1;
  }

  .progress {
    opacity: 0;
  }

  .button {
    opacity: 1;
    transform: translateY(0);
  }

  .playButton,
  .recordButton {
    transform: translate(-50%, -50%);
  }
}
