.plexPassTitle {
  font-size: 48px;
}

.header {
  composes: modalHeader from '~core/components/modals/ModalHeader.css';

  padding: 30px;
  text-align: center;
  font-size: 24px;
}

.body {
  composes: innerModalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-right: 120px;
  padding-left: 120px;
  font-size: $largeFontSize;
}

.moreFeatures {
  flex: 0 0 auto;
}

.footer {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  display: block;
  padding: 30px;
  text-align: center;
  font-size: $largeFontSize;
}

.planButton {
  @add-mixin truncate;
  display: inline-block;
  margin: 0 20px 20px;
  width: 240px;
}

.link {
  box-shadow: inset 0 -1px 0 0 rgba(255, 255, 255, 0.5);
}

.spinnerContainer {
  padding: 5px 0 25px;
}

.plansContainer {
  opacity: 1;
  transition: opacity $slowSpeed;

  &.isPlansPending {
    opacity: 0;
  }
}

.marketingFrame {
  width: 100%;
  height: 100%;
  border: 0;
}

/*
 * Breakpoints
 */

@media (max-width: $breakpointLarge) {
  .body {
    padding-right: 60px;
    padding-left: 60px;
  }
}

@media (max-width: $breakpointMedium), (max-height: $breakpointSmall) {
  .header,
  .footer {
    padding: 15px;
  }

  .body {
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: $breakpointSmall) {
  .planButton {
    width: 80%;
  }
}
