.baseForm,
.advancedForm,
.cancelConfirmation {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.advancedSettings,
.baseSettings {
  display: none;
}

.advancedForm .advancedSettings {
  display: block;
}

.baseForm .baseSettings {
  display: block;
}

.footer {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  justify-content: space-between;
}

.cancelConfirmationFooter {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  justify-content: flex-end;
}

.advancedIcon {
  top: -1;
  margin-right: 10px;
}
