.container {
  display: flex;
  align-items: center;
  flex: 1 1 220px;
  padding-right: 20px;
  min-width: 0;
}

.titleContainer {
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.title {
  @add-mixin truncate;
  color: $defaultColor;
}

.subtitle {
  @add-mixin truncate;
}

.statusContainer {
  display: flex;
  align-items: center;
}

.status {
  @add-mixin truncate;
  color: $dimColor;
}

.admin {
  margin-left: 5px;
  color: $selectedColor;
}
