.container {
  display: flex;
  align-items: flex-start;
  margin-bottom: 24px;
  padding-bottom: 18px;
  border-bottom: 1px solid $dividerColor;
}

.message {
  margin-right: 20px;
  max-width: 420px;
  color: $quietColor;
}

.messageHeader {
  color: $quietColor;
  font-size: $largeFontSize;
}

.messageDetails {
  color: $defaultColor;
}

.button {
  composes: button from '~core/components/buttons/SpinnerButton.css';

  flex-shrink: 0;
}
