.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: $trackCellPadding $trackCellPadding $trackCellPadding 0;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.container {
  display: flex;
  align-items: center;
  flex-grow: 1;
  overflow: hidden;
}

.indexPlayContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 40px;
  padding-right: 5px;
}

.posterContainer {
  flex-shrink: 0;
  margin-right: 20px;
}

.titleContainer {
  flex-grow: 1;
  overflow: hidden;
  padding-right: 20px;
  width: 100%;
}

.title {
  @add-mixin truncate;
  overflow: hidden;
  width: 100%;
}

.subtitle {
  composes: title;

  color: $dimColor;

  a {
    color: $dimColor;
  }
}

.duration {
  color: $dimColor;
}

.actions {
  display: flex;
  align-items: stretch;
  align-self: stretch;
  flex-shrink: 0;
}

.actionsMenuButton {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  padding: 0 10px;
}
