/* See: https://bugzilla.mozilla.org/show_bug.cgi?id=984869 */
.menuItemContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.menuLabel {
  flex-grow: 1;
}

.selectedIcon {
  flex-shrink: 0;
  font-size: $defaultIconFontSize;
  line-height: calc($defaultFontSize * $lineHeightComputed);
}

.isSelected {
  color: $selectedColor;

  /* Keep the icon the selected color on hover. */
  .selectedIcon {
    color: $selectedColor;
  }
}
