.hubCell {
  composes: hubCell from '~desktop/common/js/views/v3/components/metadata/lists/HubCell.css';
}

.hubCellHeader {
  composes: hubCellHeader from '~desktop/common/js/views/v3/components/metadata/lists/HubCell.css';

  /* Ensure right-aligned header components align to the right edge of the last
  poster. The posters are inset 5px from the sides of the container to reveal
  their shadow and flip effects. */
  padding-right: 5px;
}

.titleSeparator {
  composes: titleSeparator from '~desktop/common/js/views/v3/components/metadata/lists/MetadataHubCellHeader.css';
}

.sourceTitle {
  composes: sourceTitle from '~desktop/common/js/views/v3/components/metadata/lists/MetadataHubCellHeader.css';
}

.hubScroller {
  composes: hubScroller from '~desktop/common/js/views/v3/components/metadata/lists/HubCell.css';
}

.errorSeparator {
  composes: titleSeparator;

  margin: 0 10px;
}

.errorTitle {
  composes: sourceTitle;

  text-transform: uppercase;
  font-weight: normal;
}

.iconButton {
  margin-left: 20px;
  white-space: nowrap;
  font-size: $largeFontSize;
}

.iconButtonIcon {
  top: -1px;
}
