.container {
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 4px;
  background-color: #1f1f1f;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.6);
  color: $defaultColor;
  font-size: 11px;
}

.separator {
  height: 1px;
  background-color: rgba(255, 255, 255, 0.1);
}

.LegendLabel {
  composes: legendLabel from '~desktop/common/js/views/v3/components/core/charts/LegendLabel.css';

  color: $defaultColor;
}

.label {
  margin-bottom: 10px;
  text-align: center;
}

.values {
  margin: 15px 0;
}

.valueRow {
  display: flex;
  align-items: center;
}

.total {
  margin-top: 10px;
  text-align: center;
}
