.controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
}

.buttonGroup {
  display: flex;
  align-items: center;
  /* Debug */
  /* background-color: rgba(255, 255, 255, 0.25); */
}

.buttonGroupLeft {
  composes: buttonGroup;

  flex: 1 0 0;
  min-width: 0;
}

.buttonGroupCenter {
  composes: buttonGroup;

  justify-content: center;

  flex: 0 1 0;
  margin: 0 $playerControlsContentSpacing;
}

.buttonGroupRight {
  composes: buttonGroup;

  justify-content: flex-end;
  flex: 1 0 0;
  padding-right: $playerControlsContentSpacing;
}

.volumeButton {
  composes: playerButton from '~desktop/common/js/views/v3/components/players/core/PlayerIconButton.css';

  margin-right: calc($playerVolumeSliderWidth - $playerControlsContentSpacing);
}

.balanceLeft {
  padding-left: calc($playerIconButtonSize + $playerIconButtonSpacing);
}

.volumeSlider {
  composes: slider from '~desktop/common/js/views/v3/components/players/core/VolumeSlider.css';

  position: absolute;
  right: 5px;
  bottom: 18px;
  width: $playerVolumeSliderWidth;
  height: $playerVolumeSliderHeight;
}

@media screen and (max-width: $playerControlsNarrowBreakpoint) {
  /* The center controls (now on the left) are nudged to the right to keep the
  first icon button aligned to the left edge of the metadata text. */
  $buttonGroupCenterNudge: calc(
    $playerControlsPosterLeftGutter + $playerControlsContentSpacing -
      $playerIconButtonSpacing
  );

  .controls {
    flex-wrap: wrap;
  }

  .buttonGroupLeft {
    flex-basis: 100%;
    margin-bottom: 5px;
    padding-right: $playerControlsContentSpacing;
  }

  .buttonGroupCenter {
    /* Eliminate the right margin to allow 8 center buttons + 6 right buttons to
    display inline at $playerControlsMinWidth (640px). */
    margin-right: 0;
  }

  .balanceLeft {
    padding-left: 0;
  }

  .hasLandscapePoster {
    .buttonGroupCenter {
      margin-left: calc(
        $playerControlsLandscapePosterWidth + $buttonGroupCenterNudge
      );
    }
  }

  .hasSquarePoster {
    .buttonGroupCenter {
      margin-left: calc(
        $playerControlsSquarePosterWidth + $buttonGroupCenterNudge
      );
    }
  }
}
