.menuPortal {
  position: absolute;
  z-index: $menuZIndex;
}

.menuScroller {
  composes: vertical from '~core/components/base/Scroller.css';

  padding-right: 0;
}

.menu {
  @add-mixin resetFont;
  padding: $menuPadding 0;
  border-radius: 4px;
  background-color: $menuBackgroundColor;
  box-shadow: 0 4px 10px $shadowColor;
  transition: background $menuSpeed ease-out;
  transform-origin: center top;
}

.isDisabled {
  background-color: $menuDisabledBackgroundColor;
}

/*
 * Sizes
 */

.small {
  width: $menuSmallWidth;
}

.medium {
  width: $menuMediumWidth;
}

.large {
  width: $menuLargeWidth;
}
