.container {
  position: absolute;
  display: block;
  padding: 10px 0;
  width: 100%;
  height: 100%;
}

.ticks {
  position: relative;
  width: 100%;
  height: 100%;
}

.tick {
  position: absolute;
  left: 0;
  height: 1px;
  background: $quietColor;
}

.minor {
  composes: tick;

  width: 4px;
}

.major {
  composes: tick;

  width: 6px;
}

.label {
  position: absolute;
  top: 50%;
  right: 0;
  left: 10px;
  color: $quietColor;
  font-size: $smallFontSize;
  line-height: 1;
  transform: translateY(-50%);
}
