//
// Players
// --------------------------------------------------


// Base Styles
// -------------------------

@player-slider-track-height: 5px;
@player-slider-thumb-size: 12px;
@player-slider-click-padding: 9px;

@mini-player-below-track-height: @mini-player-height - @player-slider-track-height;

@media-title-width: 220px;

.player-close-btn {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  padding: 30px;
  font-size: @font-size-large + 8;
  line-height: @font-size-large + 8;
}

.seek-bar-container {
  position: relative;
}

.player-slider {
  cursor: pointer;
  touch-action: none;
  height: @player-slider-track-height + @player-slider-click-padding * 2;
  margin-top: -@player-slider-click-padding;
  margin-bottom: -@player-slider-click-padding;

  &:hover,
  &.active {
    .player-slider-thumb {
      .opacity(1);
    }
  }
  &.disabled {
    cursor: auto;
  }
}

.player-scrub-preview {
  position: absolute;
  bottom: 50px;
}

.player-scrub-preview-tooltip {
  bottom: 0;
  left: 50%;
  .opacity(0.8);
  .translate(-50%, 0);
}

.player-scrub-preview-thumbnail {
  margin-bottom: 5px;
  border: 2px solid @black;
}

.mini-player-scrub-preview {
  position: absolute;
  bottom: @mini-player-below-track-height + 12px;
  background-color: @tooltip-bg;
  border-radius: 4px;
  pointer-events: none;
}

.mini-controls-numeric-progress {
  color: @gray-alt;
  min-width: 130px;
  justify-content: center;

  > .player-position {
    color: @orange;
  }

  > .separator {
    font-size: @font-size-large;
    padding: 0 8px;
  }
}

.player-slider-track {
  position: relative;
  height: @player-slider-track-height;
  overflow: hidden;
  background-color: @gray-darkest;
  top: @player-slider-click-padding;

  .player-slider-buffer,
  .player-slider-progress {
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    background-color: @orange;
  }

  .player-slider-buffer {
    background-color: @gray;
  }
}

.player-slider-thumb {
  position: relative;
  top: (@player-slider-track-height + @player-slider-thumb-size) / -2 + @player-slider-click-padding;
  width: @player-slider-thumb-size;
  height: @player-slider-thumb-size;
  margin-left: @player-slider-thumb-size / -2;
  background-color: @gray-lightest;
  border-radius: 50%;
  .opacity(0);
  .transition(opacity 0.2s);
  z-index: 1;

  &:hover {
    background-color: @white;
  }
}

.repeat-btn {
  .repeat {
    display: inline-block;
  }

  .repeat-one {
    display: none;
  }

  &.one {
    .repeat {
      display: none;
    }

    .repeat-one {
      display: inline-block;
    }
  }
}

.volume-btn {
  .volume-up {
    display: inline-block;
  }

  .volume-down,
  .mute {
    display: none;
  }

  &.low {
    .volume-down {
      display: inline-block;
    }

    .volume-up,
    .mute {
      display: none;
    }
  }

  &.mute {
    .mute {
      display: inline-block;
    }

    .volume-up,
    .volume-down {
      display: none;
    }
  }
}


// Video
// -------------------------

.video-player,
.photo-player {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.photo-player {
  z-index: @zindex-player;
  background-color: @black-light;
}

.video-player {
  z-index: @zindex-video-player;
  padding: 30px;
}

.video-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;

  iframe {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
  }
}

.video-controls-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.video-controls {
  width: 100%;
  height: 100%;
}

.video-details-container {
  display: none;
  color: @gray-light;
}

.html-video,
.video-background,
.video-background-image,
.video-overlay-btn,
.video-error-message-container,
.video-post-play,
.flash-video-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.flash-video {
  position: absolute;
  top: 80px;
  right: 80px;
  bottom: 80px;
  left: 80px;

  object {
    outline: none;
  }
}

.html-video,
.flash-video,
.flash-video-background {
  display: none;
}

.video-background {
  background-color: @gray-darkest;
}

.video-background-image {
  background-position: center;
  background-size: cover;
  .transition(opacity 0.5s);

  &.transition-out {
    .opacity(0);
  }
}

.html-video,
.flash-video-background {
  background-color: @black; // This helps with performance.
}

.video-background-image {
  .opacity(0.3);
}

.video-loading {
  display: none;
}

.video-overlay-btn {
  background: none;
  border: 0;

  &:focus {
    outline: 0;
  }
}

.video-error-message-container {
  display: none;
}

.video-error-message-center {
  display: table-cell;
  vertical-align: middle;
  cursor: pointer;
}

.video-error-message {
  font-size: @font-size-large;
  text-align: center;
  vertical-align: middle;
}

.video-error-skip-message {
  font-size: @font-size-large;
  text-align: center;
  vertical-align: middle;
}

.alert-player {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 130px;
  height: 130px;
  margin: -65px 0 0 -65px;
  font-size: 60px;
  line-height: 142px;
  text-align: center;
  background-color: @black;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 10px;
  .opacity(0);
  .scale(1.2);
  .transition(~"opacity 0.2s, -webkit-transform 0.2s");
}


// Video Controls

@video-player-controls-height: 50px;

@video-player-width-base: 960px;
@video-player-height-base: 540px;

@video-player-poster-width: 100px;
@video-player-poster-standard-height: floor(@video-player-poster-width * @poster-standard-ratio);
@video-player-poster-square-height: @video-player-poster-width * @poster-square-ratio;
@video-player-poster-widescreen-height: floor(@video-player-poster-width * @poster-widescreen-ratio);

.video-controls-overlay {
  position: absolute;
  right: 0;
  left: 0;
  height: @video-player-controls-height;
  padding: 0 10px;
  background-color: @black;
  background-color: rgba(0, 0, 0, 0.8);
  .transition(all 0.2s);
}

.video-controls-overlay-top {
  top: 0;
  .transition(all 0.2s ease 0.1s);
}

.video-controls > .player-close-btn {
  display: none;
}

.video-controls.transition-out {
  .video-overlay-btn {
    cursor: none;
  }

  > .player-close-btn {
    display: inline-block;
  }

  .video-player-poster {
    .translate(0, @video-player-poster-standard-height + 10);
  }

  .video-controls-overlay-top,
  .video-player-alert {
    .translate(0, -@video-player-controls-height);
  }
  .video-controls-overlay-bottom {
    .translate(0, @video-player-controls-height);
  }
  .mini-controls {
    .transition(~"bottom 0.4s");
    bottom: -@mini-player-height;
  }
}

.video-controls-overlay-bottom {
  bottom: 0;
  padding-left: 130px;

  .video-controls-center-wrapper {
    overflow: hidden;

    .video-controls-center {
      position: relative;
      margin-top: 14px;

      .player-position,
      .player-duration {
        width: 60px;
        color: @gray-light;
      }

      .player-position {
        float: left;
        text-align: right;
      }

      .player-duration {
        float: right;
        text-align: left;
      }

      .seek-bar-container {
        position: absolute;
        right: 80px;
        left: 80px;
        top: 10px;
      }
    }
  }
}

.video-player-poster {
  position: absolute;
  bottom: 10px;
  left: 10px;
  width: @video-player-poster-width;
  height: @video-player-poster-standard-height;
  -webkit-transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
}

.video-controls-left {
  float: left;
  margin-right: 10px;
  font-size: 0;
}

.video-controls-overlay-top > .video-controls-left {
  width: 50%;
  font-size: @font-size-large + 2;
  line-height: @video-player-controls-height;
  color: @gray-lighter;

  .video-title {
    display: inline-block;
    max-width: 100%;
    .text-overflow();
  }
}

.video-controls-right {
  float: right;
  margin-left: 10px;
  font-size: 0;

  > .player-volume-slider-container,
  > .volume-btn {
    display: none;
  }

  > .player-volume-slider-container {
    float: left;
    width: 70px;
    margin: 23px 15px 0 5px;
  }
}

.video-controls-left,
.video-controls-right {
  .btn-link {
    padding: 0 10px;
    font-size: @font-size-large + 4;
    line-height: @video-player-controls-height;
    color: @gray-lighter;

    &:hover,
    &:focus {
      color: @white;
    }

    &.disabled {
      color: @gray-lighter;
    }

    &.previous-btn,
    &.next-btn {
      font-size: @font-size-large;
    }
    &.volume-btn {
      float: left;
    }
  }

  .btn-danger {
    padding: 8px;
    font-size: @font-size-base;
    line-height: @line-height-computed;
    color: lighten(@brand-danger, 15%);

    &:hover,
    &:focus {
      color: @link-hover-color;
    }
  }

  .dropup {
    display: inline-block;
    position: relative;
  }

  .dropdown, .dropup {
    &.open > .btn-link {
      color: @orange-light;
    }
  }

  .player-close-btn {
    position: relative;
    top: auto;
    right: auto;
  }

  .player-dropdown-selected-icon {
    display: none;
  }
  .selected .player-dropdown-selected-icon {
    display: inline-block;
  }
}

.video-controls-left > .btn-link {
  float: left;
}

.video-controls-right > .btn-link,
.video-controls-right > .dropup,
.video-controls-right > .dropdown {
  float: right;
}

.player-primary-player-name-container {
  position: absolute;
  bottom: @video-player-controls-height;
  width: 100%;
  padding-bottom: 10px;
  text-align: center;

  .primary-player-name {
    display: inline-block;
    max-width: 50%;
    font-size: @font-size-large;
    .text-overflow();
    .opacity(0.7);
  }
}

/* Extra small devices (phones, 479px and lower) */
@media (max-width: @screen-xs-max) {
  .video-controls-overlay-bottom {
    padding-left: 10px;
  }

  .video-player-poster {
    display: none;
  }
}


// Image Overlays

.video-image-overlays-region {
  display: none;
}


// Video subtitles

.video-player .video-subtitles-renderer {
  position: absolute;
  width: 100%;
  height: 100%;
}

// Alerts

.video-alert-region {
  display: none;
}

.video-player-alert {
  position: absolute;
  top: @video-player-controls-height + 20px;
  right: 20px;
  left: auto;
  z-index: 0;
  width: auto;
  max-width: 350px;
  background-color: @black;
  background-color: rgba(0, 0, 0, 0.7);

  &.transition-in,
  &.transition-out {
    .opacity(0);
    .translate(0, 0);
  }
}


// States

.video-player.buffering {
  .loading {
    display: block;
  }
}

.video-player.paused {
  .alert-paused {
    .opacity(1);
    .scale(1);
  }
}

.video-player.show-html-video .video-controls {
  > .html-video,
  > .video-image-overlays-region,
  > .video-alert-region {
    display: block;
  }

  > .flash-video,
  > .player-close-btn {
    display: none;
  }
}

.video-player.show-flash-video .video-controls {
  > .flash-video-background,
  > .flash-video {
    display: block;
  }

  > .player-close-btn {
    display: inline-block;
  }

  > .html-video,
  > .video-image-overlays-region,
  > .video-alert-region,
  > .video-background,
  > .video-loading,
  > .video-overlay-btn,
  > .alert-player,
  > .video-controls-overlay,
  > .video-error-message-container {
    display: none;
  }
}

// The error state needs to be below show-flash-video to override its styles.
.video-player.error .video-controls {
  > .video-error-message-container {
    display: table;
  }

  > .player-close-btn {
    display: inline-block;
  }

  > .html-video,
  > .flash-video,
  > .video-image-overlays-region,
  > .video-alert-region,
  > .video-overlay-btn,
  > .alert-player,
  > .video-loading {
    display: none;
  }

  > .video-player-poster {
    .translate(0, @video-player-poster-standard-height + 10);
  }

  > .video-controls-overlay-top {
    .translate(0, -@video-player-controls-height);
  }
  > .video-controls-overlay-bottom {
    .translate(0, @video-player-controls-height);
  }
}


// Video post play

@poster-post-play-height: 140px;
@poster-post-play-width: @poster-post-play-height / @poster-widescreen-ratio;
@poster-post-play-height-large: 190px;
@poster-post-play-width-large: (@poster-post-play-height-large) / @poster-widescreen-ratio;

.video-post-play {
  background-color: @black-light;

  h1, h2, h3 {
    margin: 0;
    font-size: @font-size-large * 1.5;
    text-shadow: 0 1px 2px @black;
  }
  h2, h3 {
    color: @gray-light;
  }

  .item-poster > h1 {
    height: 40px;
    font-size: @font-size-base;
    text-transform: uppercase;
  }

  .post-play-footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 270px;
    #gradient > .vertical(@start-color: transparent; @end-color: rgba(0, 0, 0, 0.7); @start-percent: 45%; @end-percent: 45%);
  }

  .item-previous {
    float: left;
    margin: 0px 20px 20px 40px;

    h1, h2 {
      width: @poster-post-play-width;
      font-size: @font-size-base;
      .text-overflow();
    }

    .media-poster {
      width: @poster-post-play-width;
      height: @poster-post-play-height;
    }

    .item-summary {
      h1, h2 {
        margin-top: 5px;
      }
    }
  }

  .item-next {
    .item-poster {
      position: absolute;
      display: inline-block;
      height: @poster-post-play-height-large + 40;

      .media-poster {
        @box-shadow: 0 0 4px rgba(0, 0, 0, 0.3), inset 0 0 0 2px @orange;
        .box-shadow(@box-shadow);

        width: @poster-post-play-width-large;
        height: @poster-post-play-height-large;
      }
    }

    .item-summary {
      position: absolute;
      right: 0;
      left: @poster-post-play-width + @poster-post-play-width-large + 60;
      padding: 40px 40px 0 20px;

      h1, h2, h3 {
        height: 40px;
        .text-overflow();
      }

      h2 {
        float: left;
        max-width: 70%;
      }
      h3 {
        float: right;
        max-width: 30%;
      }

      p {
        display: block;
        display: -webkit-box;
        max-height: 120px;
        padding: 20px 0px;
        overflow: hidden;
        clear: right;
        text-overflow: ellipsis;
        text-overflow: -o-ellipsis-lastline;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
      }
    }
  }

  .countdown {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    margin: -40px 0 0 -40px;
    line-height: 80px;
    font-size: @font-size-large * 2;
  }

  .next-btn:hover .countdown {
    opacity: 0
  }
}


// Mini
// -------------------------

@mini-player-column-width: 300px;

@mini-player-poster-height: 80px;
@mini-player-poster-standard-width: floor(@mini-player-poster-height / @poster-standard-ratio);
@mini-player-poster-square-width: @mini-player-poster-height / @poster-square-ratio;

.mini-controls {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: @zindex-player;
  background-color: @gray-darker;

  height: @mini-player-height;
  color: @gray-lighter;
}

.mini-controls-left {
  width: @mini-player-poster-square-width;
  float: left;
  > .media-poster-container > .media-poster {
    height: @mini-player-poster-height;
  }
}

.mini-player.video {
  .mini-controls-left, .mini-controls-left .media-poster {
    width: @mini-player-poster-standard-width;
  }
}

.mini-controls-right {
  // Flex properties as prefixed by autoprefixer
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;

  height: @mini-player-below-track-height;
  padding: 0 12px;


  .dropup, .dropdown {
    position: relative;
  }

  .dropdown-menu .btn-link,
  .dropup-menu .btn-link {
    font-size: inherit;
  }

  .mini-controls-below-track-segment {
    // Flex properties as prefixed by autoprefixer
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -webkit-align-items: center;
        -ms-flex-align: center;
            align-items: center;

    margin: 0; // cancels out margin on rating control

    &:last-child {
      width: @media-title-width;
      -webkit-box-pack: end;
      -webkit-justify-content: flex-end;
          -ms-flex-pack: end;
              justify-content: flex-end;
    }

    > span {
      // Flex properties as prefixed by autoprefixer
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
    }

    .dropdown-menu {
      text-align: left;
      position: absolute;
    }

    > .btn-link, .dropdown-toggle {
      color: @gray-lighter;
      padding: 0 12px;
      vertical-align: middle;

      &:hover,
      &:focus {
        color: @white;
      }

      &.disabled {
        color: @gray-darkest;
        .opacity(1);
      }

      &.selected {
        color: @orange-light;
      }

      &.btn-link-lg {
        font-size: @font-size-large + 8;
      }
    }
  }

  > .media-title {
    display: block;
    min-width: @media-title-width;

    .btn-metadata-link {
      padding: 0;
      color: @gray-lighter;
      border: 0;
      background: none;
      max-width: @mini-player-column-width - @mini-player-poster-square-width - 12px;
      .text-overflow();
      cursor: default;

      &.clickable {
        &:hover,
        &:focus {
          cursor: pointer;
          text-decoration: underline;
          color: @white;
        }
      }
    }

    .item-title {
      color: #777;
    }

    .rating {
      font-size: @font-size-base;
    }

    .vendor-logo {
      display: none;
      height: 24px;
      background-repeat: no-repeat;
    }
  }

  .player-volume-slider {
    display: none;
    width: 82px;
    padding-right: 12px;
  }

}

.video-controls.show-volume .player-volume-slider-container,
.video-controls.show-volume .volume-btn,
.show-volume .mini-controls .player-volume-slider {
  display: block;
}


// Photo
// -------------------------

.photo-player .loading {
  z-index: 2;
}

.photo-player-controls {
  .previous-btn,
  .next-btn {
    position: fixed;
    top: 0;
    z-index: 1;
    width: 50%;
    height: 100%;
    font-size: @font-size-large * 2;

    .previous-icon,
    .next-icon {
      position: absolute;
      top: 50%;
      margin-top: -@font-size-large;
    }
  }

  .previous-btn {
    left: 0;

    .previous-icon {
      left: 30px;
      .transition(left 0.2s);
    }

    &:hover .previous-icon,
    &:focus .previous-icon {
      left: 20px;
    }
  }

  .next-btn {
    right: 0;

    .next-icon {
      right: 30px;
      .transition(right 0.2s);
    }

    &:hover .next-icon,
    &:focus .next-icon  {
      right: 20px;
    }
  }
}

.photo-player-container {
  position: absolute;
  left: 0;
  display: table;
  width: 100%;
  height: 100%;
  text-align: center;
  .opacity(1);
  .transition(~"left 0.4s, opacity 1s");

  &.fade {
    .opacity(0);
  }
}

.photo-details-container {
  display: table-cell;
  vertical-align: middle;
}

.photo-details {
  position: relative;
  z-index: 1;
  display: inline-block;

  &:hover,
  &.force-hover {
    .photo-details-info {
      .opacity(100);
    }
  }

  img {
    min-width: 200px;
    min-height: 200px;
  }

  .photo-details-info {
    position: absolute;
    right: 0;
    left: 0;
    text-align: left;
    .opacity(0);
    .transition(opacity 0.4s);
  }

  .photo-title {
    max-width: 70%;
    margin-bottom: 5px;
    .text-overflow();
  }

  .photo-subtitle {
    color: @gray-light;
  }

  .photo-summary {
    margin-top: 5px;
    color: @gray-light;
  }

  .media-actions {
    display: block;
    margin-top: 12px;
  }

  .media-action-btn {
    padding: 4px 8px;
    font-size: @font-size-large;
  }

  .media-actions-dropdown {
    right: 0;
  }
}

.photo-loading-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: @black;
  .opacity(0);
  .transition(opacity 0.5s ease 0.1s);
}

.show-photo-loading .photo-loading-overlay {
  .opacity(0.7);
}

.photo-video-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  .badge {
    position: absolute;
    top: 10px;
  }
}

.pull-center {
  transform: translateX(-50%);
  left: 50%;
}
