$appleColor: #fff;
$facebookColor: #4267b2;
$googleColor: #dd4b39;

.row {
  margin-top: 12px;
}

.rowLinked {
  composes: row;
}

.rowUnlinked {
  composes: row;

  color: $dimColor;
}

.separator {
  margin-right: 6px;
  margin-left: 6px;
  color: $dimColor;
}

.button {
  margin-right: 13px;
  width: 36px;
  height: 36px;
  background-position: center;
  background-size: 18px 18px;
  background-repeat: no-repeat;
}

.buttonApple {
  composes: button;

  background-color: $appleColor;
  background-image: url('~common/img/sign-in/auth-apple-560.svg');

  &:hover {
    background-color: darken($appleColor, 0.05);
  }

  &:active,
  &:global(.isActive) {
    background-color: darken($appleColor, 0.1);
  }
}

.buttonGoogle {
  composes: button;

  background-color: $googleColor;
  background-image: url('~common/img/sign-in/auth-google-560.svg');

  &:hover {
    background-color: darken($googleColor, 0.05);
  }

  &:active,
  &:global(.isActive) {
    background-color: darken($googleColor, 0.1);
  }
}

.buttonFacebook {
  composes: button;

  background-color: $facebookColor;
  background-image: url('~common/img/sign-in/auth-facebook-560.svg');

  &:hover {
    background-color: lighten($facebookColor, 0.1);
  }

  &:active,
  &:global(.isActive) {
    background-color: darken($facebookColor, 0.1);
  }
}
