.container {
  composes: pageHeader from '~desktop/common/js/views/v3/components/header/PageHeader.css';

  display: flex;
  align-items: center;
  padding: 20px;
  border: 2px solid $v4AccentLight;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.45);
}

.button {
  composes: iconActionButton from '~desktop/common/js/v4/core/ui/buttons/components/ActionButton.css';

  margin: 0 5px;
}

.icon {
  top: -1px;
  margin-right: 5px;
}

.count {
  flex-basis: 25%;
  color: $v4AccentLight;
  font-size: $defaultFontSize;
}

.actions {
  flex-basis: 50%;
  text-align: center;
  font-size: $defaultFontSize;
}

.deselect {
  flex-basis: 25%;
  text-align: right;
  font-size: $smallFontSize;
}
