.infoContainer {
  padding: 0 10px;
  opacity: 1;
  transition-delay: 1s;
  transition-duration: $slowSpeed;
  transition-property: opacity;
}

.info {
  padding: 0 6px;
  border-radius: 2px;
  background: $badgeBackgroundColor;
  color: $defaultBadgeColor;
  user-select: none;
}

.hidden {
  opacity: 0;
  pointer-events: none;
}
