$solidTableHeaderColor: #555c5c;

.tableHeader {
  composes: tableHeader from '~desktop/common/js/v4/core/ui/lists/components/tables/TableHeader.css';

  position: sticky;
  top: 0;
  z-index: 1;
}

.tableHeaderLink {
  composes: tableHeaderLink from '~desktop/common/js/v4/core/ui/lists/components/tables/TableHeaderLink.css';

  flex-grow: 1;
}

.tableHeaderCell {
  composes: tableHeaderCell from '~desktop/common/js/v4/core/ui/lists/components/tables/TableHeaderCell.css';

  background-color: $solidTableHeaderColor;
}

a > .tableHeaderCell:hover,
button > .tableHeaderCell:hover {
  background-color: lighten($solidTableHeaderColor, 0.1);
}

.activeTableHeaderCell {
  composes: tableHeaderCell;

  color: $defaultColor;
}

.firstTableHeaderCell {
  composes: tableHeaderCell;

  padding-left: calc($playCellWidth + $tableCellPadding);
}

.firstActiveTableHeaderCell {
  composes: firstTableHeaderCell;
  composes: activeTableHeaderCell;
}

.columnMenuButton {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  width: $v4PagePadding;
  height: $tableHeaderHeight;
  border-right: 2px solid rgba(0, 0, 0, 0.3);
  background-color: $solidTableHeaderColor;
  text-align: center;

  &:hover {
    background-color: lighten($solidTableHeaderColor, 0.1);
  }
}

.actionsTableHeaderCell {
  composes: tableHeaderCell;

  justify-content: center;
  padding: 0 $actionsCellPadding;
  width: $actionsCellWidth;
}
