.container {
  margin-bottom: $liveTVSettingsDeviceGap;
  padding: 0 15px 15px;
  width: $liveTVSettingsDeviceWidth;
  border-width: 0 0 0 $liveTVSettingsDeviceBorderSize;
  border-style: solid;
  background-color: $v4BaseMediumLight;

  &:nth-of-type(5n + 1) {
    border-color: $liveTVSettingsTuner1Color;
  }

  &:nth-of-type(5n + 2) {
    border-color: $liveTVSettingsTuner2Color;
  }

  &:nth-of-type(5n + 3) {
    border-color: $liveTVSettingsTuner3Color;
  }

  &:nth-of-type(5n + 4) {
    border-color: $liveTVSettingsTuner4Color;
  }

  &:nth-of-type(5n) {
    border-color: $liveTVSettingsTuner5Color;
  }
}

.modelContainer {
  display: flex;
  margin-bottom: 5px;
  border-bottom: 1px solid $v4BaseLight;
  font-size: $largeFontSize;
  line-height: 39px;
}

.model {
  @add-mixin truncate;
  flex-grow: 1;
}

.deviceActionButton {
  composes: iconButton from '~desktop/common/js/v4/core/ui/buttons/components/IconButton.css';

  margin-left: 10px;
}

.attribute {
  display: table-row;
  margin-top: 10px;
}

.attributeTitle {
  @add-mixin truncate;
  display: table-cell;
  color: #898989;
}

.attributeValue {
  display: flex;
  align-items: center;
  margin-left: 10px;
  min-height: calc($defaultFontSize * $lineHeightComputed);
  color: $v4AltMedium;
}

.enabledChannelsCount {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  margin-left: 8px;
}

.tuner {
  margin-right: 5px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #75ae94;
}

.activeTuner {
  background-color: #e75b61;
}

.deviceWarning {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
}

.deviceWarningIconContainer {
  margin-right: 8px;
  color: $dangerColor;
  font-size: $defaultIconFontSize;
}

@media only screen and (max-width: $breakpointLarge) {
  .container {
    width: 100%;
  }
}
