.lineHeightMeasure {
  position: absolute;

  &::before {
    content: '\00a0';
  }
}

.contentTransition {
  transition: max-height $defaultSpeed ease-in-out;
}

.readMore {
  composes: disclosureArrowButton from '~core/components/buttons/DisclosureArrowButton.css';

  color: $dimColor;
  text-transform: uppercase;
  font-size: $defaultFontSize;
  font-family: $boldFontFamily;
}

.readMoreArrow {
  composes: disclosureArrow from '~core/components/buttons/DisclosureArrowButton.css';

  border-color: $dimColor;
}
