.feature {
  flex: 1 0 50%;
}

/*
 * Flexbox in IE11 doesn't like padding on flex-child elements,
 * styling a separate element allows the flex child dimensions
 * to be calculated properly.
 * See https://github.com/plexinc/plex-web-client/issues/6259
*/

.featurePadding {
  padding: 30px;
}

.featureImage {
  float: left;
  margin: 8px 20px;
  width: 60px;
  height: 60px;
  user-select: none;
}

.featureContainer {
  overflow: hidden;
}

.featureTitle {
  font-family: $boldFontFamily;
}

.featureDescription {
  color: $dimColor;
}

/*
 * Breakpoints
 */

@media (max-width: $breakpointMedium), (max-height: $breakpointSmall) {
  .feature {
    padding: 10px;
  }
}

@media (max-width: $breakpointSmall) {
  .feature {
    flex-basis: 100%;
  }
}
