/* The +2px is a kludge to push out some text from the adjacent title detail
 * leaking into the collapsed sidebar.  In theory it should not be required.
 * https://github.com/plexinc/plex-web-client/pull/14379#pullrequestreview-243481975
 * */
$iconContainerPaddingRight: calc($sourceSidebarLeftGutter + 2px);

.button {
  composes: sidebarLink from '~core/components/sidebar/SidebarLink.css';

  display: flex;
  align-items: stretch;
  justify-content: space-between;
  padding: 0;
  width: 100%;
}

.isDim {
  .title,
  .iconContainer {
    color: $defaultLinkDisabledLinkColor;
  }
}

.isDisabled {
  composes: isDim;

  pointer-events: none;
}

.isSelected {
  background-color: $sourceSidebarSelectedColor;
  box-shadow: inset 4px 0 0 0 $selectedColor;

  .title,
  .iconContainer {
    color: $defaultLinkHoverColor;
  }
}

.isActive,
.button:hover {
  background-color: $sourceSidebarSelectedColor;
}

.sourceLink:hover {
  .title,
  .iconContainer {
    color: $defaultLinkHoverColor;
  }
}

.sourceLink {
  display: flex;
  align-items: center;
  flex-grow: 1;
  flex-shrink: 1;
  padding: 0 0 0 $sourceSidebarLeftGutter;
  min-width: $sourceSidebarWidth;
  width: 75%;
}

.titles {
  display: flex;
  align-items: center;
  flex-direction: column;
  overflow: hidden;
}

.title {
  @add-mixin truncate;
  composes: title from '~core/components/sidebar/SidebarLink.css';

  display: inline-block;
  align-self: flex-start;
  color: rgba(255, 255, 255, 0.75);
  font-size: $largeFontSize;
  font-family: $defaultFontFamily;
  line-height: 1.5;
  transition: color $defaultSpeed;
}

.detail {
  @add-mixin truncate;
  align-self: flex-start;
  color: $dimColor;
  font-size: $smallFontSize;
  font-family: $defaultFontFamily;
  line-height: 1.4;
}

.iconContainer {
  composes: icon from '~core/components/sidebar/SidebarLink.css';

  padding-right: $iconContainerPaddingRight;
  width: calc($sourceSidebarIconSize + $iconContainerPaddingRight);
  height: auto;
  font-size: $sourceSidebarIconSize;
  transition: color $defaultSpeed;
}

.small {
  min-height: 40px;
  height: 40px;
}

.medium {
  min-height: $sourceSidebarItemHeight;
}
