.container {
  @add-mixin resetFont;
  display: flex;
  align-items: center;
  flex-flow: row nowrap;
  padding: 4px;
  height: 40px;
  border-radius: 4px;
  background: $searchInputBackgroundColor;
  color: $searchInputColor;
  transition: background-color $defaultSpeed, color $defaultSpeed;
}

.focused {
  background: $searchFocusBackgroundColor;
}

.searchIcon,
.closeIcon {
  margin: 4px 10px 0 18px;
  font-size: 14px;
}

.closeIcon {
  margin: 0 5px 0 10px;
}

.focused .searchIcon,
.focused .closeIcon {
  color: $searchInputFocusColor;
}

.searchInput {
  flex-grow: 1;
  height: 100%;
  outline: 0;
  border: none;
  background: transparent;
  color: $searchInputColor;
}

.searchInput::-ms-clear {
  display: none;
}

.focused .searchInput {
  color: $searchInputFocusColor;
}
