.container {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  width: 100%;
}

.sectionTitle {
  color: $v4AltMedium;
  text-transform: uppercase;
  line-height: 40px;
}

.channelsListContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
}

.dvrContainer {
  margin-left: 10px;
}

@media only screen and (max-width: $breakpointLarge) {
  .container {
    flex-direction: column;
  }

  .deviceListContainer {
    order: 1;

    width: 100%;
  }

  .dvrContainer {
    margin-left: 0;
    order: 2;

    width: 100%;
  }

  .channelsListContainer {
    order: 3;
  }
}
