.deviceImageContainer {
  position: relative;
}

.selectButton {
  @add-mixin cover;
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';

  padding: 8px;
  border: 2px solid $selectedColor;
}

.details {
  margin-top: 5px;
}

.make {
  font-size: $smallFontSize;
}

.modelContainer {
  display: flex;
}

.model {
  font-size: $smallFontSize;
}

.uri,
.channelCount {
  color: $headerColor;
  font-size: $smallFontSize;
}
