.splitButton {
  composes: menuItem from '~core/components/menus/MenuItem.css';

  display: inline-flex;
  align-content: center;
  align-items: center;
  justify-content: space-between;
}

.activityTitleContainer {
  display: inline-flex;
  flex-direction: column;
  max-width: 85%;
  color: $dimColor;
}

.activityTitle {
  @add-mixin truncate;
}

.activitySubtitle {
  @add-mixin truncate;
}
