.image {
  border-radius: $v4CardBorderRadius;
}

.selectedLink {
  composes: link from '~app/desktop/common/js/v4/core/ui/cards/PosterCardLink.css';

  border-radius: $v4CardBorderRadius;
  box-shadow: 0 0 0 2px $v4CardHoverBorderColor,
    0 0 $v4CardShadowSize $v4CardShadowColor;
}

.hoveredSelectedLink {
  composes: hoveredLink from '~app/desktop/common/js/v4/core/ui/cards/PosterCardLink.css';

  box-shadow: 0 0 0 2px $v4CardHoverBorderColor,
    0 0 $v4CardShadowSize $v4CardShadowColor;
}

.progress {
  composes: progress from './MetadataPosterCardProgressBar.css';

  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  animation-name: progressFadeIn;
}

@keyframes progressFadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.progressBar {
  composes: bar from './MetadataPosterCardProgressBar.css';

  animation-name: progressBarSlideIn;
  animation-timing-function: ease-in-out;
}

@keyframes progressBarSlideIn {
  0% {
    transform: translate3d(-100%, 0, 0);
  }
}

.badge {
  position: absolute;
  animation-name: badgeFadeIn;
  pointer-events: none;
}

.topRightBadge {
  composes: badge;

  top: $v4CardBorderRadius;
  right: $v4CardBorderRadius;
}

.bottomRightBadge {
  composes: badge;

  /* Visually align with vertical overflow icon */
  right: 6px;
  bottom: 5px;
}

@keyframes badgeFadeIn {
  0% {
    opacity: 0;
    transform: scale(0.75);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

.legacyNumberBadge {
  composes: badge;

  padding: 0 6px;
  min-width: 28px;
  border-radius: 4px;
  text-align: center;
  font-family: $boldFontFamily;
  line-height: 22px;
  animation-name: none;
}

.legacyUnwatchedTagMask {
  @add-mixin cover;

  overflow: hidden;
  border-radius: $v4CardBorderRadius;
  pointer-events: none;
}

.legacyUnwatchedTag {
  position: absolute;
  top: -14px;
  right: -14px;
  width: 28px;
  height: 28px;
  background-color: $v4AccentLight;
  transform: rotate(45deg);
}

.legacyUnwatchedEpisodesBadge {
  composes: legacyNumberBadge;
  composes: topRightBadge;

  background-color: $v4AccentLight;
  color: $v4BaseDark;
}
