.toggle {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  text-align: center;
  font-size: 20px;
}

.tilesContainer {
  display: flex;
  justify-content: center;
}

.nowPlayingList {
  display: flex;
  align-content: stretch;
  width: 100%;
}

.column {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;

  &:not(:nth-child(3n + 3)) {
    margin-right: 40px;
  }
}

.emptyMessage {
  composes: container from '~desktop/common/js/v4/features/dashboard/components/ServerDashboardPageMessage.css';

  margin-bottom: 30px;
}
