.button {
  composes: button from './Button.css';

  position: relative;
}

.spinnerContainer {
  position: absolute;
  top: 50%;
  left: -100%;
  display: inline-flex;
  visibility: hidden;
  transition: left $defaultSpeed;
  transform: translateX(-50%) translateY(-50%);
}

.spinner {
  composes: spinner from '~desktop/common/js/v4/core/ui/spinners/components/Spinner.css';

  z-index: 1;
  border-top-color: $defaultColor;
  border-left-color: $defaultColor;
}

.label {
  position: relative;
  left: 0;
  transition: left $defaultSpeed, opacity $defaultSpeed;
}

.isSpinning {
  .spinnerContainer {
    left: 50%;
    visibility: visible;
  }

  .label {
    left: 100%;
    opacity: 0;
  }
}
