.summaryContainer {
  position: relative;
  display: flex;
  overflow: hidden;
  padding: 20px;
  height: 90px;
  background-color: $blackDarkerTranslucentBackgroundColor;

  &:hover {
    .playHistoryLink {
      transform: translateY(0);
    }
  }
}

.avatar {
  composes: avatar from '~desktop/common/js/views/v3/components/core/images/AvatarImg.css';

  flex-shrink: 0;
  margin-right: 20px;
}

.summary {
  color: $dimColor;
}

.playHistoryLink {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: $primaryColor;
  color: $defaultColor;
  cursor: pointer;
  transition: transform $defaultSpeed;
  transform: translateY(90px);

  /*
    When focusing on the button via tabbing remove the transition time to
    avoid a janky animation, and specify a transform to ensure the button
    transitions back when focus is removed.
    See: https://github.com/plexinc/plex-web-client/issues/13137
  */
  &:global(.isFocused) {
    transition: transform 0s;
    transform: translateY(0);
  }
}

.row {
  padding: 10px 20px;
  height: 40px;
  color: $defaultColor;
  font-size: $smallFontSize;
}

.username {
  @add-mixin truncate;
  composes: row;

  background-color: rgba(0, 0, 0, 0.5);
}

.metadataRow {
  composes: row;

  display: flex;
  justify-content: space-between;
}

.metadataTitle {
  @add-mixin truncate;
}

.duration {
  flex-shrink: 0;
  color: $dimColor;
}
