.pageContent {
  composes: pageContent from '~desktop/common/js/views/v3/components/pages/PageContent.css';
}

.hiddenPageContent {
  position: absolute;
  visibility: hidden;
}

.pageContentScroller {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.rawTextScroller {
  flex-grow: 1;
  padding: 10px 30px;
  font-size: 12px;
  line-height: 20px;
}

.jumpLinkVisible {
  composes: link from '~core/components/buttons/Link.css';

  position: absolute;
  bottom: 0;
  padding: 24px;
  width: 100%;
  background-color: rgba(40, 40, 40, 0.95);
  text-align: center;
  transition: transform $defaultSpeed;

  &:hover {
    background-color: rgba(40, 40, 40, 1);
  }
}

.jumpLinkHidden {
  composes: jumpLinkVisible;

  transform: translateY(100%);
}
