.container {
  overflow: hidden;
  margin-left: $playerControlsContentSpacing;
  min-width: 0;
  white-space: nowrap;
}

.ratingContainer {
  opacity: 0;

  transition: opacity $defaultSpeed ease;
  pointer-events: none;
}

.container:hover .ratingContainer,
.hasUserRating .ratingContainer {
  opacity: 1;
  pointer-events: auto;
}

.durationContainer {
  composes: container from '~desktop/common/js/views/v3/components/players/core/DurationRemaining.css';

  margin-right: $playerControlsContentSpacing;
}

.mediaInfoContainer {
  composes: infoContainer from '~desktop/common/js/views/v3/components/players/core/MediaInfo.css';

  display: inline-block;
  margin-right: $playerControlsContentSpacing;
}
