.message {
  margin-bottom: 10px;
  color: $dimColor;
  font-size: $largeFontSize;
}

.iconContainer {
  position: relative;
  margin-bottom: 10px;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.7);
}

.icon {
  @add-mixin absoluteCenter;

  font-size: 40px;
}

.clock {
  composes: icon;

  color: #3dc;
}

.clockTime {
  composes: clock;

  animation: blink steps(1, end) 1s infinite;
}

@keyframes blink {
  0% {
    visibility: visible;
  }

  50% {
    visibility: hidden;
  }

  100% {
    visibility: visible;
  }
}
