//
// Tooltips
// --------------------------------------------------


// Base class

.tooltip {
  font-size: @font-size-base;
  line-height: 1.4;
}


// Plex Pass tooltip

.tooltip-plex-pass {
  &.in {
    .opacity(1);
  }

  &.bottom {
    padding-top: 12px;
  }

  &.bottom .tooltip-arrow {
    left: 50%;
    margin-left: -12px;
    border-bottom-color: rgba(0, 0, 0, 0.9);
    border-width: 0 12px 12px;
  }

  .tooltip-inner {
    min-width: 350px;
    padding: 20px;
    margin-left: -150px;
    color: #fff;
    text-align: center;
    text-decoration: none;
    background-color: rgba(0, 0, 0, 0.9);
    border-radius: 10px;
    .box-shadow(10px 10px 50px 5px rgba(0, 0, 0, 0.75));
  }

  .tooltip-inner .plex-pass-sm {
    margin-bottom: 20px;
  }

  .tooltip-inner h1 {
    padding: 0 0 20px 0;
    margin: 0;
    line-height: 32px;
    font-size: 20px;
  }

  .tooltip-inner p {
    padding-bottom: 5px;
    color: #999;
  }

  .tooltip-inner .list {
    border: solid 1px #333;
    border-radius: 5px;
  }

  .tooltip-inner .list > li {
    padding: 5px;
    border-bottom: dotted 1px #333;
    text-align: left;

    &:last-child {
      border-bottom: none;
    }
  }

  .tooltip-inner .list-header {
    margin-bottom: 4px;
  }

  .tooltip-inner .list-description {
    font-size: @font-size-small;
    color: #999;
  }
}

.persistent-tooltip {
  .tooltip;
  &.top .tooltip-arrow {
    bottom: -5px;
  }
}
