.container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: $playQueueItemHeight;

  &.isSelected {
    background-color: $stripeListSelectedBackgroundColor;
  }

  &:hover {
    .button {
      opacity: 1;
    }

    .moveControl,
    .otherControls {
      opacity: 1;
    }

    .metadataDurationContainer,
    .attributionContainer {
      opacity: 0;
    }
  }

  &.isForcingSelection,
  &.hasActiveSelection {
    .selectButton,
    .otherControls {
      opacity: 1;
    }

    .overlay {
      pointer-events: none;
    }

    .removeButton,
    .moreButton,
    .metadataDurationContainer,
    .attributionContainer {
      opacity: 0;
    }
  }

  &.isForcingSelection:hover,
  &.hasActiveSelection:hover {
    .moveControl {
      opacity: 0;
      pointer-events: none;
    }
  }
}

.alternateContainer {
  composes: container;

  background-color: $stripeListAlternateBackgroundColor;
}

.metadataContainer {
  composes: container from '~desktop/common/js/views/v3/components/pages/prePlay/playlist/PlaylistItemMetadata.css';
}

.underlay {
  @add-mixin cover;
  cursor: default;
}

.overlay {
  @add-mixin cover;
  display: flex;
  padding: 0 20px 0 0;
}

.metadataDurationContainer {
  composes: durationContainer from './PlaylistItemMetadata.css';

  opacity: 1;
  transition: opacity $fastSpeed;
}

.attributionContainer {
  composes: attributionContainer from './PlaylistItemMetadata.css';

  opacity: 1;
  transition: opacity $fastSpeed;
}

.sourceText {
  display: none;
}

.moveControl {
  padding: 0 5px 0 20px;
  color: $defaultLinkColor;
  font-size: $defaultIconFontSize;
  line-height: $playQueueItemHeight;
  opacity: 0;
  cursor: grab;
  transition: opacity $defaultSpeed;

  &:hover {
    color: $defaultLinkHoverColor;
  }
}

.grabIcon {
  top: 0;
}

.otherControls {
  position: absolute;
  right: 0;
  padding: 0 10px;
  opacity: 0;
  transition: opacity $defaultSpeed;
}

.removeButton {
  composes: button from '~core/components/buttons/IconButton.css';

  top: 1px;
  padding: 0 10px;
  vertical-align: middle;
  line-height: $playQueueItemHeight;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';
  composes: button;

  padding: 0 10px;
  vertical-align: middle;
  line-height: $playQueueItemHeight;
}

.moreButton {
  composes: button from '~core/components/buttons/IconButton.css';

  position: relative;
  top: 1px;
  padding: 0 10px;
  line-height: $playQueueItemHeight;
}

/*
 * States
 */

.isItemDragging {
  .moveControl {
    opacity: 1;
  }
}
