.container {
  opacity: 1;
  transition: opacity $defaultSpeed;

  &:global(.isDragging) {
    opacity: $dragPreviewLowOpacity;
  }

  &:global(.isDraggingBefore) {
    box-shadow: inset 0 4px 0 -2px $selectedColor;
  }

  &:global(.isDraggingAfter) {
    box-shadow: 0 2px 0 0 $selectedColor;
  }
}

.source {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  padding: 0;
  min-height: $sourceSidebarItemHeight;
  width: 100%;
}

.isOver {
  opacity: $dragPreviewLowOpacity;
}
