.actions {
  display: flex;
  justify-content: center;
  width: 40px;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';

  opacity: 0;
  transition: opacity $defaultSpeed;
}

.title {
  @add-mixin truncate;

  font-size: $largeFontSize;
  font-family: $boldFontFamily;
  user-select: none;
}

.header {
  position: absolute;
  top: calc(-5px - ($largeFontSize * $lineHeightComputed));
  left: -40px;
  display: flex;
  padding: 0 5px 5px;
  width: 100%;

  &:hover {
    .selectButton {
      opacity: 1;
    }
  }
}

.hasSelection {
  .selectButton {
    opacity: 1;
  }
}
