.cardContainer {
  composes: cardContainer from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCard.css';
}

.card {
  composes: card from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCard.css';
}

.face {
  composes: face from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardFace.css';
}

.overlay {
  composes: overlay from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';
}

.overlayBackground {
  composes: background from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';
}

.overlayLink {
  composes: link from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';
}

.overlaySelectCoverButton {
  composes: selectCoverButton from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';
}

.iconContainer {
  position: absolute;
  top: $posterIconPadding;
  right: $posterIconPadding;
  bottom: $posterIconPadding;
  left: $posterIconPadding;
}

.titleContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.titleItem {
  text-align: center;
}
