$disclosureArrowLinkPadding: 5px;

.disclosureArrowLink {
  @add-mixin truncate;
  composes: link from '~app/desktop/common/js/v4/core/ui/buttons/components/Link.css';

  position: relative;
  padding-right: 13px;
  min-width: 30px;

  &.isSelected {
    font-family: $boldFontFamily;
  }

  &.small {
    padding-right: calc($disclosureArrowLinkPadding + $smallArrowWidth);
  }

  &.medium {
    padding-right: calc($disclosureArrowLinkPadding + $mediumArrowWidth);
  }

  &.large {
    padding-right: calc($disclosureArrowLinkPadding + $largeArrowWidth);
  }
}

.disclosureArrow {
  composes: disclosureArrow from '~core/components/base/DisclosureArrow.css';

  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);

  &.down {
    transform: translate(0, -50%) rotateX(180deg);
  }

  &.left {
    transform: translate(0, -50%) rotateY(180deg);
  }

  /* Add specificity to override the DisclosureArrow style. */
  &.small,
  &.medium,
  &.large {
    &.up,
    &.right,
    &.left,
    &.down {
      margin: 0;
    }
  }
}
