.overlay {
  @add-mixin linkOverlay;
  display: flex;
  align-items: center;
  padding: $summaryCellPadding;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.underlay {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';
}

.posterContainer {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  margin-right: 20px;
  color: $posterPlaceholderIconColor;
}

.titlesContainer {
  flex-grow: 1;
  overflow: hidden;
  padding-right: 20px;
  width: 100%;
  font-size: $largeFontSize;
}

.doubleTitle {
  @add-mixin doubleLineTruncate;
  overflow: hidden;
  width: 100%;
}
