.trackList {
  margin-bottom: $v4ListSpacing;
  border-top: $v4PrePlayDividerHeight solid $v4DividerColor;
  border-bottom: $v4PrePlayDividerHeight solid $v4DividerColor;
}

.titleContainer {
  composes: titleContainer from '~desktop/common/js/v4/features/prePlay/components/core/titles/PrePlayListTitle.css';

  padding-right: 0;
  padding-left: 0;
}

.selectedTitleContainer {
  composes: selectedTitleContainer from '~desktop/common/js/v4/features/prePlay/components/core/titles/PrePlayListTitle.css';

  padding-right: 0;
  padding-left: 0;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/v4/features/prePlay/components/core/titles/PrePlayListTitle.css';

  left: -30px;
}
