.tableHeaderCell {
  composes: tableCell from './TableCell.css';

  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.1);
}

a > .tableHeaderCell:hover,
button > .tableHeaderCell:hover {
  background-color: $lightTranslucentBackgroundColor;
}

.activeTableHeaderCell {
  composes: tableHeaderCell;

  color: $defaultColor;
}
