.device {
  display: flex;
  margin-bottom: 18px;
  padding: 18px;
  background-color: $dvrBackgroundColor;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.details {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 20px;
  height: 100px;
}

.modelContainer {
  display: flex;
}

.model {
  font-size: $largeFontSize;
}

.deviceSettingsButton {
  composes: link from '~core/components/buttons/Link.css';

  display: flex;
  align-items: center;
}

.deviceSettingsIcon {
  margin-right: 8px;
  margin-bottom: -4px;
  font-size: $defaultIconFontSize;
}

.uri,
.deviceWarning {
  color: $headerColor;
}

.deviceWarning {
  display: flex;
  margin-top: 25px;
  line-height: normal;
}

.deviceWarningIconContainer {
  margin-right: 8px;
  color: $dangerColor;
  font-size: $defaultIconFontSize;
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $headerColor;
}

.info {
  color: $headerColor;
}

.removeDeviceContainer {
  display: flex;
  align-self: center;
  margin-left: auto;
}

.removeDevice {
  display: none;
  color: $dangerColor;
  font-size: 20px;
}

.device:hover {
  .removeDevice {
    display: inline-block;
  }
}
