.dvr {
  margin-bottom: 35px;
  padding: 30px;
  background-color: $dvrBackgroundColor;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: solid 2px $dividerColor;
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $headerColor;
}

.name {
  @add-mixin truncate;
  margin-right: 15px;
  font-size: $largeFontSize;
}

.deleteLink {
  white-space: nowrap;
}

.actions {
  font-size: $defaultIconFontSize;
}

.actionIcon {
  margin-right: 8px;
  margin-bottom: -1px;
}

.devices {
  margin: 25px 0;
  /* See: https://github.com/plexinc/plex-web-client/issues/14659 */
  transform: translateZ(0);
}

.addDeviceIcon {
  margin-right: 8px;
  font-size: $defaultIconFontSize;
}

.banner {
  composes: container from '~desktop/common/js/views/v3/components/livetv/guide/ProgramGuideGracenoteMigrationBanner.css';

  margin: 0 0 20px;
  width: 100%;
}
