.input {
  composes: input from '~core/components/forms/CheckInput.css';

  display: flex;
  align-items: center;
  padding: 0 15px 0 70px;
  height: $serverShareListRowHeight;
  color: rgba(255, 255, 255, 0.75);
}

.input:nth-of-type(odd) {
  background-color: $rowOddBackgroundColor;
}

.input:nth-of-type(even) {
  background-color: $rowEvenBackgroundColor;
}
