.spinner {
  composes: spin from '~core/styles/animations/spin.css';

  display: inline-block;
  border: solid 2px transparent;
  border-radius: 50%;
}

.spinnerContainer {
  text-align: center;
}

.spinnerLabel {
  margin-top: 5px;
  font-size: $defaultFontSize;
}

/*
 * Sizes
 */

.small {
  composes: spinner;

  width: 16px;
  height: 16px;
}

.medium {
  composes: spinner;

  width: 24px;
  height: 24px;
}

.large {
  composes: spinner;

  width: 30px;
  height: 30px;
}
