.form {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.modalBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.messages {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  flex-shrink: 0;
  margin-bottom: 20px;
  min-height: 42px;
  font-size: $smallFontSize;
}

.inputContainer {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  margin-bottom: 20px;
}

.icon {
  margin-left: 10px;
}

.checkIcon {
  composes: icon;

  top: 0;
  color: $successColor;
  font-size: $largeFontSize;
}

.warningMessage {
  margin-bottom: 10px;
  height: 20px;
  color: $warningColor;
}

.restrictionsInput {
  composes: input from '~desktop/common/js/views/v3/components/actions/modals/shareLibrary/RestrictionProfilesInput.css';

  max-width: 140px;
}

.list {
  composes: list from '~desktop/common/js/views/v3/components/actions/modals/users/UserList.css';

  height: 160px;
}
