.searchInputContainer {
  display: inline-flex;
  /* Bootstrap places a margin on label elements */
  margin: 0;
  /* Don't allow the dropdown button to be larger than the rest of the inputs in
  the list. */
  max-width: fit-content;
}

.button {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  flex-shrink: 0;
  width: 30px;
  border-radius: $inputBorderRadius;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background-color: $filterBuilderInputBackgroundColor;
  color: $inputColor;
  text-align: center;
}

.small {
  height: $smallSize;
  font-size: $defaultFontSize;
}

.medium {
  height: $mediumSize;
  font-size: $defaultFontSize;
}

.large {
  height: $largeSize;
  font-size: $largeFontSize;
}

.isDisabled {
  opacity: $inputDisabledOpacity;
  cursor: not-allowed;
}

.hasError {
  border: 2px solid $dangerColor;
  border-radius: $inputBorderRadius;
}

.menuContainer {
  z-index: 1;
  display: flex;
}

.menu {
  padding: $menuPadding 0;
  width: 100%;
  border-radius: 4px;
  background-color: $menuBackgroundColor;
  box-shadow: 0 4px 10px $shadowColor;
}

.loadingMenu {
  composes: menu;

  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 200px;
}

.scroller {
  composes: scroller from '~desktop/common/js/v4/core/ui/scrollers/components/Scroller.css';

  height: 100%;
}

.input {
  composes: input from '~desktop/common/js/v4/features/advancedFilters/components/inputs/FilterTextInput.css';

  flex-shrink: 1;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.list {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;
}

.listItem {
  @add-mixin resetFont;
  display: block;
  padding: $menuItemTopPadding $menuItemSidePadding;
  width: 100%;
  color: $quietColor;
  text-align: left;
  word-wrap: break-word;
  cursor: pointer;
}

.listItemHighlighted {
  composes: listItem;

  border-radius: 0 $autosuggestSuggestionRadius $autosuggestSuggestionRadius 0;
  background-color: $menuItemActiveBackgroundColor;
  color: $defaultColor;
}
