.hub {
  margin: $v4ListSpacing 0;
}

.hub:first-of-type {
  margin-top: 0;
}

.hubHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 0 $v4PagePadding;
  min-height: 25px;
}

.hubActions {
  flex-shrink: 0;
  margin-left: auto;
  font-size: 18px;
  line-height: 24px;
}

.hubScrollButton {
  composes: iconButton from '~desktop/common/js/v4/core/ui/buttons/components/IconButton.css';

  margin-left: 10px;
  padding: 0 5px;

  &:last-of-type {
    padding-right: 0;
  }

  &:global(.isDisabled) {
    opacity: 0.15;
  }
}

.hubScroller {
  composes: scroller from '~desktop/common/js/v4/core/ui/scrollers/components/VirtualListScroller.css';

  /* See: https://github.com/plexinc/plex-web-client/issues/10664 */
  padding-bottom: 20px;
}
