.selectButtonContainer {
  position: absolute;
  left: -30px;
  display: flex;
  align-items: center;
  min-width: 30px;
  height: 100%;

  &:hover {
    .selectButton {
      opacity: 1;
    }
  }
}

.isVisible {
  .selectButton {
    opacity: 1;
  }
}

.selectButton {
  composes: selectButton from '~app/desktop/common/js/v4/core/ui/buttons/components/SelectButton.css';

  width: 100%;
  height: 100%;
  opacity: 0;

  &:global(.isFocused) {
    opacity: 1;
  }
}
