.recordButton {
  position: relative;
}

.recordingProgressBarContainer {
  composes: absoluteCenterCircularProgressBarContainer from '~common/js/views/v3/components/core/CircularProgressBar.css';
}

.recordingProgressBackground {
  composes: circularProgressBarBackground from '~desktop/common/js/views/v3/components/core/CircularProgressBar.css';

  background-color: rgba(0, 0, 0, 0.2);
}

.playerButton {
  composes: playerButton from '~desktop/common/js/views/v3/components/players/core/PlayerIconButton.css';

  width: $playerIconButtonSize;
  height: $playerIconButtonSize;
}

.playerButtonRecording {
  composes: playerButton;

  color: $recordColor;
}
