.row {
  position: relative;
  display: flex;
  justify-content: flex-start;
  padding: 0 20px;
  height: 70px;
  color: $dimColor;

  &:nth-child(even) {
    background-color: $stripeListAlternateBackgroundColor;
  }

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.underlay {
  @add-mixin cover;
}
