.line {
  padding: 2px 30px;
  width: 100%;
  font-size: 13px;
}

.field {
  margin-right: 12px;
  white-space: nowrap;

  &:last-child {
    margin-right: 0;
  }
}

.fieldTime {
  composes: field;
}

.fieldThread {
  composes: field;
}

.fieldMessage {
  composes: field;

  flex-grow: 1;
  width: 100%;
}

/*
 * Levels
 */

.levelError {
  composes: line;

  background-color: rgba(255, 85, 51, 0.15);
  color: rgba(255, 85, 51, 0.75);
}

.levelWarning {
  composes: line;

  background-color: rgba(249, 190, 3, 0.15);
  color: rgba(249, 190, 3, 0.75);
}

.levelInfo {
  composes: line;

  background-color: rgba(25, 160, 215, 0.15);
  color: rgba(25, 160, 215, 0.75);
}

.levelDebug {
  composes: line;

  color: rgba(255, 255, 255, 0.75);
}

.levelVerbose {
  composes: line;

  color: rgba(255, 255, 255, 0.45);
}

.levelUnknown {
  composes: line;

  color: rgba(255, 255, 255, 0.45);
}

/*
 * States
 */

.isHighlighted {
  box-shadow: inset 0 0 0 2px #fff;
}
