.container {
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
}

.numberContainer {
  position: relative;
  flex: 1;
  height: $channelGridRowHeight;
  border-top: $channelGridBorderSize solid $channelGridBorderColor;
  border-right: $channelGridBorderSize solid $channelGridBorderColor;
  border-left: $channelGridBorderSize solid $channelGridBorderColor;
  background-color: $channelGridChannelIdentifierBackroundColor;
  text-align: center;
  font-size: $largeFontSize;
  line-height: $channelGridRowHeight;
}

.thumbnailContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  overflow: hidden;
  padding: 5px;
  width: $channelGridChannelIconWidth;
  height: $channelGridRowHeight;
  border-top: $channelGridBorderSize solid $channelGridBorderColor;
  border-right: $channelGridBorderSize solid $channelGridBorderColor;
  background-color: $channelGridChannelThumbBackgroundColor;
  text-align: center;
  font-size: $largeFontSize;
}

.fullWidth {
  flex: 1 1 auto;
  width: $channelGridTitleColumnWidth;
  border-left: $channelGridBorderSize solid $channelGridBorderColor;
}

.icon {
  position: absolute;
  top: 23px;
  right: 10px;
  width: 15px;
  height: 15px;
  opacity: 0.5;
}

.isRecent {
  background-color: $channelGridRecentChannelThumbBackgroundColor;
}

.thumbnail {
  max-width: 100%;
  max-height: 100%;
}

.channelCallSignText {
  $lineHeight: 1.5;
  @add-mixin truncate;

  overflow: hidden;
  max-height: calc($largeFontSize * $lineHeight * 2);
  line-height: $lineHeight;
}

.playButton {
  @add-mixin cover;
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';

  transform: scale(1.2, 1.2);
}

.fullWidthPlayButton {
  composes: playButton;

  background-color: $v4CardBackgroundColor;
}
