$tooltipLeft: 24px;
$tooltipRight: 24px;

.toolbarButton {
  display: inline-block;
  flex-shrink: 0;
  width: $toolbarButtonWidth;
  height: 100%;
  text-align: center;
  font-size: 20px;

  &:global(.isDisabled) {
    color: $defaultLinkDisabledLinkColor;
  }
}

.bottomLeft {
  composes: bottomLeft from '~core/components/tooltips/Tooltip.css';

  margin-top: $tooltipMarginTop;

  &::after {
    left: $tooltipLeft;
  }
}

.bottomCenter {
  composes: bottomCenter from '~core/components/tooltips/Tooltip.css';

  margin-top: $tooltipMarginTop;
}

.bottomRight {
  composes: bottomRight from '~core/components/tooltips/Tooltip.css';

  margin-top: $tooltipMarginTop;

  &::after {
    right: $tooltipRight;
  }
}

.hasFocus {
  margin-top: calc($tooltipMarginTop * -1);
}
