.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  margin: auto;
  padding: 0 20px;
  width: 950px;
}

.actions {
  width: 300px;
}

.stepContainer {
  width: 480px;
}

.stepText {
  color: $dimColor;
  text-transform: uppercase;
  font-size: 16px;
  font-family: $boldFontFamily;
}

.stepHeading {
  margin: 30px 0;
  padding: 0;
  color: $defaultColor;
  font-size: 24px;
  font-family: $boldFontFamily;
}

.stepBody {
  margin-bottom: 50px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 18px;
  font-family: $defaultFontFamily;
  line-height: 1.67;
}

.stepIcon {
  position: relative;
  top: -2px;
  display: inline-block;
  margin-left: 10px;
  width: 24px;
  height: 24px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  color: rgba(255, 255, 255, 0.3);
  text-align: center;
  font-size: 14px;
  font-family: $boldFontFamily;
  line-height: 1;
}

.activeStepIcon {
  composes: stepIcon;

  border: 1px solid #f9be03;
  color: #f9be03;
}

.stepButton {
  margin-right: 10px;
}

/*
 * Breakpoints
 */

@media only screen and (max-width: 900px) {
  /* Add specificity to override modal styles with different dimensions. */
  .container {
    display: block;
    width: 500px;
  }

  .stepContainer {
    margin-top: 40px;
  }
}
