.spinner {
  position: relative;
  top: 2px;
  left: 2px;
  margin-right: 10px;
  width: $defaultIconFontSize;
  height: $defaultIconFontSize;
  border-top-color: $primaryColor;
  border-left-color: $primaryColor;
}

.container {
  position: relative;
}

.badge {
  position: absolute;
  top: 7px;
  left: 12px;
  width: $defaultIconFontSize;
  height: $defaultIconFontSize;
}

.image {
  width: 100%;
}
