.selectButton {
  composes: link from '~app/desktop/common/js/v4/core/ui/buttons/components/Link.css';

  line-height: 0;
  cursor: default;

  &:global(.isActive) .selectCircle {
    background-color: $selectedColor;
  }
}

.selectCircle {
  position: relative;
  display: inline-block;
  width: 14px;
  height: 14px;
  border: 2px solid $defaultLinkColor;
  border-radius: 50%;
  color: $defaultLinkColor;
  text-align: center;
  font-size: 9px;
  line-height: 14px;
  transition: all $defaultSpeed;
}

.selectedIcon {
  @add-mixin absoluteCenter;
  transition: opacity $defaultSpeed;
}

.unselectedCircle {
  composes: selectCircle;

  &:hover .selectCircle {
    border-color: $defaultLinkHoverColor;
  }
}

.selectedCircle {
  composes: selectCircle;

  border-color: $selectedColor;
  background-color: $selectedColor;
  box-shadow: 0 0 4px $posterBadgeShadowColor;
  color: rgba(0, 0, 0, 0.75);
  transform: scale(1.4);
}

$rangeColor: #896008;

.rangeCircle {
  composes: selectCircle;

  border-color: $rangeColor;
  background-color: $rangeColor;
  box-shadow: 0 0 4px $posterBadgeShadowColor;
  color: rgba(0, 0, 0, 0.75);
  transform: scale(1.4);
}
