.pane {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 0;
  flex-shrink: 0;
  width: calc($recordingPriorityPaneWidth + $recordingPriorityPaneBorderWidth);
  border-left: $recordingPriorityPaneBorderWidth solid rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.03);
}

.paneHeader {
  flex-shrink: 0;
  padding: 24px 24px 15px;
  text-transform: uppercase;
  font-size: $largeFontSize;
  font-family: $boldFontFamily;
}

.paneScroller {
  composes: vertical from '~core/components/base/Scroller.css';

  flex-grow: 1;
  padding-right: 0;
}

.error {
  padding: 20px 40px;
  color: $dimColor;
  text-align: center;
}

.empty {
  margin-top: -7px;
  padding: 0 24px;
  color: $dimColor;
}
