.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: $summaryCellPadding;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.selectOverlay {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  z-index: 1;
}

.selectedListRow {
  composes: row from '~desktop/common/js/v4/core/ui/lists/components/ListRow.css';

  background-color: $stripeListSelectedBackgroundColor;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.rangeListRow {
  composes: row from '~desktop/common/js/v4/core/ui/lists/components/ListRow.css';

  background-color: $stripeListHoverBackgroundColor;
}

.summaryContainer {
  display: flex;
  align-items: center;
  flex-grow: 1;
  width: 100%;
}

.posterContainer {
  flex-shrink: 0;
  margin-right: 20px;
  pointer-events: all;
}

.titlesContainer {
  flex-grow: 1;
  overflow: hidden;
  padding-right: 20px;
  width: 100%;
  font-size: $largeFontSize;
}

.titleContainer {
  display: block;
}

.title {
  overflow: hidden;
  width: 100%;
}

.singleTitle {
  @add-mixin truncate;
  composes: title;
}

.doubleTitle {
  @add-mixin doubleLineTruncate;
  composes: title;
}

.subtitle {
  @add-mixin truncate;
  composes: titleContainer;
  composes: title;

  color: $dimColor;
  font-size: $defaultFontSize;

  a {
    color: $dimColor;
  }
}

.summary {
  @add-mixin doubleLineTruncate;
  margin-top: 5px;
  max-width: $prePlaySummaryMaxWidth;
  color: $defaultLinkColor;
  font-size: $defaultFontSize;
}

.actions {
  flex-shrink: 0;
}

.actionsButton {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  padding: 10px;
}

.userRatingContainer {
  display: inline-block;
  margin-left: 10px;
  color: $defaultColor;
}
