//
// Labels
// --------------------------------------------------


// Base classes

.label {
  padding: 2px 15px;
  font-size: @font-size-base;
  font-weight: normal;
  color: @white;
  border-radius: (@font-size-base + 10) / 2;
}


// Button

.label-btn {
  padding-left: 5px;

  &:hover,
  &:focus {
    color: @white;

    .label-icon {
      .opacity(1);
    }
  }

  &:active,
  &.active {
    .opacity(0.5);

    .label-icon {
      .opacity(1);
    }
  }

  &.active {
    pointer-events: none;
  }

  .label-icon {
    width: 18px;
    .opacity(0.5);
  }
}


// Progress bar button

.label-progress {
  background-color: @gray-dark;
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0, @orange));
  background-size: 0% 100%;
  background-repeat: no-repeat;
}
