.innerEmptyPage {
  composes: emptyPage from '~desktop/common/js/views/v3/components/pages/empty/EmptyPage.css';

  position: absolute;
  top: 0;
  right: 5px;
  left: 0;
  min-height: 100%;
  width: auto;
  height: auto;
}

.innerEmptyPageContent {
  composes: content from '~desktop/common/js/views/v3/components/pages/empty/EmptyPage.css';

  padding-bottom: calc($pageHeaderRowHeight + $pagePadding);
}
