.thumbWrapper {
  background-color: $thumbnailBackgroundColor;

  &:hover {
    box-shadow: 0 0 0 2px $primaryColor;
  }
}

.isActive:not(:hover) {
  box-shadow: 0 0 0 2px $selectedColor;
}

.titleContainer {
  padding: $posterTitlePadding 0 0;
}

.title {
  @add-mixin truncate;
}

.secondaryTitle {
  color: $dimColor;
}
