.sourceItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px 0 30px;
  width: 280px;
  height: 40px;
  color: rgba(255, 255, 255, 0.75);
  font-size: 16px;
  font-family: $boldFontFamily;
  cursor: pointer;
}

.sourceTitle {
  @add-mixin truncate;
  margin-right: 10px;
}

.sourceIconContainer {
  margin-right: 20px;
}

.iconImg {
  composes: iconImg from '~desktop/common/js/views/v3/components/sidebar/source/SourceSidebarItem.css';
}

.input {
  composes: input from '~core/components/forms/CheckInput.css';

  margin-right: 0 !important;
}
