//
// Section
// --------------------------------------------------

.section-loading-container {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  height: 40px;
  background-color: @black;
  background-color: rgba(20, 20, 20, 0.9);

  &.has-scrollbar {
    right: @scrollbar-width;
  }

  &.bottom {
    top: auto;
    bottom: 0;
  }
}

.empty-message > span { display: none; }
.empty-message.default > .empty-default { display: block; }
.empty-message.refreshing > .empty-refreshing { display: block; }
.empty-message.filter > .empty-filter { display: block; }
.empty-message.folder > .empty-folder { display: block; }
