.playerContainer {
  display: flex;
  padding: 10px;
  height: 70px;
  background-color: $serverDashboardTileRowBackgroundPrimaryColor;
}

.playerIcon {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.productLogo {
  margin-right: 10px;
  border-radius: 4px;
}

.statusIcon {
  position: absolute;
  top: -6px;
  left: -8px;
  border-radius: 50%;
  background-color: #000;
  font-size: 17px;
}

.playerInformation {
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  font-size: 11px;
}

.title {
  @add-mixin truncate;
  color: $defaultColor;
}

.subtitle {
  @add-mixin truncate;
  color: $dimColor;
}

.streamInformation {
  display: flex;
  padding: 10px;
  font-size: 11px;
}

.videoStreamInformation {
  composes: streamInformation;

  background-color: $serverDashboardTileRowBackgroundSecondaryColor;
}

.audioStreamInformation {
  composes: streamInformation;

  background-color: $serverDashboardTileRowBackgroundTertiaryColor;
}

.subtitlesStreamInformation {
  composes: streamInformation;

  background-color: $serverDashboardTileRowBackgroundQuaternaryColor;
}

.streamType {
  margin-right: 10px;
  color: $dimColor;
}

.streams {
  @add-mixin truncate;
  display: flex;
  flex-direction: column;
}

.rightArrow::before {
  margin-right: 10px;
  content: '';
}

.rightArrowForEdge {
  composes: rightArrow;

  font-family: 'Segoe UI Symbol', sans-serif;
}
