.recordButton {
  composes: statusButton from '~desktop/common/js/views/v3/components/pages/prePlay/core/titles/PrePlayStatusButton.css';

  padding-left: 8px;
  background-color: $blackDarkerTranslucentBackgroundColor;

  &:hover {
    background-color: $recordBackgroundColor;

    .recordingIcon {
      color: $defaultLinkHoverColor;
    }
  }

  &:global(.isActive) {
    background-color: darken($recordBackgroundColor, 0.1);
  }
}

.recordIcon,
.recordingIcon {
  top: -1px;
  margin-right: 6px;
  font-size: $defaultIconFontSize;
}

.recordingIcon {
  color: $recordColor;
}
