.strip {
  display: flex;
  overflow: hidden;
  padding: 4px 0;
  background: $playerSecondaryOverlayColor;
}

.thumbs {
  position: relative;
  flex-grow: 1;
}

.cell {
  transition: transform 0.3s;
}

.thumbContainer {
  box-sizing: border-box;
  padding: 1px;
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
  opacity: 0.7;

  &:hover {
    border-color: $primaryBackgroundColor;
    opacity: 0.9;
  }

  &.isSelected {
    border-color: $selectedColor;
    opacity: 1;
  }
}

.thumb {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  background: $playerSecondaryOverlayColor;
}

.thumbOverlay {
  @add-mixin cover;
}
