.item {
  &:hover,
  &.isActionsMenuOpen {
    background-color: $stripeListHoverBackgroundColor;

    .moreButton {
      opacity: 1;
    }
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: calc($summaryCellPadding / 2) 40px;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.posterContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  padding: calc($summaryCellPadding / 2);
  width: 134px;
}

.summaryContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  padding: calc($summaryCellPadding / 2);
}

.titleContainer {
  display: flex;
  overflow: hidden;
  font-size: $largeFontSize;
}

.title {
  @add-mixin truncate;
  flex-grow: 1;
  padding-right: 20px;
}

.type {
  overflow: hidden;
  margin-top: 5px;
  color: $dimColor;
}

.actions {
  display: flex;
  flex-shrink: 0;
  padding: 0 10px;
}

.moreButton {
  padding: 0 10px;
  opacity: 0;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 1;
  }
}
