.innerModalBody {
  composes: innerModalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-direction: column;
  height: 100%;
}

.text {
  color: $v4AltMedium;
}

.accentText {
  color: $v4AccentLight;
}

.additionalDevices {
  margin-bottom: 20px;
}

.spinnerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
}

.spinner {
  composes: spinner from '~app/desktop/common/js/v4/core/ui/spinners/components/Spinner.css';

  margin-bottom: 20px;
  width: $dvrSetupSpinnerSize;
  height: $dvrSetupSpinnerSize;
}
