.container {
  display: flex;
  flex-direction: row;
  padding: 0 $modalBodyPadding;
  height: $dvrSetupChannelMappingItemHeight;
  line-height: $dvrSetupChannelMappingItemHeight;
}

.mappingDisabled {
  color: $v4AltMediumDark;
}

.deviceChannel {
  display: flex;
  align-items: center;
  flex: 0 0 50%;
  flex-direction: row;
  min-width: 0;
}

.channelEnabled {
  width: 45px;
  height: $dvrSetupChannelMappingItemHeight;
}

.channelEnabledCheckbox {
  composes: input from '~desktop/common/js/v4/core/ui/forms/components/Checkbox.css';

  margin: 0 10px !important;
  height: $dvrSetupChannelMappingItemHeight;
}

.deviceChannelIdentifier {
  flex-shrink: 0;
  margin-right: 30px;
  width: 25px;
  text-align: left;
}

.deviceChannelInfo {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  min-width: 0;
}

.deviceChannelName {
  @add-mixin truncate;

  flex-grow: 1;
}

.signalQuality {
  margin: 0 10px;
}

.lineupChannel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex: 0 0 50%;
}

.lineupChannelSelect {
  composes: input from '~desktop/common/js/v4/core/ui/forms/components/Select.css';

  width: calc(100% - 45px);
}

.drmChannel {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0 0 50%;
}

.drmChannelBadge {
  margin-right: 45px;
}

.hdBadge,
.drmChannelBadge {
  height: 30px;
  background-color: $v4BaseMediumLight;
  color: $v4AltMedium;
  line-height: 30px;
}

@media only screen and (max-width: $breakpointExtraSmall) {
  .deviceChannelIdentifier {
    margin-right: 0;
  }
}
