.title {
  @add-mixin truncate;
  margin: 9px 0 6px;
  padding: 5px 24px;
  width: 346px;
  height: 30px;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.05);
  font-size: $smallFontSize;
  font-family: $boldFontFamily;
  line-height: $lineHeightComputed;
}

.noResults {
  margin: 9px 0 6px;
  padding: 5px 24px;
  color: $dimColor;
}

.spinnerContainer {
  display: flex;
  width: 360px;
  height: 86px;
}

.spinner {
  align-self: center;
  margin: 0 auto;
}
