$seekBarInteractionHeight: 24px;

.seekBar {
  composes: slider from '~desktop/common/js/views/v3/components/core/Slider.css';

  position: absolute;
  top: calc(($seekBarInteractionHeight - $seekBarHeight) / -2);
  width: 100%;
  height: $seekBarInteractionHeight;

  &:hover .seekBarTrack::before {
    background-color: $seekBarTrackColorHover;
  }
}

.seekBarTrack {
  composes: track from '~desktop/common/js/views/v3/components/core/Slider.css';

  height: $seekBarHeight;
  background-color: transparent;

  &::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: $seekBarTrackColor;
    content: '';
    transition: background-color $slowSpeed;
    transition-delay: 0.2s;
  }
}

.seekBarFill {
  composes: fill from '~desktop/common/js/views/v3/components/core/Slider.css';

  top: 0;
  height: $seekBarHeight;
  background-color: $seekBarFillColor;
}

.seekBarBuffer {
  composes: secondaryFill from '~desktop/common/js/views/v3/components/core/Slider.css';

  top: 0;
  height: $seekBarHeight;
  background-color: $seekBarBufferColor;
}

.noFillTransition {
  transition: none;
}
