.menuItem {
  @add-mixin resetFont;
  display: block;
  padding: $menuItemTopPadding $menuItemSidePadding;
  width: 100%;
  text-align: left;
  word-wrap: break-word;
}

.isDisabled {
  opacity: 0.3;
  pointer-events: none;
}

.isHidden {
  display: none;
}

/*
 * Kinds
 */

.default {
  &:hover {
    background-color: $menuItemHoverBackgroundColor;
  }

  &:global(.isActive) {
    background-color: $menuItemActiveBackgroundColor;
  }
}

.primary {
  &:hover {
    background-color: $primaryBackgroundColor;
  }

  &:global(.isActive) {
    background-color: $primaryActiveBackgroundColor;
  }
}

.success {
  &:hover {
    background-color: $successBackgroundColor;
  }

  &:global(.isActive) {
    background-color: $successActiveBackgroundColor;
  }
}

.danger {
  &:hover {
    background-color: $dangerBackgroundColor;
  }

  &:global(.isActive) {
    background-color: $dangerActiveBackgroundColor;
  }
}
