.item {
  &:hover,
  &.isActionsMenuOpen {
    background-color: $stripeListHoverBackgroundColor;

    .button {
      opacity: 1;
    }
  }

  &.hasActiveSelection,
  &.isForcingSelection {
    .underlay {
      display: none;
    }

    .overlay a,
    .overlay button {
      pointer-events: none;
    }

    .editButton,
    .moreButton {
      opacity: 0;
    }
  }

  &.isSelected {
    background-color: $stripeListSelectedBackgroundColor;

    .selectButton {
      opacity: 1;
    }
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: calc($summaryCellPadding / 2);

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.posterContainer {
  flex-shrink: 0;
  padding: calc($summaryCellPadding / 2);
}

.summaryContainer {
  flex-grow: 1;
  overflow: hidden;
  padding: calc($summaryCellPadding / 2);
}

.librarySummaryContainer {
  composes: summaryContainer;

  padding-right: 40px;
}

.titleContainer {
  display: flex;
  overflow: hidden;
  font-size: $largeFontSize;
}

.title {
  @add-mixin truncate;
  flex-grow: 1;
}

.doubleTitle {
  @add-mixin doubleLineTruncate;
  flex-grow: 1;
}

.subtitle {
  composes: title;

  color: $dimColor;
}

.rating {
  flex-shrink: 0;
  padding-left: 20px;
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $dimColor;
}

.summary {
  overflow: hidden;
  margin-top: 5px;
  max-width: $prePlaySummaryMaxWidth;
  height: calc($lineHeightComputed * $defaultFontSize * 3);
  color: $defaultLinkColor;
}

.shorterSummary {
  composes: summary;

  height: calc($lineHeightComputed * $defaultFontSize * 2);
}

.actions {
  flex-shrink: 0;
  padding-left: 10px;
}

.button {
  padding: 0 10px;
  opacity: 0;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 1;
  }
}

.selectCoverButton {
  @add-mixin cover;
  cursor: default;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';
  composes: button;
}

.editButton,
.moreButton {
  composes: button;
}
