.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: calc($summaryCellPadding / 2);

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.selectOverlay {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  z-index: 1;
}

.selectedListRow {
  composes: row from '~desktop/common/js/v4/core/ui/lists/components/ListRow.css';

  background-color: $stripeListSelectedBackgroundColor;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.rangeListRow {
  composes: row from '~desktop/common/js/v4/core/ui/lists/components/ListRow.css';

  background-color: $stripeListHoverBackgroundColor;
}

.simpleSummaryContainer {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
  padding: calc($summaryCellPadding / 2);
}

.indexPlayContainer {
  position: relative;
  flex: 0 0 70px;
  padding-right: 5px;
}

.titleEpisodeIndex {
  color: $dimColor;
  font-size: $largeFontSize;
  opacity: 1;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 0;
  }
}

.playButtonContainer {
  position: absolute;
  top: 0;
  left: 0;
}

.titleAndSummaryContainer {
  overflow: hidden;
  width: 100%;
}

.titleContainer {
  display: flex;
}

.title {
  @add-mixin truncate;
  flex-grow: 1;
  padding-right: 20px;
  font-size: $largeFontSize;
}

.summary {
  @add-mixin truncate;
  margin-top: 5px;
  max-width: $prePlaySummaryMaxWidth;
  height: calc($lineHeightComputed * $defaultFontSize * 1);
  color: $defaultLinkColor;
}

.actions {
  flex-shrink: 0;
  padding-left: 10px;
}
