//
// Dropdown menus
// --------------------------------------------------


// Bootstrap uses .dropdown-menu > li > a, but we might want to add these styles
// to children further down the hierarchy.
.dropdown-menu-item {
  display: block;
  padding: 3px 20px;
  clear: both;
  font-weight: normal;
  line-height: @line-height-base;
  color: @dropdown-link-color;
  white-space: nowrap; // prevent links from randomly breaking onto new lines

  &:hover,
  &:focus {
    text-decoration: none;
    color: @dropdown-link-hover-color;
    background-color: @dropdown-link-hover-bg;
  }
}

// States

.dropdown.open > a {
  color: @orange-light;
}

// Labels

.dropdown-truncated-label {
  display: inline-block;
  max-width: 300px;
  vertical-align: top;
  .text-overflow();
}

.dropdown-secondary-label {
  display: block;
}

// Dropdown icons

.dropdown-item-icon {
  margin-right: 5px;
}

.dropdown-selected-icon {
  margin-left: 8px;
}

// Menu States

.dropdown-menu .has-danger {
  &:hover,
  &:focus {
    background-color: @brand-danger;
  }
}

.dropdown-menu > li > a.disabled {
  cursor: default;
  .opacity(0.3);

  &:hover,
  &:focus {
    color: @dropdown-link-color;
    background-color: transparent;
  }
}

// Header

.dropdown-header {
  max-width: 200px;
  font-family: @font-family-bold;
  color: @dropdown-header-color;
  text-transform: uppercase;
  .text-overflow();

  &.primary-player {
    max-width: none;

    & > a {
      padding-right: 0;

      &:hover {
        background-color: inherit;
      }
    }
  }
}

.dropdown-header-selected-item {
  .dropdown-header;
  text-transform: none;
}

// Signed in items

.signed-in-item {
  display: none;
}

.signed-in {
  .signed-in-item {
    display: block;
  }

  .signed-out-item {
    display: none;
  }
}

.signed-in:not(.full-user) {
  .full-user-item {
    display: none;
  }
}
