.hubCell {
  margin-bottom: 40px;
}

.hubCell:first-of-type {
  margin-top: 25px;
}

.list {
  margin: 2px -15px;
  border-width: 2px 0;
  border-style: solid;
  border-color: $dividerColor;
}

.hubCellHeader {
  composes: hubCellHeader from '~desktop/common/js/views/v3/components/metadata/lists/HubCellHeader.css';

  position: relative;
}

.attribution {
  flex-shrink: 0;
  margin-right: 4px;
  padding: 2px;
  width: 20px;
  height: 20px;
}

.title {
  composes: hubCellTitle from '~desktop/common/js/views/v3/components/metadata/lists/HubCellTitle.css';

  display: flex;
}

.titleButton {
  color: $dimColor;
  text-transform: uppercase;
  font-size: $defaultFontSize;
  font-family: $boldFontFamily;
}
