//
// Typography
// --------------------------------------------------


// Fonts
// -------------------------

@font-face {
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  font-family: 'Open Sans Regular';
  src: url('~core/fonts/open-sans/open-sans-regular.woff') format('woff');
}

@font-face {
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  font-family: 'Open Sans Semibold';
  src: url('~core/fonts/open-sans/open-sans-semibold.woff') format('woff');
}

@font-face {
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  font-family: 'Open Sans Bold';
  src: url('~core/fonts/open-sans/open-sans-bold.woff') format('woff');
}


// Headings
// -------------------------

h1, .h1 { font-size: floor(@font-size-base * 2); } // ~28px
h2, .h2 { font-size: ceil(@font-size-base * 1.7); } // ~24px
h3, .h3 { font-size: ceil(@font-size-base * 1.4); } // ~20px
h4, .h4 { font-size: floor(@font-size-base * 1.15); } // ~16px
h5, .h5 { font-size: @font-size-base; }
h6, .h6 { font-size: ceil(@font-size-base * 0.85); } // ~12px

h1 small, .h1 small { font-size: ceil(@font-size-base * 1.70); } // ~24px
h2 small, .h2 small { font-size: ceil(@font-size-base * 1.25); } // ~18px
h3 small, .h3 small,
h4 small, .h4 small { font-size: @font-size-base; }

h1,
h2,
h3 {
  margin-top: 40px;
  margin-bottom: 20px;
}

h1.padded,
h2.padded,
h3.padded {
  margin-top: @line-height-computed;
  margin-bottom: (@line-height-computed);
}

.container > h1,
.container > h2,
.container > h3,
.container > h4 {
  margin-right: 20px;
  margin-left: 20px;
}

h5, .h5,
h6, .h6 {
  font-family: @font-family-bold;
  color: @gray;
  text-transform: uppercase;
}
