//
// Progress bars
// --------------------------------------------------


.circle-progress {
  width: 100%;
  height: 100%;
  color: @white;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 50%;
  position: relative;
}

.circle-progress-text {
  position: absolute;
  width: 100%;
  height: 100%;
  font-family: @font-family-bold;
}

.circle-progress-bar {
  position: absolute;
  left: 0;
  top: 0;
  stroke-dashoffset: 0;
  -webkit-transform-origin: center center;
  -webkit-transform: rotate(-90deg);
  transform-origin: center center;
  transform: rotate(-90deg);
}
