.container {
  display: flex;
  margin-bottom: 10px;
  padding: $modalBodyPadding $modalBodyPadding 10px;
  border-bottom: 1px solid $v4BaseLight;
}

.label {
  line-height: $mediumSize;
}

.lineups {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-left: 15px;

  & > :not(:first-child) {
    margin-top: 10px;
  }
}

.lineup {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.lineupSelect {
  display: flex;
  align-items: center;
  flex-grow: 1;
  max-width: 380px;
}

.addLineup {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  &:hover:not(.disabled) .addLineupCircle {
    background-color: $defaultLinkHoverColor;
  }

  &:global(.isDisabled) {
    color: $dimColor;

    .addLineupCircle {
      background-color: $dimColor;
    }
  }
}

.addLineupCircle {
  display: inline-block;
  margin-right: 5px;
  width: 13px;
  border-radius: 50%;
  background-color: $defaultLinkColor;
  color: $v4BaseDark;
  text-align: center;
  line-height: 13px;
  transition: background-color $defaultSpeed;
}

.deleteButton {
  composes: iconButton from '~desktop/common/js/v4/core/ui/buttons/components/IconButton.css';

  margin-right: 20px;

  margin-bottom: -2px;
}

@media only screen and (max-width: $breakpointExtraSmall) {
  .container {
    flex-direction: column;
  }

  .lineups {
    margin-top: 10px;
    margin-left: 0;
  }
  .lineup {
    align-items: flex-start;
    flex-direction: column;
  }

  .lineupSelect {
    width: 100%;
  }
}
