.button {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  position: absolute;
  display: block;
  width: 32px;
  height: 32px;
  text-align: center;
  font-size: $defaultIconFontSize;
  line-height: $defaultIconFontSize;
  transition: all $defaultSpeed;
}

.playButton {
  @add-mixin absoluteCenter;
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: button;

  transform: translate(-50%, -50%);
}

.playCoverButton {
  composes: hoveredLink from '~desktop/common/js/v4/core/ui/cards/PosterCardLink.css';
  composes: playButton;

  width: 100%;
  height: 100%;
}

.playCircle {
  composes: playCircle from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: cardCircle from './MetadataPosterCardCircle.css';

  font-size: 12px;
}

.editButton {
  composes: button;

  bottom: 0;
  left: 0;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/v4/core/ui/buttons/components/SelectButton.css';
  composes: button;

  top: 0;
  left: 0;
}

.moreButton {
  composes: button;

  right: 0;
  bottom: 0;
}

/*
 * States
 */

.isHidden {
  opacity: 0;
}
