.dropFileOverlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(40, 40, 40, 0.8);
  font-size: $largeFontSize;
  font-family: $boldFontFamily;
  pointer-events: none;
}

.icon {
  margin-right: 8px;
}
