.container {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  max-height: 35%;
  background-color: rgba(255, 255, 255, 0.07);
}

.loadingContainer {
  composes: container;

  align-items: center;
  justify-content: center;
  min-height: 80px;
}

.scroller {
  composes: scroller from '~desktop/common/js/v4/core/ui/scrollers/components/Scroller.css';

  flex-grow: 1;
  padding: 10px 50px;
  min-height: 80px;
}

.editHeading {
  font-size: $largeFontSize;
  font-family: $defaultFontFamily;
}

.buttonBar {
  display: flex;
  justify-content: space-between;
  flex-grow: 0;
  flex-shrink: 0;
  padding: 10px 50px;
}

.buttonCluster {
  > button,
  div {
    margin-left: 10px;
  }
}

.applyButton {
  composes: button from '~desktop/common/js/v4/core/ui/buttons/components/Button.css';

  margin: 0 10px;
}

.formLabel {
  composes: label from '~app/desktop/common/js/v4/core/ui/forms/components/FormLabel.css';

  display: inline-block;
  margin: 0 10px;
}

.textInput {
  composes: input from '~desktop/common/js/v4/core/ui/forms/components/TextInput.css';

  display: inline-block;
  max-width: 50px;
}
