.button {
  composes: link from '~core/components/buttons/Link.css';

  position: relative;
  flex-shrink: 0;
  margin: 0 15px;
  padding: 3px 17px;
  height: 24px;
  border-radius: 4px;
  background-color: $primaryColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
  line-height: 1;
}
