.sidebarContainer {
  display: flex;
  flex: 0 0 $sidebarWidth;
  flex-direction: column;
  width: $sidebarWidth;
  height: 100%;
  background-color: $sidebarBackgroundColor;
  /*
    Fixes Safari rendering glitch
    See: https://github.com/plexinc/plex-web-client/issues/13103
  */
  transform: translate3d(0, 0, 0);
}
