//
// Badges
// --------------------------------------------------


// Base classes

.badge {
  padding: 3px 7px;
  font-weight: normal;
  color: @white;
  background-color: @orange;
  border-radius: (@font-size-small + 6) / 2;
}

.badge-lg {
  padding: 4px 8px;
  font-size: @font-size-base;
  border-radius: (@font-size-base + 6) / 2;
}

.badge-transparent {
  padding: 0;
  background-color: transparent;
}

.badge-round {
  font-size: @font-size-base;
  width: @font-size-base * 2;
  height: @font-size-base * 2;
  border-radius: 50%;
}


// Dropdown badge

.dropdown-menu {
  .badge {
    margin-left: 5px;
  }
}
