.subscription {
  display: flex;
  align-items: stretch;
}

.link {
  flex-grow: 1;
  overflow: hidden;
}

/* See: https://bugzilla.mozilla.org/show_bug.cgi?id=984869 */
.linkContainer {
  display: flex;
  align-items: center;
  flex-grow: 1;
  padding: 12px 0 12px 24px;
}

.posterContainer {
  position: relative;
  flex-shrink: 0;
  margin-right: 14px;
  background-color: $blackDarkTranslucentBackgroundColor;
}

.titleContainer {
  flex-grow: 1;
  overflow: hidden;
}

.dragHandle {
  composes: link from '~core/components/buttons/Link.css';

  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  width: $recordingPriorityDragHandleWidth;
  color: rgba(255, 255, 255, 0.3);
  text-align: center;
  font-size: $defaultIconFontSize;
  cursor: grab;
}

.dragIcon {
  top: 0;
}
