/**
 * From: https://github.com/suitcss/utils-text/blob/master/lib/text.css
 *
 * Text truncation
 *
 * Prevent text from wrapping onto multiple lines, and truncate with an
 * ellipsis.
 *
 * 1. Explicitly setting `min-width` allows truncated text on flex child
 *    elements. Source: https://css-tricks.com/flexbox-truncated-text
 *
 * 2. Ensure that the node has a maximum width after which truncation can
 *    occur.
 */

@define-mixin truncate {
  overflow: hidden !important;
  min-width: 0; /* 1 */
  max-width: 100%; /* 2 */
  text-overflow: ellipsis;
  white-space: nowrap !important;
}
