.outerContainer {
  /* The outer container is responsible for aligning the innerContainer */
  @add-mixin cover;

  display: flex;
  align-items: center;
  justify-content: center;
}

.innerContainer {
  position: relative;
}

.horizontal,
.vertical {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: $playerMediaOverlayOuterMargin;
  pointer-events: none;
}

.horizontal {
  flex-direction: row;

  .overlay {
    margin-left: $playerMediaOverlayInnerMargin;
  }

  .overlay:first-child {
    margin-left: 0;
  }
}

.vertical {
  flex-direction: column;

  .overlay {
    margin-top: $playerMediaOverlayInnerMargin;
  }

  .overlay:first-child {
    margin-top: 0;
  }
}

.northwest {
  composes: horizontal;

  top: 0;
  left: 0;
}

.north {
  composes: horizontal;

  top: 0;
  right: 0;
  left: 0;
}

.northeast {
  composes: horizontal;

  top: 0;
  right: 0;
}

.east {
  composes: vertical;

  top: 0;
  right: 0;
  bottom: 0;
}

.southeast {
  composes: horizontal;

  right: 0;
  bottom: 0;
}

.south {
  composes: horizontal;

  right: 0;
  bottom: 0;
  left: 0;
}

.southwest {
  composes: horizontal;

  bottom: 0;
  left: 0;
}

.west {
  composes: vertical;

  top: 0;
  bottom: 0;
  left: 0;
}

.center {
  composes: horizontal;

  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
