.image {
  border-radius: $v4CardBorderRadius;
}

/* Avoid display: flex on .titleContainer so break-word can be used to wrap. */
.titleContainer {
  position: absolute;
  top: 50%;
  right: 20px;
  left: 20px;
  text-align: center;
  transform: translate(0, -50%);
  pointer-events: none;
}

.title {
  text-shadow: 0 2px 4px $shadowColor;
  word-wrap: break-word;
  font-size: $largeFontSize;
  user-select: none;
}
