.container {
  position: absolute;
  top: 40px;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: $v4BaseMediumLight;
}

.emptyContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
}

.emptyMessage {
  margin-top: 10px;
}

.scroller {
  composes: scroller from '~desktop/common/js/v4/core/ui/scrollers/components/VirtualListScroller.css';

  position: relative;
  flex-grow: 1;
}

@media only screen and (max-width: $breakpointLarge) {
  .container {
    position: relative;
    top: 0;
    flex-grow: 1;
  }
}
