.playQueueIsLoading {
  /* Become a flex container only long enough to center the spinner. */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  /* Present a spinner in the same visible region as the bottom bar controls */
  height: $playerControlsHeight;
}

@media screen and (max-width: $playerControlsNarrowBreakpoint) {
  .playQueueIsLoading {
    height: $playerControlsNarrowHeight;
  }
}
