.header {
  display: flex;
  align-items: center;
  padding: 0 15px;
  height: $serverShareListRowHeight;
}

.toggleButton {
  composes: link from '~core/components/buttons/Link.css';

  margin-right: 10px;
  width: 20px;
}

.toggleButtonHidden {
  composes: toggleButton;

  visibility: hidden;
}

.libraries {
  overflow: hidden;
  transition: max-height $defaultSpeed ease-in-out;
}

.hidden {
  display: none;
}

.emptyMessage {
  padding: 0 15px 0 70px;
  height: $serverShareListRowHeight;
  color: rgba(255, 255, 255, 0.75);
  font-size: $largeFontSize;
}
