$tagLineHeight: 1.54;

.container {
  display: inline-block;
  flex-grow: 0;
  flex-shrink: 0;
  margin: 2px;
  height: 20px;
  line-height: $tagLineHeight;
}

.tag {
  padding: 1px 8px;
  border-radius: 2px;
  background-color: rgba(0, 0, 0, 0.6);
}

.remove {
  composes: remove from '~core/components/forms/TagLabel.css';
}
