$verticalSpacing: 40px;

.emptyPageContentScroller {
  composes: pageContentScroller from '~desktop/common/js/v4/core/ui/pages/components/PageContent.css';

  display: flex;
  align-items: center;
  flex-direction: column;
  padding: $v4PagePadding $v4PagePadding
    calc($pageHeaderRowHeight + $v4PagePadding);
}

.content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  max-width: 640px;
  font-size: $largeFontSize;
}

.icon {
  margin-bottom: $verticalSpacing;
  padding: 30px;
  border-radius: 50%;
  background-color: $blackDarkerTranslucentBackgroundColor;
  font-size: 40px;
  line-height: 0;
}

.imgIcon {
  margin-bottom: $verticalSpacing;
  width: 100px;
  height: 100px;
}

.title {
  text-align: center;
  font-size: 24px;
  font-family: $boldFontFamily;
  line-height: 1.5;
}

.description {
  margin: 30px 0 $verticalSpacing;
  color: $secondaryContentColor;
  text-align: center;
  font-size: 15px;
  line-height: 1.5;
}

.footer {
  flex-shrink: 0;
  margin-bottom: $verticalSpacing;
  color: $secondaryContentColor;
  text-align: center;
}
