$stripeStartColor: rgba(0, 0, 0, 0.05);
$stripeEndColor: rgba(0, 0, 0, 0.15);
$stripeHoverStartColor: rgba(255, 255, 255, 0.05);
$stripeHoverEndColor: transparent;

.modalBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  padding: 0;
}

.container {
  background-image: repeating-linear-gradient(
    -45deg,
    $stripeStartColor,
    $stripeStartColor 10px,
    $stripeEndColor 10px,
    $stripeEndColor 20px
  );
}

.dropHover {
  background-image: repeating-linear-gradient(
    -45deg,
    $stripeHoverStartColor,
    $stripeHoverStartColor 10px,
    $stripeHoverEndColor 10px,
    $stripeHoverEndColor 20px
  );
}

.uploadingContainer {
  padding: 30px;
}

.fileInputContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px;
  color: #bbb;
}

.fileInputLink {
  color: $primaryColor;
}

.separator {
  display: inline-block;
  margin: 5px;
}
