.innerModalBody {
  composes: innerModalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-direction: column;
  height: 100%;
}

.header {
  color: $v4AltMedium;
  font-size: $largeFontSize;
}

.formContainer {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.formElements {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.formError {
  margin-top: 15px;
  color: $dangerColor;
}

.section {
  width: 48%;
}

.buttonBar {
  composes: buttonBar from '~desktop/common/js/v4/core/ui/modals/components/ModalFooterButtonBar.css';

  display: flex;
  justify-content: space-between;
  width: 100%;
}

@media only screen and (max-width: $breakpointExtraSmall) {
  .form {
    flex-direction: column;
  }

  .section {
    margin-bottom: 20px;
    width: 100%;
  }
}
