.input {
  composes: input from './input.css';

  position: relative;
  background-color: $inputBackgroundColor;
  color: $inputColor;
}

.input::after {
  position: absolute;
  display: inline-block;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: $inputColor;
  border-top-width: 5px;
  border-right: 4px solid transparent;
  border-bottom-width: 0;
  border-left: 4px solid transparent;
  content: '';
  pointer-events: none;
}

.small {
  composes: small from './input.css';

  &::after {
    top: 10px;
    right: 10px;
  }

  .select {
    padding: 2px 25px 2px 10px;
  }
}

.large {
  composes: large from './input.css';

  &::after {
    top: 16px;
    right: 10px;
  }

  .select {
    padding: 5px 25px 5px 10px;
  }
}

.select {
  position: absolute;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
  color: $inputColor;
  appearance: none;

  /*
    Firefox and IE will use the color from the select and apply it to the option.
    Both browsers will not use the color property on the `option`, but will use a
    background color property. This makes the `option` readable in Firefox and IE.
  */
  option {
    background-color: #2d2d2d;
  }

  &::-ms-expand {
    display: none;
  }
}

.isDisabled {
  opacity: $inputDisabledOpacity;

  .select {
    cursor: not-allowed;
  }
}

.hasError {
  border: 2px solid $dangerColor;
}
