//
// Forms
// --------------------------------------------------

// Normalize non-controls
//
// Restyle and baseline non-control form elements.

label {
  font-weight: normal;
  color: @input-label-color;

  &.disabled {
    color: @input-label-disabled-color;
    cursor: not-allowed;
  }
}

// Common form controls
//
// Shared size and type resets for form controls. Apply `.form-control` to any
// of the following form controls:
//
// select
// textarea
// input[type="text"]
// input[type="password"]
// input[type="datetime"]
// input[type="datetime-local"]
// input[type="date"]
// input[type="month"]
// input[type="time"]
// input[type="week"]
// input[type="number"]
// input[type="email"]
// input[type="url"]
// input[type="search"]
// input[type="tel"]
// input[type="color"]

.form-control {
  line-height: 1.5em;
  border: 0;
  .box-shadow(none);
  .transition(background-color 0.2s);

  &[disabled] {
    color: @gray;
  }
}

.form-control-btn {
  margin-bottom: 5px;
}

.control-error {
  display: none;
  margin-left: 10px;
}

.form-group {
  margin-bottom: 20px;
}

.form-footer {
  display: flex;
  align-items: center;
  margin-top: 40px;

  .btn {
    flex-shrink: 0;
  }
}

.form-message {
  margin: 0 20px;
  color: @gray-light;

  &.has-error {
    color: @state-danger-text;
  }
}

.input-group-addon:hover,
.input-group-addon:focus {
  color: @gray-lighter;
  &.disabled {
    color: @gray-darkest;
    cursor: not-allowed;
  }
}

// Form control sizing
.input-sm {
  font-size: @font-size-base;
  line-height: 1.3em;
}

// Search

.form-group-search {
  position: relative;
  margin: 0;
}

.form-control-search {
  height: @input-height-base - 9;
  padding: 2px 28px 2px 32px;
  background-color: #3a3a3a;
  border-radius: (@input-height-base - 9) / 2;
}

.control-label-search {
  position: absolute;
  font-size: 12px;
  width: 100%;

  i.search {
    position: absolute;
    top: 9px;
    left: 10px;
  }

  .clear-search-btn {
    position: absolute;
    top: 5px;
    right: 10px;
    color: @gray-light;

    &:hover {
      color: @white;
    }
  }

  &.selected {
    .clear-search-btn:hover {
      color: @gray-dark;
    }
  }
}

// Help text
//
// Apply to any element you wish to create light text for placement immediately
// below a form control. Use for general help, formatting, or instructional text.

.help-block {
  color: rgba(255, 255, 255, 0.45);

  .inline-icon {
    margin-right: 8px;
  }
}

// Form control feedback states
//
// Apply contextual and semantic states to individual form controls.

// Warning
.has-warning {
  .form-control-validation(
    @state-warning-text; @state-warning-text; @state-warning-bg
  );
}
// Error
.has-error {
  .form-control-validation(
    @state-danger-text; @state-danger-text; @state-danger-bg
  );
  .selectize-group {
    .form-control-validation(
      @state-danger-text; @state-danger-text; @state-danger-bg
    );

    .selectize-input {
      background-color: @state-danger-bg;
    }
  }
}

// Success
.has-success {
  .form-control-validation(
    @state-success-text; @state-success-text; @state-success-bg
  );
}

// Selectize overrides
.selectize-group {
  width: 100%;

  .form-control.selectize-control {
    cursor: text;

    &.single .selectize-input {
      cursor: text;
      transition: background-color 0.2s;
      border-radius: @border-radius-base;

      // Apply app focused-input style to selectize input
      .form-control-focus();
      input {
        color: @gray;
        width: 100%;
        position: absolute;
        left: 14px;
        cursor: text;
      }

      // Hide down-arrow caret
      &:after {
        content: '';
        display: none;
      }
    }
  }

  .lockable-field.form-control.selectize-control.single .selectize-input {
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
  }
}
.autocomplete-annotate {
  color: @gray-light;
}

// Suppress spinner styles for input[type="number"] controls in Webkit browsers
&::-webkit-inner-spin-button,
&::-webkit-outer-spin-button {
  margin: 0;
  -webkit-appearance: none;
}
