.modalBody {
  composes: modalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  padding: 0;
}

.topContainer {
  display: flex;
  height: 76px;
}

.avatarImg {
  composes: avatar from '~desktop/common/js/views/v3/components/core/images/AvatarImg.css';

  flex-shrink: 0;
  margin-right: 30px;
  width: 76px;
  height: 76px;
}

.description {
  color: $dimColor;
}

.modalFooter {
  composes: modalFooter from '~desktop/common/js/v4/core/ui/modals/components/ModalFooter.css';

  justify-content: center;
  flex-direction: column;
  height: 224px;
}

.formLabel {
  composes: label from '~core/components/forms/FormLabel.css';

  margin-bottom: 10px;
  width: 100%;
}

.form {
  margin-bottom: 15px;
  width: 100%;
}

.textArea {
  composes: textArea from '~core/components/forms/TextArea.css';

  max-width: none;
}

.buttonBar {
  composes: buttonBar from '~desktop/common/js/v4/core/ui/modals/components/ModalFooterButtonBar.css';

  display: flex;
  justify-content: flex-end;
  width: 100%;
}
