.container {
  @add-mixin resetFont;
  position: relative;
  z-index: $navbarZIndex;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-grow: 1;
  width: 100%;
  height: 60px;
  background: $darkOpaqueBackgroundColor;
}

:global(.show-video-player) .container {
  opacity: 0;
  pointer-events: none;
}

.side {
  display: flex;
  align-items: center;
  height: 100%;
}

.button {
  composes: button from './NavBarIconButton.css';
}

.buttonSeparator {
  margin: 10px;
  width: 2px;
  height: 66%;
  background-color: $menuSeparatorBackgroundColor;
}

.accountButton {
  composes: button from './NavBarAccountButton.css';

  display: inline-flex;
  align-items: center;
  flex-shrink: 0;
  margin-left: 25px;
}

.plexLogo {
  composes: button from './NavBarIconButton.css';

  margin: 0 10px;
  width: 45px;
  background-image: url('~common/img/plex-logo.svg');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.left {
  composes: side;

  padding-left: 10px;
}

.right {
  composes: side;

  padding-right: 17px;

  .button {
    padding: 0 15px;
  }
}
