$smallArrowBorderWidth: calc($smallArrowWidth / 2);
$mediumArrowBorderWidth: calc($mediumArrowWidth / 2);
$largeArrowBorderWidth: calc($largeArrowWidth / 2);

.disclosureArrow {
  display: inline-block;
  margin: 0 5px;
  width: 0;
  height: 0;
  border-style: solid;
  vertical-align: middle;
  transition: border $defaultSpeed, transform 0.4s;
}

/*
 * Kinds
 */

.default {
  border-color: $defaultLinkColor;
}

.primary {
  border-color: $primaryColor;
}

.success {
  border-color: $successColor;
}

.danger {
  border-color: $dangerColor;
}

/*
 * Directions & Sizes
 */

/* Add specificity to override hover state. */
.disclosureArrow.small {
  &.up {
    margin-top: -1px;
    border-top-width: 0;
    border-right: $smallArrowBorderWidth solid transparent;
    border-bottom-width: 4px;
    border-left: $smallArrowBorderWidth solid transparent;
  }

  &.right {
    margin-top: -2px;
    border-top: $smallArrowBorderWidth solid transparent;
    border-right-width: 0;
    border-bottom: $smallArrowBorderWidth solid transparent;
    border-left-width: 4px;
  }
}

.disclosureArrow.medium {
  &.up {
    margin-top: -2px;
    border-top-width: 0;
    border-right: $mediumArrowBorderWidth solid transparent;
    border-bottom-width: 5px;
    border-left: $mediumArrowBorderWidth solid transparent;
  }

  &.right {
    margin-top: -2px;
    border-top: $mediumArrowBorderWidth solid transparent;
    border-right-width: 0;
    border-bottom: $mediumArrowBorderWidth solid transparent;
    border-left-width: 5px;
  }
}

.disclosureArrow.large {
  &.up {
    margin-top: -3px;
    border-top-width: 0;
    border-right: $largeArrowBorderWidth solid transparent;
    border-bottom-width: 6px;
    border-left: $largeArrowBorderWidth solid transparent;
  }

  &.right {
    margin-top: -2px;
    border-top: $largeArrowBorderWidth solid transparent;
    border-right-width: 0;
    border-bottom: $largeArrowBorderWidth solid transparent;
    border-left-width: 6px;
  }
}

.down {
  composes: up;

  transform: rotateX(180deg);
}

.left {
  composes: right;

  transform: rotateY(180deg);
}

/*
 * States
 */

.isSelected {
  border-color: $selectedColor;
}

a,
button {
  &:hover .disclosureArrow {
    border-color: $defaultLinkHoverColor;
  }
}
