@define-mixin stripeListItem {
  &:nth-child(odd) {
    background-color: $stripeListAlternateBackgroundColor;
  }

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }

  &:global(.isDragging) {
    background-color: $stripeListSelectedBackgroundColor;
  }

  &:global(.isDraggingBefore) {
    box-shadow: inset 0 4px 0 -2px $selectedColor;
  }

  &:global(.isDraggingAfter) {
    box-shadow: 0 2px 0 0 $selectedColor;
  }
}
