.placeholder {
  background: linear-gradient(
    90deg,
    transparent,
    $stripeListHoverBackgroundColor
  );
  background-size: 400% 100%;
  animation: shimmer $slowSpeed alternate ease-in infinite;
}

@keyframes shimmer {
  0% {
    background-position: 0% 0%;
  }

  100% {
    background-position: 100% 0%;
  }
}
