.overlay {
  @add-mixin cover;

  overflow: hidden;
  user-select: none;
}

.overlay:hover .background {
  background: $posterHoverBackgroundColor;
  opacity: 1;
}

.background {
  @add-mixin cover;

  background: $posterHoverBackgroundColor;
  opacity: 0;
  transition: opacity $defaultSpeed;
}

.hoverBackground,
.overlay:hover .hoverBackground {
  background: $posterHoverBackgroundColor;
}

.selectedBackground,
.overlay:hover .selectedBackground {
  background: $posterSelectedBackgroundColor;
}

.visibleBackground {
  opacity: 1;
}
