.modalContent {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
  background-color: $modalBackgroundColor;
}

.closeButton {
  composes: link from '~core/components/buttons/Link.css';

  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 60px;
  height: 60px;
  text-align: center;
  font-size: 18px;
  line-height: 60px;
}
