.container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px 0 0;
  height: $playQueueItemHeight;

  &:hover {
    opacity: 1;
    transition: opacity $defaultSpeed;

    .moveControls,
    .removeControls {
      opacity: 1;
    }

    .metadataDurationContainer {
      opacity: 0;
    }

    .sourceText {
      opacity: 1;
    }
  }
}

.metadataContainer {
  composes: container from './AudioVideoPlayerPlayQueueMetadata.css';

  &:hover {
    .metadataDurationContainer {
      opacity: 0;
    }
  }
}

.metadataDurationContainer {
  composes: durationContainer from './AudioVideoPlayerPlayQueueMetadata.css';

  opacity: 1;
  transition: opacity $fastSpeed;
}

.sourceText {
  composes: text from '~desktop/common/js/views/v3/components/core/SourceBadge.css';

  opacity: 0;
  transition: opacity $fastSpeed;
}

.leftContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: $playQueueMoveButtonWidth;
  height: 100%;
}

.moveControls {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: $defaultLinkColor;
  font-size: $defaultIconFontSize;
  opacity: 0;
  cursor: grab;
  transition: opacity $defaultSpeed;

  &:hover {
    color: $defaultLinkHoverColor;
  }
}

.removeControls {
  position: absolute;
  top: 0;
  right: 0;
  opacity: 0;
  transition: opacity $defaultSpeed;
}

.removeButton {
  composes: button from '~core/components/buttons/IconButton.css';

  padding: 0 20px;
  line-height: $playQueueItemHeight;
}

.opacity20Percent {
  opacity: 0.2;
}

.opacity40Percent {
  opacity: 0.4;
}

.opacity60Percent {
  opacity: 0.6;
}

.opacity80Percent {
  opacity: 0.8;
}

/*
* States
*/

.isCurrentItem {
  &:hover {
    .metadataDurationContainer {
      opacity: 1;
    }
  }
}

.isItemDragging {
  .moveControls {
    opacity: 1;
  }
}
