.measure {
  @add-mixin cover;
}

.content {
  @add-mixin absoluteCenter;
  max-width: 80%;
  transition: transform $defaultSpeed;
}

.card {
  composes: card from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCard.css';

  margin: 0 auto;
}

.titlesContainer {
  margin-top: 40px;
  text-align: center;
}

.title {
  composes: title from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterTitle.css';

  a {
    color: $defaultColor;
  }
}

.primaryTitle {
  composes: title;

  height: 30px;
  font-size: 24px;
}

.secondaryTitle {
  composes: title;

  font-size: 15px;
}
