.iconContainer {
  position: relative;
  display: inline-block;
  width: $navigationIconSize;
  height: $navigationIconSize;
  border-radius: 50%;
  background-color: $lightTranslucentBackgroundColor;
  vertical-align: middle;
}

.iconContainer .icon {
  position: absolute;
  top: 0;
}

.activityIndicator {
  composes: spinner from '~core/components/base/Spinner.css';

  position: absolute;
  top: -2px;
  left: -2px;
}
