.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  width: 100%;
  height: 100%;

  &:hover {
    .transientTitleLeft {
      opacity: 0;
    }

    .button {
      opacity: 1;
    }
  }
}

.cardContainer {
  position: relative;
  flex-shrink: 0;
  margin: 0 14px 0 0;
  width: $playlistItemCardWidth;
}

.card {
  composes: card from '~desktop/common/js/views/v3/components/search/posters/QuickSearchMetadataPosterCard.css';

  margin: 0 auto;
}

.indexContainer {
  position: relative;
  flex-shrink: 0;
  width: $playQueueItemHeight;
  height: $playQueueItemHeight;
}

.titlesContainer {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
}

.durationContainer {
  padding-left: 20px;
  color: $dimColor;
  white-space: nowrap;
  transition: opacity $fastSpeed;
}

.downloadIcon {
  composes: downloadIcon from '~desktop/common/js/views/v3/components/core/icons/DownloadIcon.css';

  top: 0;
  color: $defaultBadgeColor;
}

.attributionContainer {
  composes: container from '~desktop/common/js/views/v3/components/core/SourceBadge.css';
}

.transientTitle {
  color: $dimColor;
  opacity: 1;
  transition: opacity $defaultSpeed;
}

.transientTitleLeft {
  composes: transientTitle;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.transientTitleRight {
  composes: transientTitle;
}

.button {
  opacity: 0;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 1;
  }
}

.playButton {
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: button;

  position: absolute;
  top: 50%;
  left: 50%;
  transition: opacity $defaultSpeed;
  transform: translateX(-50%) translateY(-50%);
}

.equalizer {
  composes: equalizer from '~common/js/views/v3/components/core/icons/EqualizerIcon.css';

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
