.placeholder {
  display: flex;
  align-items: center;
  overflow: hidden;
  padding: 4px 10px 4px 24px;
  width: $searchResultWidth;
  height: 44px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  color: $searchResultTitleColor;
  font-size: $defaultFontSize;
  font-family: $defaultFontFamily;
  transition: color $defaultSpeed, background-color $defaultSpeed;

  &:hover {
    background-color: $searchHoverResultBackgroundColor;
    color: $searchResultTitleSelectedColor;
  }
}

.isPlaceholderSelected {
  background-color: $searchSelectedResultBackgroundColor;
  color: $searchResultTitleSelectedColor;
}
