//
// Queue
// --------------------------------------------------

.queue-help-container {

  .row {
    margin-top: 25px;
    margin-bottom: 50px;

    .queue-email {
      cursor: copy;
      width: 405px;
      margin-left: auto;
      margin-right: auto;

      &.disabled {
        background-color: @input-bg-disabled;
      }
    }

    img {
      max-width: 100%;
    }

    .plexit-img {
      margin-top: -15px;
    }

    .steps-list {
      margin-top: 15px;
    }
  }

  .plexit-hidden {
    display: inline-block;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    width: 0;
  }
}
