.link {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';
}

.hoveredLink {
  composes: link;

  border-radius: $v4CardBorderRadius;
  background-color: $v4CardHoverBackgroundColor;
  box-shadow: 0 0 0 1px $v4CardHoverBorderColor,
    0 0 $v4CardShadowSize $v4CardShadowColor;
}
