.concert {
  display: inline-flex;
  align-items: center;
  margin: 10px 25px 10px 5px;
}

.concertCalendar {
  align-self: flex-start;
  flex-shrink: 0;
  margin-right: 20px;
  width: 60px;
  box-shadow: 0 0 2px $shadowColor;
  text-align: center;
}

.concertMonth {
  background: $dangerBackgroundColor;
  color: $defaultColor;
  text-transform: uppercase;
  font-size: $smallFontSize;
  line-height: 20px;
}

.concertDay {
  background: $defaultColor;
  color: $inverseColor;
  font-size: 28px;
  line-height: 40px;
}

.concertDetails {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.concertTitle {
  color: $defaultColor;
}

.concertLocation {
  color: $dimColor;
}
