.fullPlayerContainer {
  @add-mixin cover;
  z-index: $playerZIndex;
  transition: background $defaultSpeed;
}

.fullPlayerContainerBackground {
  background-color: $playerBackgroundColor;
}

.miniPlayerContainer {
  position: relative;
  background-color: $miniPlayerBackgroundColor;
}

.adPlayerDisplayIndexContainer {
  position: absolute;
  top: $playerOverlayActionEdgeGutter;
  right: $playerOverlayActionEdgeGutter;
  padding: 10px 20px;
  border-radius: 4px;
  background-color: $playerSecondaryOverlayColor;
  box-shadow: 0 0 4px $playerShadowColor;
  font-size: $largeFontSize;
}
