.jumpBar {
  display: flex;
  justify-content: space-around;
  flex-shrink: 0;
  flex-flow: column;
  overflow: hidden;
  margin: $jumpBarMargin 0;
  padding: 0 $jumpBarCharacterPadding;
  font-size: $defaultFontSize;
}

.character {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  height: $jumpBarCharacterHeight;
  color: $dimColor;
  text-align: center;
}
