.page {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: scroll;
  width: 100%;
  height: 100%;
}

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-shrink: 0;
  padding-right: 30px;
  padding-left: 30px;
  width: 480px;
  background-color: $formSectionBackgroundColor;
}

.section {
  display: flex;
  align-items: stretch;
  flex-direction: column;
  width: 100%;
  text-align: center;

  &:first-child {
    margin-top: 30px;
  }

  &:last-child {
    margin-bottom: 30px;
  }
}

.addendumLink {
  margin: 20px 0;
}

.switchToSkipSignInLink {
  position: fixed;
  right: 20px;
  bottom: 20px;
  font-size: $defaultFontSize;
}

.button {
  composes: button from '~core/components/buttons/SpinnerButton.css';
  composes: large from '~core/components/buttons/Button.css';

  margin-top: 2px;
  padding-top: 6px;
  padding-bottom: 5px;
}
