.slider {
  composes: slider from './Slider.css';
}

.fill {
  composes: fill from './Slider.css';

  transform-origin: center bottom;
}

.thumb {
  composes: thumb from './Slider.css';

  margin: 0 0 calc($sliderThumbSize / -2) 0;
}

.thumbTrack {
  composes: thumbTrack from './Slider.css';

  align-items: flex-end;
  flex-direction: row;
  height: 100%;
}

.track {
  composes: track from './Slider.css';

  width: 2px;
  height: 100%;
}
