.actionButton {
  @add-mixin truncate;
  composes: button from '~desktop/common/js/v4/core/ui/buttons/components/Button.css';
}

.labeledActionButton {
  composes: actionButton;

  padding: 0 15px;

  .icon {
    margin-right: 2px;
  }
}

.iconActionButton {
  composes: actionButton;

  padding: 0;
  min-width: 40px;
  background-color: rgba(0, 0, 0, 0.3);
}

.icon {
  top: -1px;
  font-size: 18px;
}

.tooltip {
  composes: topCenter from '~core/components/tooltips/Tooltip.css';

  margin-bottom: 12px;
}

.small {
  height: $smallSize;
  font-size: $defaultFontSize;
}

.medium {
  height: $mediumSize;
  font-size: $defaultFontSize;
}

.large {
  height: $largeSize;
  font-size: $largeFontSize;
}
