.partialButton {
  composes: statusButton from './PrePlayStatusButton.css';

  background-color: $prePlayPartialButtonBackgroundColor;

  &:hover {
    background-color: $prePlayPartialButtonBackgroundColor;
  }

  &:global(.isActive) {
    background-color: darken($prePlayPartialButtonBackgroundColor, 0.2);
  }
}

.failedButton {
  composes: statusButton from './PrePlayStatusButton.css';

  background-color: $prePlayUnavailableButtonBackgroundColor;

  &:hover {
    background-color: $prePlayUnavailableButtonBackgroundColor;
  }

  &:global(.isActive) {
    background-color: darken($prePlayUnavailableButtonBackgroundColor, 0.2);
  }
}
