.modalHeader {
  composes: modalHeader from '~core/components/modals/ModalHeader.css';

  border-bottom: 2px solid $dangerColor;
}

.innerModalBody {
  composes: innerModalBody from '~core/components/modals/ModalBody.css';

  padding: 0;
}

.message {
  padding: 15px 30px;
  color: $dimColor;
  font-size: $smallFontSize;
}

.conflicts {
  margin-bottom: 20px;
  background-color: rgba(255, 255, 255, 0.03);
}

.footerMessage {
  flex-grow: 1;
  color: $dimColor;
  font-size: $smallFontSize;
}
