.titleContainer {
  position: relative;
  padding: 0 $v4PagePadding $v4PrePlayListTitleGap;

  &:hover {
    .selectButton {
      opacity: 1;
    }
  }
}

.selectedTitleContainer {
  composes: titleContainer;

  .selectButton {
    opacity: 1;
  }
}

.selectButton {
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';

  position: absolute;
  top: 5px;
  left: 20px;
  opacity: 0;

  &:global(.isFocused) {
    opacity: 1;
  }
}
