.button {
  composes: link from './Link.css';

  display: inline-block;
  overflow: hidden;
  border-radius: 4px;
  color: $v4DefaultColor;
  vertical-align: middle;
  text-align: center;
  text-transform: uppercase;
  white-space: nowrap;
  font-family: $boldFontFamily;
  transition: background-color $defaultSpeed, color $defaultSpeed;
  user-select: none;

  &:global(.isDisabled) {
    color: $dimColor;
  }
}

/*
 * Kinds
 */

.default {
  background-color: $defaultBackgroundColor;

  &:hover {
    background-color: $defaultHoverBackgroundColor;
  }

  &:global(.isActive) {
    background-color: $defaultActiveBackgroundColor;
  }

  &:global(.isDisabled) {
    background-color: $defaultDisabledBackgroundColor;
  }
}

.primary {
  background-color: $primaryBackgroundColor;
  color: $primaryButtonColor;

  &:hover {
    background-color: $primaryHoverBackgroundColor;
    color: $primaryButtonColor;
  }

  &:global(.isActive) {
    background-color: $primaryActiveBackgroundColor;
  }

  &:global(.isDisabled) {
    background-color: $primaryDisabledBackgroundColor;
    color: $primaryDisabledColor;
  }
}

.success {
  background-color: $successBackgroundColor;

  &:hover {
    background-color: $successHoverBackgroundColor;
  }

  &:global(.isActive) {
    background-color: $successActiveBackgroundColor;
  }

  &:global(.isDisabled) {
    background-color: $successDisabledBackgroundColor;
  }
}

.danger {
  background-color: $dangerBackgroundColor;

  &:hover {
    background-color: $dangerHoverBackgroundColor;
  }

  &:global(.isActive) {
    background-color: $dangerActiveBackgroundColor;
  }

  &:global(.isDisabled) {
    background-color: $dangerDisabledBackgroundColor;
  }
}

/*
 * Sizes
 */

.small {
  padding: 0 10px;
  font-size: $defaultFontSize;
}

.medium {
  padding: 5px 20px;
  font-size: $defaultFontSize;
}

.large {
  padding: 8px 30px;
  font-size: $largeFontSize;
}
