.container {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  padding: 15px 60px 15px 30px;
  border-bottom: 2px solid $dangerColor;
  background-color: $errorBackgroundColor;
  color: $defaultColor;
  font-size: $largeFontSize;
}

.icon {
  top: 0;
  flex-shrink: 0;
}

.message {
  padding: 0 10px;
}
