.container {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 0;
  border-top: $channelGridBorderSize solid $channelGridBorderColor;
  border-right: $channelGridBorderSize solid $channelGridBorderColor;
  background-color: $channelGridBackgroundColor;
  white-space: nowrap;

  &.selected {
    margin-bottom: 1px;
    height: calc(100% - 1px);
    border-bottom: 0;
    background-color: $channelGridSelectedItemDetailsBackgroundColor;
  }

  &:hover {
    background-color: $channelGridHoverColor;
  }
}

.hoverable:hover {
  .subTitleContainer {
    opacity: 0;
    transform: translateX(-5px);
  }

  .actions {
    opacity: 1;
    transform: translateX(0);
  }
}

.onAir {
  background-color: $channelGridOnAirBackgroundColor;
}

.finishedAiring {
  background-color: $channelGridFinishedAiringBackgroundColor;
  opacity: 0.4;

  &.selected,
  &:hover {
    background-color: transparent;
  }
}

.underlay {
  @add-mixin cover;
}

.content {
  margin: 5px;
}

.actionsContainer {
  position: relative;
}

.subTitleContainer {
  position: absolute;
  display: flex;
  align-items: center;
  flex-direction: row;
  min-height: 24px;
  opacity: 1;
  transition: opacity $defaultSpeed, transform $fastSpeed;
  transform: translateX(0);
  pointer-events: none;
}

.subTitle {
  margin-right: 8px;
  color: $v4AltMedium;
}

.premiereBadge {
  composes: badge from '~desktop/common/js/views/v3/components/pages/schedule/PremiereAiringBadge.css';

  background-color: $channelGridNewBadgeColor;
}

.subscriptionBadge {
  padding: 0 4px 0 3px;
  background-color: $recordBackgroundColor;
  font-size: 11px;
}

.actions {
  position: absolute;
  opacity: 0;
  transition: opacity $defaultSpeed, transform $fastSpeed;
  transform: translateX(5px);
}

.placeholderContainer {
  display: flex;
  justify-content: space-evenly;
  flex-direction: column;
  height: 100%;
}
