$confirmModalColor: rgba(255, 255, 255, 0.75);

.confirmBody {
  composes: modalScroller from '~core/components/modals/ModalBody.css';

  color: $confirmModalColor;
  word-break: break-word;
}

.accordLabel {
  display: flex;
  margin-top: 25px;
  color: $confirmModalColor;
  user-select: none;

  /* Nest to override Bootstrap input styles */
  .accordInput {
    margin-right: 5px;
  }
}

.cancelButton,
.confirmButton {
  min-width: 65px;
}
