.miniPlayerPoster {
  margin-left: $playerControlsPosterLeftGutter;
}

.cardContainer {
  position: relative;

  &:hover .expandButton {
    opacity: 1;
  }
}

.card {
  composes: card from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCard.css';

  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.3);
}

.expandButton {
  @add-mixin cover;
  composes: playerButton from '~desktop/common/js/views/v3/components/players/core/PlayerIconButton.css';

  z-index: 2;
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0.75);
  text-align: center;
  opacity: 0;
  transition: opacity $defaultSpeed;
}
