.button {
  position: relative;
  overflow: hidden;
  width: $searchResultWidth;
  height: $searchResultHeight;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  color: $searchResultTitleColor;
  transition: color $defaultSpeed, background-color $defaultSpeed;
  pointer-events: none;

  a,
  button {
    pointer-events: all;
  }
}

.button:focus,
.button:hover,
.selected {
  .title {
    color: $searchResultTitleSelectedColor;
  }
}

.button:hover {
  background-color: $searchHoverResultBackgroundColor;
}

.selected {
  background-color: $searchSelectedResultBackgroundColor;
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin cover;

  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 4px 10px 4px 24px;
}

.imageContainer {
  align-self: center;
  flex-basis: 36px;
  margin-right: 10px;
}

.titles {
  position: relative;
  flex-grow: 1;
  width: 163px;
}

.moreButton {
  align-self: center;
  margin-left: 10px;
  height: 100%;
  color: $searchResultTitleColor;
  opacity: 0;
  transition: opacity $defaultSpeed, color $defaultSpeed;

  &:hover {
    color: $searchResultTitleSelectedColor;
  }
}

.moreIcon {
  top: 0;
}

.button:hover .moreButton,
.button.selected .moreButton {
  opacity: 1;
}
