.upsell {
  position: relative;
  display: flex;
  margin: 30px 0;
  padding: 28px 30px;
  height: 136px;
  background-color: $blackDarkerTranslucentBackgroundColor;
}

.description {
  flex-shrink: 0;
  margin-right: 40px;
  width: 264px;
  color: $defaultColor;
  font-size: 14px;
  line-height: normal;
}

.chartImageContainer {
  display: flex;
  margin-right: 40px;
  max-width: 276px;
}

.listImageContainer {
  display: flex;
  max-width: 184px;
}

.image {
  width: 100%;
}

.button {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  position: absolute;
  top: 20px;
  right: 20px;
  font-size: $largeFontSize;
}
