.button {
  position: absolute;
  top: 0;
  bottom: 0;
  padding: 30px;
  text-shadow: $shadowColor 0 0 5px;
  font-size: 40px;
  transition: all $defaultSpeed;

  &:global(.isDisabled) {
    opacity: 0;
  }
}

.leftButton {
  composes: button;

  top: $playerTopControlsHeight;
  bottom: $playerControlsHeight;
  left: -$playerSideControlsWidth;
  width: $playerSideControlsWidth;
  transform: translateX($playerSideControlsWidth);
}

.rightButton {
  composes: button;

  top: $playerTopControlsHeight;
  right: -$playerSideControlsWidth;
  bottom: $playerControlsHeight;
  width: $playerSideControlsWidth;
  transform: translateX(-$playerSideControlsWidth);
}

.topBar {
  position: absolute;
  top: -$playerTopControlsHeight;
  right: 0;
  left: 0;
  height: $playerTopControlsHeight;
  background-color: $playerTopControlsBackgroundColor;
  transition: transform $defaultSpeed, background $defaultSpeed;
  transform: translateY($playerTopControlsHeight);
}

.hideOverlays {
  .leftButton,
  .rightButton {
    transform: translateX(0);
  }

  .topBar {
    transform: translateY(0);
  }

  .playButton {
    opacity: 0;
  }
}
