.container {
  composes: container from './AudioVideoStripeContainer.css';

  left: auto;
  padding: 20px 40px;
  width: auto;
}

.closeButton {
  composes: link from '~core/components/buttons/Link.css';

  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 30px;
  height: 30px;
  text-align: center;
  font-size: $defaultFontSize;
  line-height: 30px;
}

.title {
  margin: 0 20px 20px;
  color: $defaultColor;
  text-align: center;
  text-transform: uppercase;
  font-size: $largeFontSize;
  font-family: $boldFontFamily;
}

.buttonContainer {
  display: flex;
  justify-content: space-between;
}

.button {
  composes: button from '~core/components/buttons/Button.css';

  margin-right: 15px;

  &:last-child {
    margin-right: 0;
  }
}
