$smallTitleSize: 18px;
$mediumTitleSize: 22px;
$largeTitleSize: 28px;

$smallCallToActionSize: 16px;
$mediumCallToActionSize: 20px;
$largeCallToActionSize: 24px;

$smallSummarySize: 12px;
$mediumSummarySize: 14px;
$largeSummarySize: 18px;

$attributionLogoMaxHeight: 40px;
$textContentMaxWidth: 1400px;

.spotlightImage {
  width: auto;
  height: 100%;
}

.copyOverlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  padding: 20px 20px 40px;
  background-image: linear-gradient(
      to left,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0.4)
    ),
    linear-gradient(
      to top,
      rgba(0, 0, 0, 0.9),
      rgba(0, 0, 0, 0.7) 50%,
      rgba(0, 0, 0, 0) 100%
    );
}

.shadeOverlay {
  position: absolute;
  width: 100%;
  height: 100%;
}

.colorOverlay {
  position: relative;
  display: none;
  flex: 1;
  margin-right: -400px;
}

.title {
  @add-mixin doubleLineTruncate;
  margin-bottom: 12px;
  max-width: $textContentMaxWidth;
  color: $defaultColor;
  font-size: $smallTitleSize;
  font-family: $boldFontFamily;
  line-height: calc($smallTitleSize * 1.25);
}

.attributionLogo {
  position: absolute;
  top: 20px;
  right: auto;
  bottom: auto;
  left: 20px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 100px;
  height: $attributionLogoMaxHeight;
}

.summary {
  @add-mixin tripleLineTruncate;
  margin-bottom: 12px;
  max-width: $textContentMaxWidth;
  color: $quietColor;
  text-overflow: ellipsis;
  font-size: $smallSummarySize;
}

.callToAction {
  @add-mixin truncate;
  max-width: $textContentMaxWidth;
  color: $primaryColor;
  font-size: $smallCallToActionSize;
  font-family: $boldFontFamily;
  line-height: 1.25;
}

@media (min-width: 600px) {
  .attributionLogo {
    top: 40px;
    right: auto;
    bottom: auto;
    left: 40px;
  }

  .callToAction {
    font-size: $mediumCallToActionSize;
  }

  .colorOverlay {
    display: block;
  }

  .copyOverlay {
    justify-content: flex-end;
    padding: 40px;
  }

  .summary {
    font-size: $mediumSummarySize;
  }

  .title {
    font-size: $mediumTitleSize;
    line-height: calc($mediumTitleSize * 1.25);
  }
}

@media (min-width: 900px) {
  .copyOverlay {
    justify-content: center;
    padding-top: calc(40px + $attributionLogoMaxHeight);
    padding-right: 340px;
    padding-bottom: 40px;
  }

  .callToAction {
    font-size: $largeCallToActionSize;
  }

  .summary {
    font-size: $largeSummarySize;
  }

  .title {
    font-size: $largeTitleSize;
    line-height: calc($largeTitleSize * 1.25);
  }
}
