.item {
  display: flex;
  padding: 10px;

  &:hover,
  &.isDeleteConfirmModalOpen {
    background-color: $stripeListHoverBackgroundColor;

    .deleteButton {
      opacity: 1;
    }
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.posterContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  padding: 10px;
  width: 134px;
}

.poster {
  position: relative;
  background-color: $posterBackgroundColor;
  box-shadow: 0 0 2px $shadowColor;
}

.summaryContainer {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  overflow: hidden;
  padding: 10px;
}

.titleContainer {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-size: $largeFontSize;
}

.title {
  @add-mixin truncate;
}

.subtitle {
  composes: title;

  color: $dimColor;
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $dimColor;
}

.actions {
  flex-shrink: 0;
  padding-left: 10px;
}

.deleteButton {
  padding: 0 10px;
  opacity: 0;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 1;
  }
}

.icon {
  font-size: $largeFontSize;
}
