//
// Star Rating
// --------------------------------------------------

.rating {
  display: inline-block;
  font-size: @font-size-large + 4;
  color: @white;
  cursor: pointer;
  white-space: nowrap;

  .user-select(none);

  .transition(color 0.2s);
  .clearfix();

  &.disabled {
    color: @white;
    pointer-events: none;
  }

  &.unrated {
    .star-icon {
      visibility: hidden;
    }

    .empty-icon {
      display: inline-block;
    }
  }

  .star {
    position: relative;
  }

  .star-icon {
    width: auto;
    margin-left: 2px;

    &.dislikes {
      .opacity(0.5);
    }
  }

  .empty-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    display: none;
    width: 0.2em;
    height: 0.2em;
    background-color: @gray-alt;
    border-radius: 50%;
    .translate(-50%, -50%);
  }

  .btn-link {
    color: @white;
    opacity: 0.5;

    &.user-rating {
      color: @orange-light;
      opacity: 1;

      &.disabled {
        color: @orange-light;
      }
    }

  }
}

a .rating.disabled {
  cursor: pointer;
}

.star-rating,
.heart-rating {
  &:hover,
  &:focus,
  &.active {
    color: @link-hover-color;
  }

  &.user-rating {
    color: @orange-light;
    opacity: 1;

    &.disabled {
      color: @orange-light;
    }
  }
}
