/**
 * v3 components assume a different inherited font family and smoothing
 * properties. Use this mixin to apply these assumed values to top level v3
 * components until the core styles are updated.
 */

@define-mixin resetFont {
  font-size: $defaultFontSize;
  font-family: $defaultFontFamily;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
