.stream {
  position: relative;
}

.menuItemContainer {
  composes: menuItemContainer from '~core/components/menus/SelectedMenuItem.css';

  padding-right: 40px;
}

.deleteContainer {
  @add-mixin linkOverlay;
  display: flex;
  justify-content: flex-end;
  padding: 4px 20px;

  &:hover {
    background-color: $menuItemHoverBackgroundColor;
  }
}

.deleteButton {
  composes: button from '~core/components/buttons/IconButton.css';

  &:hover {
    color: $dangerColor;
  }
}

.extendedTitle {
  display: block;
  color: $dimColor;
  font-size: $smallFontSize;
}
