.button {
  composes: button from '~core/components/buttons/IconButton.css';

  position: relative;
  display: inline-flex;
  align-items: center;
  padding: 0 10px;
  height: 100%;
  font-size: $navigationIconSize;

  &:global(.isDisabled) {
    color: $dimColor;
  }
}

.button .icon {
  top: 0;
}

.alertIcon {
  position: absolute;
  top: 34px;
  left: 0;
  color: $warningColor;
  font-size: $smallIconFontSize;
}

.tooltip {
  composes: bottomCenter from '~core/components/tooltips/Tooltip.css';

  margin-top: $tooltipMarginTop;
}

.tooltipTriggerHasFocus {
  composes: tooltip;

  margin-top: calc($tooltipMarginTop * -1);
}
