.modalContainer {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.modalBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.inputs {
  display: flex;
  flex-shrink: 0;
  margin-bottom: 20px;
  height: 40px;
}

.languageInput {
  composes: input from '~core/components/forms/SelectInput.css';

  margin-right: 20px;
  width: 220px;
}

.titleInput {
  composes: input from '~core/components/forms/TextInput.css';

  max-width: none;
}

.centeredContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}
