.addDeviceButton {
  margin-right: $liveTVSettingsDeviceGap;
  padding: 7px;
  width: calc($liveTVSettingsDeviceWidth - $liveTVSettingsDeviceGap);
  border-width: 0 0 0 $liveTVSettingsDeviceBorderSize;
  border-style: solid;
  border-color: $v4AltMedium;
  border-radius: 0;
  background-color: $v4BaseMediumLight;
  color: $v4AltMedium;
  text-align: left;
  text-transform: uppercase;
  font-size: 11px;
  font-family: $defaultFontFamily;
}

.addDeviceButtonIcon {
  margin-right: 7px;
  font-size: 19px;
}

@media only screen and (max-width: $breakpointLarge) {
  .container {
    width: 100%;
  }

  .addDeviceButton {
    margin-right: 0;
    width: 100%;
  }
}
