//
// Media Lists
// --------------------------------------------------


// Base Styles
// --------------------------------------------------

@poster-margin-bottom: 8px;
@poster-title-height-base: 20px;
@poster-title-height-large: 24px;

.poster-item {
  .media-poster,
  .media-poster-fade {
    width: 100%;
    margin-bottom: @poster-margin-bottom;
  }

  .media-heading,
  .rating {
    min-height: @poster-title-height-base;
    font-size: @font-size-small;
    line-height: @poster-title-height-base;
  }

  .media-heading {
    padding: 0 3px;
    color: @gray-lightest;
    .text-overflow();

    &.secondary {
      color: @gray-light;
    }
  }

  .unwatched-icon {
    float: right;
    margin: 6px 3px 0;
    .translate3d(0, 0, 0);
  }

  .rating.disabled {
    color: @gray-light;
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .poster-item {
    .media-heading,
    .rating {
      min-height: @poster-title-height-large;
      font-size: @font-size-base;
      min-height: @poster-title-height-large;
    }
  }
}

.unwatched-icon {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: @orange;
  border-radius: 50%;

  &.unwatched-icon-partial {
    width: 5px;
    border-radius: 5px 0 0 5px;
  }
}

.media-actions {
  display: none;
  float: right;
  font-size: 0;
}

.media-action-btn {
  .btn-white;
  padding: 0 8px;
  font-size: @font-size-base;
  .transition(color 0.2s);
}

.media-actions-dropdown {
  position: absolute;
  z-index: 1;
}


// Media List
// --------------------------------------------------

@list-item-height: 34px;

.media-list {
  margin: -25px;
  .user-select(none);

  &.show-context .media-context {
    display: block;
  }
}

.disc-number {
  padding: 16px;
  color: @gray-light;
}

.media-list-item {
  position: relative;
  width: 100%;
  height: @list-item-height;

  &.media-list-item-2x {
    height: @list-item-height * 2;
  }

  &.on-top {
    z-index: 1;
  }

  &.show-sort .media-sort {
    display: block;
  }

  &.show-subtitle .media-heading {
    max-width: 35%;

    &.media-context,
    &.media-sort {
      max-width: 20%;
    }
  }

  > a,
  > .media-list-inner-item {
    // The media-heading class needs a height on its parent since it has a
    // display of inline-block with overflow: hidden.
    height: 100%;
    padding: 5px 20px 10px;
  }

  .media-heading {
    display: inline-block;
    max-width: 60%;
    margin-right: 5px;
    vertical-align: middle;
    .text-overflow();

    &.secondary {
      color: @gray-alt;
      max-width: 20%;
    }
  }

  .media-context,
  .media-sort {
    display: none;
    float: right;
    max-width: 20%;
    margin-right: 0;
    margin-left: 5px;
  }

  .media-index {
    display: inline-block;
    width: 35px;
    margin-right: 10px;
    font-size: @font-size-small;
    line-height: @list-item-height - 10;
    text-align: right;
    vertical-align: top;

    i {
      display: none;
      color: @gray-lighter;
    }

    &.date {
      width: 50px;
    }
  }

  .media-action-btn {
    line-height: @list-item-height - 8;
  }

  .media-actions-dropdown {
    top: 0;
    right: 0;
  }

  .rating {
    font-size: @font-size-base;

    &.disabled {
      color: @gray-alt;
    }
  }
}

.media-list-item > .show-actions:hover,
.media-list-item > .show-actions:active,
.media-list-item.force-hover > .show-actions {
  .media-actions {
    display: block;
  }

  .media-context,
  .media-sort {
    display: none;
  }
}
.hide-actions {
  .media-list-item > .show-actions:hover,
  .media-list-item > .show-actions:active,
  .media-list-item.force-hover > .show-actions {
    .media-actions {
      display: none;
    }

    .media-context,
    .media-sort {
      display: block;
    }
  }
}


@details-list-padding: 10px;


// Media Tile List
// --------------------------------------------------

.media-tile-list {
  margin: 0 -24px -24px 0;
  .user-select(none);

  &.show-context .media-context {
    display: block;
  }

  &.virtual-list {
    > .movie,
    > .show,
    > .season {
      height: @poster-tile-list-standard-height-base + @poster-margin-bottom + (@poster-title-height-base * 2);

      &.show-sort {
        height: @poster-tile-list-standard-height-base + @poster-margin-bottom + (@poster-title-height-base * 3);
      }
    }

    > .episode {
      height: @poster-tile-list-standard-height-base + @poster-margin-bottom + (@poster-title-height-base * 3);

      &.show-sort {
        height: @poster-tile-list-standard-height-base + @poster-margin-bottom + (@poster-title-height-base * 4);
      }
    }

    > .artist,
    > .album,
    > .track,
    > .photo,
    > .photoalbum,
    > .channel,
    > .channelDirectory,
    > .device {
      height: @poster-tile-list-square-height-base + @poster-margin-bottom + (@poster-title-height-base * 2);

      &.show-sort {
        height: @poster-tile-list-square-height-base + @poster-margin-bottom + (@poster-title-height-base * 3);
      }
    }

    > .clip {
      height: @poster-tile-list-widescreen-height-base + @poster-margin-bottom + (@poster-title-height-base * 2);

      &.show-sort {
        height: @poster-tile-list-widescreen-height-base + @poster-margin-bottom + (@poster-title-height-base * 3);
      }
    }
  }
}

.media-tile-list-item {
  position: relative;
  width: @poster-tile-list-standard-width-base;
  margin: 0 24px 24px 0;

  &.show-sort .media-sort {
    display: block;
  }

  &.movie,
  &.show,
  &.season,
  &.episode,
  &.collection {
    .media-poster {
      height: @poster-tile-list-standard-height-base;
    }
  }

  &.artist,
  &.album,
  &.track,
  &.photo,
  &.photoalbum,
  &.playlist,
  &.channel,
  &.channelDirectory,
  &.device {
    .media-poster {
      height: @poster-tile-list-square-height-base;
    }
  }

  &.clip {
    width: @poster-tile-list-widescreen-width-base;

    .media-poster {
      height: @poster-tile-list-widescreen-height-base;
    }
  }

  .media-context,
  .media-sort {
    display: none;
  }

  &.on-top {
    z-index: 1;
  }
}

.container .media-tile-list {
  margin-right: -30px;
}

.container .media-tile-list-item {
  margin-right: 30px;
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: @screen-md) {
  .container .media-tile-list {
    margin-right: -44px;
  }

  .container .media-tile-list-item {
    margin-right: 44px;

    &.clip {
      margin-right: 46px;
    }
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .media-tile-list {
    margin: 0 -32px -32px 0;

    &.virtual-list {
      > .movie,
      > .show,
      > .season {
        height: @poster-tile-list-standard-height-large + @poster-margin-bottom + (@poster-title-height-large * 2);

        &.show-sort {
          height: @poster-tile-list-standard-height-large + @poster-margin-bottom + (@poster-title-height-large * 3);
        }
      }

      > .episode {
        height: @poster-tile-list-standard-height-large + @poster-margin-bottom + (@poster-title-height-large * 3);

        &.show-sort {
          height: @poster-tile-list-standard-height-large + @poster-margin-bottom + (@poster-title-height-large * 4);
        }
      }

      > .artist,
      > .album,
      > .track,
      > .photo,
      > .photoalbum,
      > .playlist,
      > .channel,
      > .channelDirectory,
      > .device {
        height: @poster-tile-list-square-height-large + @poster-margin-bottom + (@poster-title-height-large * 2);

        &.show-sort {
          height: @poster-tile-list-square-height-large + @poster-margin-bottom + (@poster-title-height-large * 3);
        }
      }

      > .clip {
        height: @poster-tile-list-widescreen-height-large + @poster-margin-bottom + (@poster-title-height-large * 2);

        &.show-sort {
          height: @poster-tile-list-widescreen-height-large + @poster-margin-bottom + (@poster-title-height-large * 3);
        }
      }
    }
  }

  .media-tile-list-item {
    width: @poster-tile-list-standard-width-large;
    margin: 0 32px 32px 0;

    &.movie,
    &.show,
    &.season,
    &.episode,
    &.collection {
      .media-poster {
        height: @poster-tile-list-standard-height-large;
      }
    }

    &.artist,
    &.album,
    &.track,
    &.photo,
    &.photoalbum,
    &.playlist,
    &.channel,
    &.channelDirectory,
    &.device {
      .media-poster {
        height: @poster-tile-list-square-height-large;
      }
    }

    &.clip {
      width: @poster-tile-list-widescreen-width-large;

      .media-poster {
        height: @poster-tile-list-widescreen-height-large;
      }
    }
  }

  .container .media-tile-list {
    margin: 0 -48px -32px 0;
  }

  .container .media-tile-list-item {
    margin: 0 48px 32px 0;

    &.clip {
      margin-right: 47px;
    }
  }
}


// Horizontal Media Tile List
// --------------------------------------------------

.horizontal-media-tile-list {
  margin: 0 -20px -34px 0;
}

.horizontal-media-tile-list > .media-tile-list-item {
  margin: 0 20px 34px 0;

  &.clip {
    margin: 0 20px 34px 0;
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .horizontal-media-tile-list {
    margin: 0 -24px -48px 0;
  }

  .horizontal-media-tile-list > .media-tile-list-item {
    margin: 0 24px 48px 0;

    &.clip {
      margin: 0 24px 48px 0;
    }
  }
}


// Episode List
// --------------------------------------------------

.episode-list {
  > a,
  > .episode-list-inner-item {
    padding-left: 0;
  }
}


// Episode Details List
// --------------------------------------------------

.episode-details-list-item.episode {
   height: @poster-details-list-widescreen-height + @details-list-padding;

  .media-poster {
    width: @poster-details-list-widescreen-width;
  }
}


// Episode Tile List
// --------------------------------------------------

.episode-tile-list > .media-tile-list-item {
  width: @poster-tile-list-widescreen-width-base;

  .media-poster {
    height: @poster-tile-list-widescreen-height-base;
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .episode-tile-list > .media-tile-list-item {
    width: @poster-tile-list-widescreen-width-large;

    .media-poster {
      height: @poster-tile-list-widescreen-height-large;
    }
  }
}


// Album Tile List
// --------------------------------------------------

.album-tile-list > .media-tile-list-item {
  width: @poster-album-width-base;

  .media-poster {
    height: @poster-album-height-base;
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .album-tile-list > .media-tile-list-item {
    width: @poster-album-width-large;

    .media-poster {
      height: @poster-album-height-large;
    }
  }
}


// Album List
// --------------------------------------------------

@album-list-padding: 40px;

.album-list {
  margin-bottom: -@album-list-padding;
}

.album-list-item {
  margin-bottom: @album-list-padding;
  .clearfix();

  .media-poster-container {
    float: left;
    width: @poster-album-width-base;
  }

  .media-poster {
    height: @poster-album-height-base;
  }

  .track-list {
    margin-left: @poster-album-width-base + @album-list-padding;
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .album-list-item {
    .media-poster-container {
      width: @poster-album-width-large;
    }

    .media-poster {
      height: @poster-album-height-large;
    }

    .track-list {
      margin-left: @poster-album-width-large + @album-list-padding;
    }
  }
}


// Track List
// --------------------------------------------------

.track-list {
  margin: 0;
}

.track-list-item {
  &.current-track {
    .track-index {
      span {
        display: none;
      }
      i {
        display: inline-block;
      }
    }
  }

  .track-music-video-btn {
    position: absolute;
    right: 80px;
    color: @gray;
  }

  > .show-actions:hover,
  > .show-actions:active,
  &.force-hover > .show-actions {
    .track-music-video-btn {
      color: @white;
    }
  }

  > .show-actions .track-music-video-btn:hover,
  > .show-actions .track-music-video-btn:focus {
    color: @link-hover-color;
  }
}

.striped-list-item > .track-list-item {
  padding: 0;

  > a {
    padding-left: 3px;

    &.selected {
      padding-left: 0;
    }
  }
}

// Playlist Items
// --------------------------------------------------

.playlist-items {
  margin: 0 0;
  max-width: 1280px;

  counter-reset: playlist-index;
}

.playlist-item {
  height: auto;

  > a {
    padding: 0;
  }

  .playlist-inner-item {
    height: 60px;
    line-height: 60px;

    &.track {
      height: 40px;
      line-height: 40px;
    }
  }

  .media-poster-container {
    float: left;
    height: 60px;
    padding: 1px 0px 1px 5px;
  }

  .media-poster {
    display: inline-block;
    width: @poster-playlist-item-video-width;
    height: @poster-playlist-item-video-height;
  }

  .photo .media-poster {
    width: @poster-playlist-item-photo-width;
    height: @poster-playlist-item-photo-height;
  }

  .media-item {
    margin-left: 40px + 20px + @poster-playlist-item-video-width;
    margin-right: 10px;

    &.track {
      margin-left: 40px;
    }

    &.photo {
      margin-left: 40px + 20px + @poster-playlist-item-photo-width;
    }
  }

  // Compensate for a button on either side
  .media-item.has-actions {
    margin-left: 80px + 20px + @poster-playlist-item-video-width;
    margin-right: 40px;

    &.track {
      margin-left: 80px;
    }

    &.photo {
      margin-left: 80px + 20px + @poster-playlist-item-photo-width;
    }
  }

  .media-detail, .media-duration, .media-index {
    display: inline-block;
    vertical-align: middle;
    font-size: @font-size-base;
    .text-overflow();

    &.secondary {
      max-width: none;
    }
  }

  .media-index {
    &:before {
      counter-increment: playlist-index;
      content: counter(playlist-index);
    }
    width: 30px;
    line-height: inherit;
    text-align: right;
    float: left;
    font-size: @font-size-small;
  }

  .media-details {
    position: relative;
    font-size: 0; // Keeps inline-blocks flush

    .media-detail {
      padding-right: 10px;
      width: 28%;
    }

    .media-duration {
      width: 16%;
      right: 0;
      text-align: right;
    }

    .media-heading {
      margin: 0;
    }
  }

  .movie,
  .photo .media-details {
    .media-detail {
      width: 50%;
    }

    .media-optional {
      width: 0;
      padding: 0;
    }

    .media-always {
      width: 25%;
    }

    .media-duration {
      width: 25%;
    }
  }

  .media-reorder {
    float: left;
  }

  .media-actions {
    text-align: center;
    display: inline-block;
    width: 40px;
    height: 100%;
    font-size: 16px;
  }

  // Only show the buttons when hovering over the whole list item
  .media-action-btn {
    display: none;
  }

  &:hover {
    .media-action-btn {
      display: inline-block;
    }
  }

  .sortable-handle {
    display: none;
  }

  &:hover {
    .sortable-handle {
      display: inline-block;
      &:hover {
        .cursor(grab);
      }
    }
  }

  &.current-track {
    .media-index {
      &:before {
        display: block;
        visibility: hidden;
        height: 0;
        width: 0;
      }
      i {
        display: inline-block;
      }
    }
  }
}

// Version Generator Items
// --------------------------------------------------

.version-generator-item {
  @poster-generator-height: 60px;
  @poster-generator-movie-width: 40px;
  @poster-generator-track-width: 60px;

  .playlist-inner-item.track {
    height: @poster-generator-height;
    line-height: @poster-generator-height;

    .media-poster {
      width: @poster-generator-track-width;
      margin-left: 0;
    }
  }

  .media-poster {
    width: @poster-generator-movie-width;
    margin-left: (@poster-generator-track-width - @poster-generator-movie-width) / 2;
  }

  .media-item {
    margin-left: 115px;
    margin-right: 120px;
  }

  .generator-poster-container {
    float: left;
    height: @poster-generator-height;
    padding: 1px 0px 1px 5px;
  }

  .generator-actions {
    width: 120px;
    padding-right: 10px;
    text-align: right;
  }

  .col-sm,
  .col-md,
  .col-lg {
    display: inline-block;
    padding-right: 10px;
    vertical-align: middle;
    font-size: @font-size-base;
    .text-overflow();
  }

  .col-sm {
    width: 20%;
  }

  .col-md {
    width: 30%;
  }

  .col-lg {
    width: 40%;
  }

  .col-secondary {
    color: @gray-light;
  }

  .generator-status-container {
    display: none;
  }

  .generator-status-icon {
    margin-right: 5px;
  }

  .generator-status-converting {
    vertical-align: middle;
    line-height: 0;
  }

  // States

  .generator-status.complete > .generator-status-complete {
    display: inline-block;
  }

  .generator-status.failed > .generator-status-failed {
    display: inline-block;
  }

  .generator-status.converting > .generator-status-converting {
    display: inline-block;
  }

  .generator-status.converting {
    > .generator-status-converting {
      display: inline-block;
    }

    .generator-status-icon {
      color: @orange-light;
    }
  }

  .generator-status.pending {
    > .generator-status-pending {
      display: inline-block;
    }

    .generator-status-icon {
      color: @gray;
    }
  }
}


// Version Generator Metadata Items
// --------------------------------------------------

.version-generator-metadata-list {
  margin: 0 -20px;
}

.version-generator-metadata-list-item {
  @poster-generator-width: 40px;
  @poster-generator-height: 60px;

  .striped-list-inner-item {
    .clearfix();
  }

  .media-poster {
    float: left;
    width: @poster-generator-width;
    height: @poster-generator-height;
    margin-right: 20px;
  }

  .generator-metadata-title {
    margin-top: 4px;
    font-size: @font-size-large;
    .text-overflow();
  }

  .generator-metadata-subtitle {
    color: @gray-light;
    .text-overflow();
  }

  .conversion-status {
    float: right;
    margin: 4px 0 0 10px;
  }
}

.conversion-status {
  .conversion-status-container {
    display: none;
  }

  .conversion-status-icon {
    margin-right: 5px;
  }

  .conversion-status-converting {
    vertical-align: middle;
    line-height: 0;
  }

  .conversion-status-waiting-count {
    margin-left: 5px;
    color: @gray;
  }

  .progress-bar-region {
    display: inline-block;

    .circle-progress {
      background-color: rgba(0, 0, 0, 0.3);
    }
  }

  .conversion-status-converting-label-container {
    display: inline-block;
    margin-left: 10px;
    line-height: 1.5;
  }

  .conversion-status-converting-progress {
    color: @gray-alt;
  }

  // States

  &.processed > .conversion-status-processed {
    display: inline-block;
  }

  &.disabled > .conversion-status-disabled {
    display: inline-block;
  }

  &.error > .conversion-status-error {
    display: inline-block;
  }

  &.converting > .conversion-status-converting {
    display: inline-block;
  }

  &.pending {
    > .conversion-status-pending {
      display: inline-block;
    }

    .conversion-status-icon {
      color: @gray;
    }
  }
}


// Photo Tile List
// --------------------------------------------------

.photo-tile-list {
  margin: 0 -10px -10px 0;
}

.photo-tile-list > .media-tile-list-item {
  width: @poster-photo-tile-list-width-base;
  margin: 0 10px 10px 0;

  .media-poster {
    height: @poster-photo-tile-list-height-base;
  }
}


// Channel Directory Tile List
// --------------------------------------------------

.channel-directory-tile-list-item {
  .installed-icon {
    position: absolute;
    right: 10px;
    bottom: 0;
    font-size: 24px;
    color: @orange-light;
    text-shadow: 0 0 1px @black, 0 0 1px @black, 0 1px 1px @black;
  }
}

.container .channel-directory-tile-list,
.container .sync-tile-list {
  margin-right: -24px;
}

.container .channel-directory-tile-list-item,
.container .sync-tile-list-item {
  margin-right: 24px;
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .container .channel-directory-tile-list,
  .container .sync-tile-list  {
    margin-right: -24px;
  }

  .directory-container .channel-directory-tile-list-item,
  .container .sync-tile-list-item {
    margin-right: 24px;
  }
}


// Sync Tile List
// --------------------------------------------------

.sync-tile-list-item {
  height: 248px;
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .container .sync-tile-list-item {
    height: 300px;
  }
}


// Session List
// --------------------------------------------------

.session-tile-list {
  margin: 0 -30px -12px 0;
}

.session-tile-list-item {
  margin: 0 30px 12px 0;

  .media-poster-btn {
    float: left;

    .glyphicon {
      font-size: 18px;
      vertical-align: middle;
    }

    .media-poster-btn-text {
      font-size: @font-size-small;
      margin-left: 5px;
    }
  }

  .media-poster-container:hover {
    .device-icon,
    .session-state {
      display: inline-block;
    }

    .session-transcode-details {
      display: block;
    }

    .session-player,
    .session-state {
      right: auto;
      left: @poster-session-user-width-small + 20;
    }

    .session-player {
      .opacity(1);
    }
  }

  .media-poster-container {
    display: inline-block;
    width: @poster-session-tile-list-width-small;
  }

  .media-poster,
  .media-poster-fade {
    width: @poster-session-tile-list-width-small;
    height: @poster-session-tile-list-height-small;
  }

  //all children need to be above the fade image
  .media-poster * {
    z-index: 2;
  }

  .media-poster-fade {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    .transition(opacity 0.8s);

    &.invisible {
      .opacity(0);
    }
  }

  .media-title {
    max-width: 80%;
  }

  .media-subtitle-1 {
    max-width: 50%;
  }

  .device-icon {
    position: absolute;
    top: 10px;
    left: 10px;
    width: @poster-session-user-width-small;
    height: @poster-session-user-height-small;
    border-radius: @border-radius-base;
    background-size: @poster-session-user-width-small @poster-session-user-height-small;
  }

  .session-player,
  .session-state {
    position: absolute;
    top: 10px;
    left: 10px;
    display: inline-block;
    max-width: 50%;
    font-size: @font-size-small;
    color: @white;
    .text-overflow();
    .opacity(0.7);
  }

  .session-state {
    top: 32px;
  }

  .session-transcode-details {
    position: absolute;
    top: @poster-session-user-height-small + 30px;
    left: 10px;
    color: @white;
    font-size: @font-size-small;

    p {
      margin-bottom: 2px;
    }
  }

  .session-transcode-throttled,
  .session-transcode-label {
    .opacity(0.7);
  }

  .session-transcode-label {
    margin-right: 5px;
  }

  .session-user {
    position: relative;
  }

  .session-username {
    position: absolute;
    right: @poster-session-user-width-small + 10;
    bottom: 0;
    max-width: 60px;
    .text-overflow();
    .backface-visibility(hidden); // Prevent flicker during transition.
  }

  .user-poster {
    float: right;
    width: @poster-session-user-width-small;
    height: @poster-session-user-height-small;
    background-size: @poster-session-user-width-small @poster-session-user-height-small;
  }

  .device-icon,
  .session-state,
  .session-transcode-details {
    display: none;
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .session-tile-list {
    margin: 0 -45px -32px 0;

    .media-subtitle-1 {
      max-width: 50%;
    }
  }

  .session-tile-list-item {
    margin: 0 45px 32px 0;

    .media-poster-container:hover {
      .session-player,
      .session-state {
        left: @poster-session-user-width-base + 20;
      }
    }

    .media-poster-container {
      width: @poster-session-tile-list-width-large;
    }

    .media-poster-btn {
      .glyphicon {
        font-size: 24px;
        vertical-align: text-top;
      }

      .media-poster-btn-text {
        font-size: @font-size-base;
        margin-left: 5px;
      }
    }

    .media-poster,
    .media-poster-fade {
      width: @poster-session-tile-list-width-large;
      height: @poster-session-tile-list-height-large;
    }

    .session-player,
    .session-state {
      font-size: @font-size-base;
    }

    .session-username {
      max-width: 120px;
    }

    .session-transcode-details {
      font-size: @font-size-base;
    }

    .device-icon,
    .user-poster {
      width: @poster-session-user-width-base;
      height: @poster-session-user-height-base;
      background-size: @poster-session-user-width-base @poster-session-user-height-base;
    }
  }
}
