.form {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.channelMappingsBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.programGuideLabel {
  margin-bottom: 5px;
  color: $dimColor;
  text-transform: uppercase;
}

.programGuideSelectContainer {
  margin-bottom: 30px;
  width: 250px;
}

.programGuideTitle {
  margin-bottom: 30px;
  font-size: $largeFontSize;
}

.centeredContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
}

.scroller {
  composes: vertical from '~core/components/base/Scroller.css';

  flex-grow: 1;
}

.channelMappingsContainer {
  padding: 30px;
}
