.listItem {
  display: inline-block;
  margin-bottom: 40px;
  width: 100%;
  cursor: default;
}

.mediaInformationContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: 104px;
  /*
    This prevents the flickering in safari by forcing hardware acceleration.
    See: https://github.com/plexinc/plex-web-client/issues/13746
  */
  /* stylelint-disable property-no-vendor-prefix */
  -webkit-transform: translate3d(0, 0, 0);
  /* stylelint-enable */
  transform-style: preserve-3d;
}

.posterContainer {
  position: relative;
  flex-shrink: 0;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  /*
    This prevents the flickering in safari by forcing hardware acceleration.
    We also make sure to preserve z-layer ordering.
    See: https://github.com/plexinc/plex-web-client/issues/13746
  */
  /* stylelint-disable property-no-vendor-prefix */
  -webkit-transform: translate3d(0, 0, -1px);
  /* stylelint-enable */
}

.attribution {
  position: absolute;
  bottom: 4px;
  padding: 4px;
  width: 28px;
  height: 28px;
}

.infoContainer {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.2);
  /*
    This prevents the flickering in safari by forcing hardware acceleration.
    We also make sure to preserve z-layer ordering.
    See: https://github.com/plexinc/plex-web-client/issues/13746
  */
  /* stylelint-disable property-no-vendor-prefix */
  -webkit-transform: translate3d(0, 0, -1px);
  /* stylelint-enable */
}

.previewThumbnailContainer {
  height: 100%;
}

.metadataText {
  padding: 0 22px;
}

.visibleProgressBar {
  padding-bottom: 4px;
}

.title {
  @add-mixin truncate;
  color: $defaultColor;
}

.subtitle {
  @add-mixin truncate;
  color: $dimColor;
}

.progressTrack {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 4px;
  background-color: #000;
  /*
    This prevents the flickering in safari by forcing hardware acceleration.
    We also make sure to preserve z-layer ordering.
    See: https://github.com/plexinc/plex-web-client/issues/13746
  */
  /* stylelint-disable property-no-vendor-prefix */
  -webkit-transform: translate3d(0, 0, -1px);
  /* stylelint-enable */
}

.progressPercent {
  position: absolute;
  height: 100%;
}

.playProgressPercent {
  composes: progressPercent;

  background-color: $primaryColor;
}

.transcodeProgressPercent {
  composes: progressPercent;

  background-color: $seekBarBufferColor;
}

.detailsContainer {
  overflow: hidden;
  max-height: 0;
  transition: max-height $slowSpeed ease-out;
}

.detailsContainerExpanded {
  max-height: 230px;
}

.userContainer {
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  height: 60px;
  background-color: $blackDarkerTranslucentBackgroundColor;

  &:hover {
    .stopButton {
      transform: translateY(0);
    }
  }
}

.stopButton {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  padding: 0 20px;
  width: 100%;
  height: 100%;
  background-color: $sessionStopBackgroundColor;
  color: $defaultColor;
  transition: transform $defaultSpeed;
  transform: translateY(60px);

  /*
    When focusing on the button via tabbing remove the transition time to
    avoid a janky animation, and specify a transform to ensure the button
    transitions back when focus is removed.
    See: https://github.com/plexinc/plex-web-client/issues/13137
  */
  &:global(.isFocused) {
    transition: transform 0s;
    transform: translateY(0);
  }
}

.stopIcon {
  top: 0;
  margin-right: 20px;
  color: $sessionStopIconColor;
  font-size: 20px;
}

.avatarImg {
  composes: avatar from '~desktop/common/js/views/v3/components/core/images/AvatarImg.css';

  flex-shrink: 0;
  margin-left: 10px;
  width: 40px;
  height: 40px;
}

.username {
  @add-mixin truncate;
  padding: 10px;
  color: $dimColor;
}
