.cell {
  position: relative;
  display: flex;
  flex-shrink: 0;
  scroll-snap-align: start;
  padding: 1px 0;
  width: 100%;
  height: $v4SpotlightHeight;
}

.hub {
  composes: hub from '~desktop/common/js/v4/core/ui/hubs/components/VirtualHubScroller.css';

  position: relative;
  overflow: hidden;
  margin-top: $v4ListSpacing;
  height: $v4SpotlightHeight;

  &:first-of-type {
    margin-top: 5px;
  }
}

.hubScroller {
  composes: hubScroller from '~desktop/common/js/v4/core/ui/hubs/components/VirtualHubScroller.css';

  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  scroll-snap-type: x mandatory;
}

.hubScrollerControlledScroll {
  composes: hubScroller;

  pointer-events: none;
}

.dots {
  position: absolute;
  bottom: 10px;
  left: 50%;
  display: flex;
  transform: translateX(-50%);
}
