.sidebarMenuButton {
  display: block;
  flex-shrink: 0;
  padding: 0 25px;
  width: 100%;
  height: 60px;
  background-color: $sidebarMenuButtonBackgroundColor;
  font-size: $largeFontSize;
  font-family: $boldFontFamily;

  &:hover {
    background-color: $sidebarMenuButtonHoverBackgroundColor;
    color: $defaultLinkHoverColor;
  }

  &:global(.isActive) {
    background-color: $sidebarMenuButtonActiveBackgroundColor;
  }
}

/* See: https://bugzilla.mozilla.org/show_bug.cgi?id=984869 */
.sidebarMenuButtonContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.sidebarMenuSpinner {
  border-top-color: $selectedColor;
  border-left-color: $selectedColor;
}

.sidebarMenuTitle {
  flex-grow: 1;
  max-width: 85%;
  text-align: left;
  line-height: 1.5;
}

.sidebarMenuPrimaryTitleContainer {
  display: block;
}

.sidebarMenuPrimaryTitle {
  @add-mixin truncate;
  display: inline-block;
  max-width: 85%;
  vertical-align: top;
}

.sidebarMenuSecondaryTitle {
  @add-mixin truncate;
  display: block;
  color: $defaultLinkColor;
  font-size: $defaultFontSize;
}

.icon {
  top: 0;
}

/* States */

.isDisabled {
  .sidebarMenuPrimaryTitle {
    max-width: 100%;
  }
}
