.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 370px;
  background-color: $blackDarkTranslucentBackgroundColor;
}

.titleBar {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 40px 10px 30px;
  width: 100%;
  height: 40px;
  background-color: rgba(0, 0, 0, 0.45);
  color: rgba(255, 255, 255, 0.45);
  font-size: 13px;
  font-family: $defaultFontFamily;
}

.titleText {
  @add-mixin truncate;
  max-width: 100%;
}

.scroller {
  composes: vertical from '~core/components/base/Scroller.css';

  height: 100%;
}
