.input {
  composes: input from './input.css';

  background-color: $inputBackgroundColor;
  color: $inputColor;
  transition: background-color $defaultSpeed;

  &:focus {
    outline: 0;
    background-color: $inputFocusBackgroundColor;
    color: $inputFocusColor;
  }

  &::placeholder {
    color: $inputPlaceholderColor;
  }
}

.small {
  composes: small from './input.css';

  padding: 2px 10px;
}

.large {
  composes: large from './input.css';

  padding: 5px 10px;
}

.isDisabled {
  composes: isDisabled from './input.css';
}

.hasError {
  composes: hasError from './input.css';
}
