.card {
  composes: card from './WatchTogetherHubItemPosterCard.css';

  margin-bottom: $v4CardTitleGap;
}

.splitTitleContainer {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.title {
  composes: singleLineTitle from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardTitle.css';
}

.secondaryTitle {
  composes: title;
  composes: isSecondary from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardTitle.css';
}

.splitLineTitle {
  composes: title;

  max-width: 70%;
}

.splitLineSecondaryTitle {
  composes: secondaryTitle;
  composes: splitLineTitle;
}
