//
// Dashboard
// --------------------------------------------------

.dashboard-container {
  padding: 0 15px;
  clear: both;
}

.dashboard-list-container {
  position: relative;
  .translate(0, 0);
  .transition(all 0.2s);
}

.dashboard-empty-servers-message {
  margin-top: 40px;
  text-align: center;

  > p {
    margin-top: 20px;
    text-align: left;
  }
}

.empty-servers-container {
  text-align: center;

  p {
    color: @gray-light;
    max-width: 600px;
    margin: 0 auto 20px;
  }

  button {
    margin: 10px 0;
  }
}

// Dropdowns should render above containers below them.
.priority-1-container { position: relative; z-index: 4; }
.priority-2-container { position: relative; z-index: 3; }
.priority-3-container { position: relative; z-index: 2; }
.priority-4-container { position: relative; z-index: 1; }
.priority-5-container { position: relative; z-index: 0; }
