.button {
  composes: button from './SourceSidebarLink.css';

  position: relative;

  &:hover {
    .iconImg {
      opacity: 1;
    }
  }
}

.isActive {
  composes: isActive from './SourceSidebarLink.css';

  .overflowButton {
    color: #fff;
  }

  .iconImg {
    color: #fff;
    opacity: 1;
  }
}

.isSelected {
  composes: isSelected from './SourceSidebarLink.css';

  .iconImg {
    opacity: 1;
  }
}

.iconImg {
  position: relative;
  top: -2px;
  width: 18px;
  height: $sourceSidebarIconSize;
  opacity: 0.6;
}

.actionContainer {
  position: relative;
  display: inline-flex;
  align-items: stretch;
  justify-content: center;
  flex-shrink: 0;

  &:empty {
    padding-right: 10px;
  }
}

.pinnedIcon {
  top: 50%;
  margin: 0 10px;
  color: $dimColor;
  font-size: 14px;
  transform: translateY(-50%);
  pointer-events: none;
}

.overflowButton {
  padding: 0 10px;
}

.hiddenOverflowButton {
  visibility: hidden;
  width: 0;
}
