.container {
  position: relative;
  width: 100%;
}

.input {
  margin: 0 5px;
  width: 100%;
  outline: none;
  border: 0;
  background: transparent;
  color: $defaultColor;
}

.suggestionsContainer {
  composes: scroller from '~core/components/base/Scroller.css';
  composes: vertical from '~core/components/base/Scroller.css';
  composes: menu from '~core/components/menus/Menu.css';

  z-index: $menuZIndex;
  display: none;
  margin-top: 4px;
  min-height: 40px;
}

.suggestionsContainerOpen {
  display: block;
}

.suggestionsList {
  margin: 0;
  padding: 0;
  list-style: none;
}

.suggestion {
  composes: menuItem from '~core/components/menus/MenuItem.css';

  color: $quietColor;
  cursor: pointer;
}

.suggestionHighlighted {
  border-radius: 0 $autosuggestSuggestionRadius $autosuggestSuggestionRadius 0;
  background-color: $menuItemActiveBackgroundColor;
  color: $defaultColor;
}
