// Progress Bar
// --------------------------------------------------

.media-progress {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4px;

  .media-progress-pct {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    background-color: @orange;
  }

  .media-progress-track {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 100%;
    background-color: @black;
    background-color: rgba(0, 0, 0, 0.8);
  }
}
