.dayOfWeek {
  @add-mixin truncate;
  flex: 1 1 100%;
  padding: 0 2px;
  min-width: 120px;
  background-color: $dayOfWeekBackgroundColor;
  background-clip: content-box;
  color: $dayOfWeekColor;
  text-align: center;
  line-height: 40px;
}

.isToday {
  background-color: $todayBackgroundColor;
  color: $defaultColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
}
