.item {
  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.columns {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  display: flex;
  justify-content: flex-start;
  color: $dimColor;
  line-height: $tableRowHeight;

  &:hover {
    /* Override the link hover color */
    color: $dimColor;
  }
}
