.measureContainer {
  position: relative;
  max-height: inherit;
}

.menuScroller {
  composes: scroller from '~desktop/common/js/v4/core/ui/scrollers/components/Scroller.css';

  padding-right: 0;
  max-height: inherit;
}

.headerContainer {
  flex-grow: 0;
  flex-shrink: 0;
}

.header {
  padding: 2px 12px 6px;
}

.filterOptionsContainer {
  display: flex;
  flex-direction: column;
}

.headerTitle {
  display: flex;
  flex-direction: column;
}

.filterOptionsScroller {
  composes: menuScroller;

  flex-grow: 1;
}

.backIcon {
  font-size: $defaultIconFontSize;
}

.filterText {
  @add-mixin truncate;
  margin-left: 8px;
  color: $headerColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
}

.searchInput {
  composes: input from '~core/components/forms/TextInput.css';

  margin-top: 8px;
  padding-right: 22px;
  padding-left: 22px;
  background-color: rgba(255, 255, 255, 0.07);

  &:not(:focus)::placeholder {
    color: rgba(255, 255, 255, 0.3);
  }
}

.selectedMenuItem {
  composes: menuItemContainer from '~core/components/menus/SelectedMenuItem.css';

  height: auto;
}

.emptyText {
  padding: 20px 10px;
  color: $dimColor;
  text-align: center;
}
