$iconMarginLeft: 10px;

.serverButton {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  padding: 0 20px 0 30px;
  width: 100%;
  height: 60px;

  &:hover {
    background-color: rgba(0, 0, 0, 0.45);
  }
}

.isSelected {
  background-color: $blackDarkTranslucentBackgroundColor;

  .friendlyName,
  .serverMenuIcon {
    color: $selectedColor;
  }

  .titles {
    max-width: calc(100% - ($iconMarginLeft + $defaultIconFontSize));
  }
}

.isUnavailable {
  .friendlyName {
    color: $dimColor;
  }
}

.titles {
  display: inline-flex;
  justify-content: center;
  flex-direction: column;
  max-width: 100%;
}

.title {
  @add-mixin truncate;
  color: $defaultColor;
  font-size: 16px;
  font-family: $boldFontFamily;
}

.details {
  @add-mixin truncate;
  color: rgba(255, 255, 255, 0.45);
  font-size: 13px;
  font-family: $defaultFontFamily;
}

.serverMenuIcon {
  top: 0;
  flex-shrink: 0;
  margin-left: $iconMarginLeft;
  font-size: $defaultIconFontSize;
}
