.poster {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  position: relative;
  flex-shrink: 0;
  width: 53px;
  height: 100%;
  cursor: pointer;
}

.artistPoster {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  position: relative;
  flex-shrink: 0;
  overflow: hidden;
  margin: 10px 0 10px 10px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  /* See: https://github.com/plexinc/plex-web-client/issues/14449 */
  transform: translateZ(0);
}

.summary {
  @add-mixin truncate;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  color: $dimColor;
  font-size: $smallFontSize;
}

.itemTitle {
  @add-mixin truncate;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  color: $defaultColor;
}
