.slider {
  position: relative;
  display: inline-block;
  min-width: $sliderThumbSize;
  min-height: $sliderThumbSize;
  width: $sliderTrackSize;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;

  &:hover:not(.isDisabled) .thumb,
  &.isDragging .thumb {
    background-color: $sliderThumbHoverBackgroundColor;
    opacity: 1;
  }
}

.hasAutoHideThumb .thumb {
  opacity: 0;
  pointer-events: none;
}

.track {
  @add-mixin absoluteCenter;

  overflow: hidden;
  width: 100%;
  height: 2px;
  background-color: $sliderTrackBackgroundColor;
}

.fill {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: $primaryColor;
  transition: transform $fastSpeed;
  transform-origin: center left;
}

.secondaryFill {
  composes: fill;

  background-color: rgba(204, 123, 25, 0.4);
}

.thumbTrack {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  pointer-events: none;
}

.thumb {
  composes: link from '~core/components/buttons/Link.css';

  margin-left: calc($sliderThumbSize / -2);
  width: $sliderThumbSize;
  height: $sliderThumbSize;
  border-radius: 50%;
  background-color: $sliderThumbBackgroundColor;
  box-shadow: 0 0 2px $shadowColor;
  transition: left $fastSpeed, top $fastSpeed, opacity $defaultSpeed,
    background-color $defaultSpeed;
  pointer-events: auto;
}

.isDisabled {
  pointer-events: none;
}

.isDragging {
  cursor: pointer !important;

  .fill,
  .thumb {
    transition: none;
  }
}
