.groupItem {
  display: flex;

  a {
    color: $defaultColor;
  }
}

.label {
  flex: 0 0 110px;
  margin-right: 10px;
  padding-top: calc($largeFontSize - $defaultFontSize);
  color: rgba(250, 250, 250, 0.45);
  text-transform: uppercase;
  font-family: $boldFontFamily;
}

.content {
  overflow: hidden;
  font-size: $largeFontSize;
}
