//
// Sign In
// --------------------------------------------------

.sign-in-container > .sign-in-logo {
  margin-top: 50px;
}

.sign-in-container > .row-padded {
  margin-top: 50px;
}

.sign-in-container {
  .tv-mode-btn {
    position: absolute;
    top: 30px;
    right: 30px;
    font-size: @font-size-large;
  }

  .tv-mode-icon {
    margin-left: 10px;
    font-size: 20px;
  }
}

.sign-in-header {
  > .selected {
    color: @gray-lightest;
  }

  > .sign-in-header-divider {
    margin: 0 10px;
  }
}

.sign-in-form {
  .email-group,
  .confirm-group,
  .age-disclaimer,
  .sign-up-label
   {
    display: none;
  }
}

.sign-up-form {
  .sign-in-label {
    display: none;
  }

  .age-disclaimer {
    font-size: @font-size-small;
    color: @gray-light;
  }
}

.skip-sign-in-container {
  margin-top: 50px;
  text-align: center;

  .why-sign-in-btn {
    position: fixed;
    right: 20px;
    bottom: 20px;
  }
}
