.item {
  &:hover,
  &.isActionsMenuOpen {
    background-color: $stripeListHoverBackgroundColor;

    .index,
    .duration {
      opacity: 0;
    }

    .playButton,
    .button {
      opacity: 1;
    }
  }

  &.hasActiveSelection,
  &.isForcingSelection {
    .underlay {
      display: none;
    }

    .overlay a,
    .overlay button {
      pointer-events: none;
    }

    .playButton,
    .editButton,
    .moreButton,
    .duration {
      opacity: 0;
    }

    .index {
      opacity: 1;
    }
  }

  &.isSelected {
    background-color: $stripeListSelectedBackgroundColor;

    .index,
    .selectButton {
      opacity: 1;
    }
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: calc($trackCellPadding / 2);

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  overflow: hidden;
}

.playIndexContainer {
  position: relative;
  padding: $trackCellPadding;
  width: 40px;
}

.posterContainer {
  flex-shrink: 0;
  margin-right: 20px;
  padding: calc($trackCellPadding / 2);
}

.titleContainer {
  @add-mixin truncate;
  flex: 1;
  overflow: hidden;
  padding-right: 20px;
}

.actionsContainer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.index {
  position: absolute;
  left: 15px;
  color: $dimColor;
  transition: opacity $defaultSpeed;
  transform: translateY(-50%);
}

.playButton {
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
  composes: button;

  position: absolute;
  top: 50%;
  left: 50%;
  transition: opacity $defaultSpeed;
  transform: translateX(-50%) translateY(-50%);
}

.equalizer {
  composes: equalizer from '~common/js/views/v3/components/core/icons/EqualizerIcon.css';

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.subtitle,
.subtitle a {
  color: $dimColor;
}

.rating {
  flex-shrink: 0;
  padding-right: 20px;
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $dimColor;
}

.duration {
  position: absolute;
  top: 0;
  right: 20px;
  height: 100%;
  color: $dimColor;
  line-height: $trackCellHeight;
  opacity: 1;
  transition: opacity $defaultSpeed;
}

.buttons {
  flex-shrink: 0;
  padding-left: 10px;
}

.button {
  padding: 0 10px;
  opacity: 0;
  transition: opacity $defaultSpeed;

  &:global(.isFocused) {
    opacity: 1;
  }
}

.selectCoverButton {
  @add-mixin cover;
  cursor: default;
}

.selectButton {
  composes: selectButton from '~desktop/common/js/views/v3/components/core/buttons/SelectButton.css';
  composes: button;
}

.editButton,
.moreButton {
  composes: button;
}
