.linkCover {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';
}

.overlay {
  composes: linkCover;

  z-index: 1;
}

.underlay {
  composes: linkCover;

  z-index: 0;
  cursor: default;
}

.selectedListRow {
  composes: row from '~desktop/common/js/v4/core/ui/lists/components/ListRow.css';

  background-color: $stripeListSelectedBackgroundColor;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.rangeListRow {
  composes: row from '~desktop/common/js/v4/core/ui/lists/components/ListRow.css';

  background-color: $stripeListHoverBackgroundColor;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.isSelectionActive {
  pointer-events: none;
}

.firstTableCell {
  composes: tableCell from '~desktop/common/js/v4/core/ui/lists/components/tables/TableCell.css';

  padding-left: calc($playCellWidth + $tableCellPadding);
}

.title {
  composes: title from '~desktop/common/js/v4/core/ui/lists/components/tables/TableCell.css';

  color: $defaultColor;
  cursor: default;
  user-select: none;
}

a.title,
.title a {
  z-index: 1;
  cursor: pointer;

  &:hover {
    /* Override global v2 anchor color style */
    color: $defaultColor;
    text-decoration: underline;
  }
}

.isSecondary,
.isSecondary a {
  color: $dimColor;
}

.playCell {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: $playCellWidth;
  height: 100%;
}

.downloadIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 5px;
  width: 21px;
  height: 100%;
}

.outsideIcon {
  position: absolute;
  right: calc($tableCellPadding + $actionsCellPadding);
}

.hiddenIcon {
  opacity: 0;
}

.actionsCell {
  composes: tableCell from '~desktop/common/js/v4/core/ui/lists/components/tables/TableCell.css';

  align-items: stretch;
  padding: 0 $actionsCellPadding;
}

.actionsButton {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  padding: 0 10px;
}
