.container {
  display: flex;
  flex-shrink: 0;
  padding: 0 $modalBodyPadding;
  height: $dvrSetupChannelMappingItemHeight;
}

.checkbox {
  composes: input from '~desktop/common/js/v4/core/ui/forms/components/Checkbox.css';

  margin: 0 10px !important;
  height: $dvrSetupChannelMappingItemHeight;
}

.titleContainer {
  display: flex;
  flex-direction: row;
}

.title {
  padding-right: 10px;
  color: $v4AltMedium;
  text-transform: uppercase;
}

.count {
  padding-right: 10px;
  color: $v4AltMediumDark;
}

.rescanButton {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  text-transform: uppercase;

  font-family: $boldFontFamily;
}

.device {
  display: flex;
  align-items: center;
  flex: 0 0 50%;
  flex-direction: row;

  .titleContainer {
    margin-left: 10px;
  }
}

.lineup {
  display: flex;
  align-items: center;
  flex: 0 0 50%;
}

@media only screen and (max-width: $breakpointSmall) {
  .container {
    height: calc($dvrSetupChannelMappingItemHeight * 2);
  }

  .titleContainer {
    flex-direction: column;
  }
}
