.tag {
  display: inline-block;
  padding: 0 10px 0 0;
  color: rgba(255, 255, 255, 0.75);
  font-size: $defaultFontSize;
  font-family: $defaultFontFamily;
}

.tagText {
  display: inline-block;
  padding: 0 0 0 5px;
}

.icon {
  color: rgba(255, 255, 255, 0.15);
  font-size: 14px;
}

/*
 * States
 */

.isFollowed {
  color: $selectedColor;
}

.isMuted {
  color: #f53;
}
