.container {
  margin: 0 $v4PagePadding 40px;
  padding: 45px 70px 0;
  border-radius: $v4CardBorderRadius;
  background-image: linear-gradient(
      to left,
      rgba(255, 255, 255, 0.0001) 500px,
      #f3f1e7 800px
    ),
    url('~common/img/upsell/tidal-upsell-background.jpg'),
    linear-gradient(to left, #fffffd 0%, #f3f1e7 100%);
  background-position: 0 0, bottom 0 right 60px, 0 0;
  background-size: auto auto, auto 100%, auto auto;
  background-repeat: no-repeat, no-repeat, no-repeat;
}

.text {
  color: $inverseColor;
  font-size: $largeFontSize;
}

.logo {
  width: 240px;
  height: 32px;
  background-image: url('~common/img/vendor-logos/tidal-text.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.body {
  padding: 30px 0;
  max-width: 430px;
}

.link {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  color: $primaryColor;

  &:hover {
    color: $inverseColor;
  }
}

@media (max-width: $breakpointLarge) {
  .container {
    background: linear-gradient(to left, #fffffd 0%, #f3f1e7 100%);
  }
}
