.container {
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;
  padding: 6px;
  width: 360px;
  border-radius: 6px;
  background-color: $v4BaseMediumLight;
}

.info {
  margin-left: 10px;
}

.make {
  color: $v4DefaultColor;
  font-size: 11px;
  line-height: 15px;
}

.model {
  color: $v4DefaultColor;
  font-size: 13px;
  line-height: 24px;
}

@media only screen and (max-width: $breakpointExtraSmall) {
  .container {
    width: 100%;
  }
}
