.channel {
  display: flex;
  padding: 8px 20px;
  color: rgba(255, 255, 255, 0.75);
}

.channel:nth-of-type(odd) {
  background-color: transparent;
}

.channel:nth-of-type(even) {
  background-color: #292929;
}

.channelIdentifier {
  width: 60px;
}

.signalStrength {
  composes: signalStrength from '~desktop/common/js/views/v3/components/livetv/setup/SignalStrength.css';

  width: 50px;
}
