.photoContainer {
  box-sizing: border-box;
  padding: $mosaicCellPadding;
}

.photo {
  position: relative;
  width: 100%;
  height: 100%;
}

.image {
  z-index: 0;
}

.badge {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 2px 7px;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.8);
  color: $defaultColor;
  pointer-events: none;
}

.title {
  position: absolute;
  bottom: 10px;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0 1px 2px $shadowColor;
  font-family: $boldFontFamily;
  pointer-events: none;
}
