.header {
  position: relative;
  display: flex;
  height: 100px;
}

.tintedHeader {
  composes: header;

  background-color: rgba(229, 160, 13, 0.3);
}

.titleBackground {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.emptyTitleBackground {
  composes: titleBackground;

  background-color: rgba(229, 160, 13, 0.3);
}

.imageContainer {
  position: absolute;
  width: 100%;
  height: 100%;
}

.titleText {
  margin: auto;
  color: $defaultColor;
  text-transform: uppercase;
  font-size: $largeFontSize;
  font-family: $boldFontFamily;
}
