.event {
  position: relative;
  height: 60px;

  &:nth-of-type(odd) {
    background-color: $stripeListAlternateBackgroundColor;
  }

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.isComplete {
  &:nth-of-type(odd) {
    background-color: $calendarEventSuccessAlternateBackgroundColor;
  }

  &:nth-of-type(even) {
    background-color: $calendarEventSuccessBackgroundColor;
  }

  &:hover {
    background-color: $calendarEventSuccessHoverBackgroundColor;
  }
}

.hasError {
  &:nth-of-type(odd) {
    background-color: $calendarEventErrorAlternateBackgroundColor;
  }

  &:nth-of-type(even) {
    background-color: $calendarEventErrorBackgroundColor;
  }

  &:hover {
    background-color: $calendarEventErrorHoverBackgroundColor;
  }
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  display: flex;
  align-items: flex-start;
  padding: 8px 8px 8px 14px;

  .statusIcon {
    pointer-events: all;
  }
}

.date {
  @add-mixin truncate;
  flex: 1 1 15%;
  padding-right: 5px;
  max-width: 150px;
}

.isToday {
  color: $defaultColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
}

.time {
  flex: 1 1 10%;
  padding-right: 5px;
  max-width: 150px;
  color: $dimColor;
}

.title {
  @add-mixin truncate;
  flex: 2 0 0;
  color: $defaultColor;
}

.titleDetails {
  @add-mixin truncate;
  color: $dimColor;
}

.status {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  margin-left: 5px;
  min-width: 24px;
}

.recording {
  position: relative;
}

.recordingProgressBackground {
  composes: circularProgressBarBackground from '~desktop/common/js/views/v3/components/core/CircularProgressBar.css';

  background-color: rgba(0, 0, 0, 0.2);
}

.recordingIcon {
  @add-mixin absoluteCenter;
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: $recordingIconColor;
}
