$whatsNewModalWidth: 886px;
.modal {
  composes: modal from '~core/components/modals/Modal.css';

  width: 886px;
}

.innerModalBody {
  padding: 0;
}

.modalHeader {
  composes: modalHeader from '~core/components/modals/ModalHeader.css';

  font-family: $boldFontFamily;
}

.modalFooter {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  justify-content: center;
  flex-direction: column;
}

.image {
  width: 100%;
  height: 281px;
  background-color: #5f71b3;
}

.title {
  margin-bottom: 20px;
  text-align: center;
  font-size: 24px;
  font-family: $boldFontFamily;
  line-height: 32px;
}

.copyContainer {
  padding: 30px 20px 50px;
}

.copy {
  margin-bottom: 0;
  text-align: center;
  font-size: 18px;
  font-family: $defaultFontFamily;
  line-height: 25px;
}

.okButton {
  composes: button from '~core/components/buttons/Button.css';

  min-width: 140px;
}

.footer {
  margin: 10px 0;
  color: $v4AltMedium;
  text-align: center;
  font-size: 14px;
  line-height: 24px;
}

@media only screen and (max-height: 600px) {
  .image {
    display: none;
  }

  .innerModalBody {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 100%;
  }

  .copyContainer {
    padding: 20px;
  }
}

@media only screen and (max-width: $whatsNewModalWidth) {
  .modal {
    max-height: 100%;
    width: 100%;
    height: 100%;
  }

  .image {
    width: 100%;
    height: 40vw;
  }
}
