.titleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 $sourceSidebarLeftGutter;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.title {
  @add-mixin truncate;
  text-transform: uppercase;
  font-size: $largeFontSize;
  font-family: $defaultFontFamily;
}

.closeButton {
  font-size: $sourceSidebarIconSize;
}

.expandedSidebarContent {
  composes: expandedSidebarContent from '~desktop/common/js/views/v3/components/sidebar/SourceSidebarContent.css';
}
