.modalBody {
  flex-grow: 1;
  padding: $modalBodyPadding;
  background-color: $modalBodyBackgroundColor;
}

/*
 * Use a separate container for padding within a scroller.
 * See: https://bugzilla.mozilla.org/show_bug.cgi?id=748518
 */
.modalScroller {
  composes: modalBody;

  padding: 0;
}

.innerModalBody {
  padding: $modalBodyPadding;
}
