.conflict {
  display: flex;
  align-items: center;
  padding: 5px 30px;
  width: 100%;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.conflict:nth-child(even) {
  background-color: $stripeListAlternateBackgroundColor;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.thumbnail {
  flex-shrink: 0;
}

.title {
  padding: 15px;
}

.titleHighlight {
  font-family: $boldFontFamily;
}
