.input {
  display: inline-block;
  /* Bootstrap applies margin and padding */
  margin: 10px !important;
}

.small {
  font-size: $defaultFontSize;
}

.medium {
  font-size: $defaultFontSize;
}

.large {
  font-size: $largeFontSize;
}

.isDisabled {
  opacity: $inputDisabledOpacity;
  cursor: not-allowed;
}

.hasError {
  color: $dangerColor;
}
