.hubCell {
  margin-bottom: 40px;
}

.hubCell:first-of-type {
  margin-top: 25px;
}

.hubActions {
  flex-shrink: 0;
  margin-left: auto;
  font-size: 18px;
  line-height: 24px;
}

.hubScrollButton {
  margin-left: 13px;
  padding: 0 5px;

  &:global(.isDisabled) {
    opacity: 0.15;
  }
}

.hubScroller {
  composes: scroller from '~core/components/lists/VirtualListScroller.css';

  /* See: https://github.com/plexinc/plex-web-client/issues/10664 */
  padding-bottom: 20px;
}

.hubCellHeader {
  composes: hubCellHeader from '~desktop/common/js/views/v3/components/metadata/lists/HubCellHeader.css';

  position: relative;
}
