//
// Mixins
// --------------------------------------------------


// Utilities
// -------------------------

// @2x images for retina screens
.at2x(@path, @w: auto, @h: auto) {
  background-image: url(@path);

  @media all and (-webkit-min-device-pixel-ratio: 1.5) {
    @at2x_path: ~`"@{path}".split('.').slice(0, "@{path}".split('.').length - 1).join(".") + "@2x" + "." + "@{path}".split('.')["@{path}".split('.').length - 1]`;
    background-image: url(@at2x_path);
    background-size: @w @h;
  }
}

// Vertical and horizontal centered
.centered() {
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  text-align: center;
  line-height: 0px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

// Webkit-style focus
.tab-focus() {
  outline: 0;
}

.no-transform() {
  -webkit-transform: none;
  -ms-transform: none; // IE9+
  transform: none;
}

.cursor(grab) {
  cursor: move;
  cursor: -webkit-grab;
  cursor: -moz-grab;
  cursor: grab;
}


// Button variants
// -------------------------
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons
.button-variant(@color; @background; @border) {
  color: @color;
  background-color: @background;
  .transition(background-color 0.1s);

  &:hover,
  &:focus {
    color: @color;
    background-color: lighten(@background, 8%);
  }

  &:active,
  &.active,
  .open .dropdown-toggle& {
    color: @color;
    background-color: darken(@background, 12%);
  }

  &:active,
  &.active,
  .open .dropdown-toggle& {
    background-image: none;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &:active,
    &.active {
      background-color: @background;
    }
  }
}


// CSS3
// -------------------------

.animation(@animation) {
  -webkit-animation: @animation;
  -moz-animation: @animation;
  -o-animation: @animation;
  animation: @animation;
}

.stripes(@start-color: #000; @end-color: #fff; @stripe-width: 10px; @deg: -45deg) {
  background-image: -webkit-repeating-linear-gradient(@deg, @start-color, @start-color @stripe-width, @end-color @stripe-width, @end-color @stripe-width * 2);
  background-image: -moz-repeating-linear-gradient(@deg, @start-color, @start-color @stripe-width, @end-color @stripe-width, @end-color @stripe-width * 2);
  background-image: -ms-repeating-linear-gradient(@deg, @start-color, @start-color @stripe-width, @end-color @stripe-width, @end-color @stripe-width * 2);
  background-image: -o-repeating-linear-gradient(@deg, @start-color, @start-color @stripe-width, @end-color @stripe-width, @end-color @stripe-width * 2);
  background-image: repeating-linear-gradient(@deg, @start-color, @start-color @stripe-width, @end-color @stripe-width, @end-color @stripe-width * 2);
}


// Form validation states
//
// Used in forms.less to generate the form validation CSS for warnings, errors,
// and successes.

.form-control-validation(@text-color: #555; @border-color: #ccc; @background-color: #f5f5f5) {
  // Color the label and help text
  .help-block,
  .control-label,
  .control-label a {
    color: @text-color;
  }
  .control-error {
    display: inline;
    color: @gray-light;
  }
  // Set the border and box shadow on specific inputs to match
  .form-control {
    background-color: @background-color;
    .placeholder(lighten(@text-color, 10%));
    &:focus {
      background-color: @gray-lightest;
    }
  }
  // Set validation states also for addons
  .input-group-addon {
    color: @text-color;
    border-color: @border-color;
    background-color: @background-color;
  }
}

// Form control focus state
//
// Generate a customized focus state and for any input with the specified color,
// which defaults to the `@input-focus-border` variable.

.form-control-focus(@color: @input-border-focus) {
  @color-rgba: rgba(red(@color), green(@color), blue(@color), .6);
  &:focus, &.focus {
    color: @input-color-active;
    background-color: @input-bg-active;
    outline: 0;
    .box-shadow(none);
  }
}


// COMPONENT MIXINS
// --------------------------------------------------

// Horizontal dividers
// -------------------------
// Dividers (basically an hr) within dropdowns and nav lists
.nav-divider(@color: #e5e5e5) {
  height: 1px;
  margin: 5px;
  overflow: hidden;
  background-color: @color;
}

// Labels
// -------------------------
.label-variant(@color) {
  background-color: @color;
  &[href] {
    &:hover,
    &:focus {
      background-color: darken(@color, 5%);
    }
  }
}

// Lists
// -------------------------
.disabled-list() {
  &.disabled {
    position: relative;
    .opacity(0.3);

    &:before {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
      content: "";
    }
  }
}
