.container {
  position: absolute;
  top: 0;
  left: -20px;
  width: 25px;
  height: 100%;
  text-align: center;
  font-size: $largeFontSize;
}

.icon {
  composes: icon from '~desktop/common/js/views/v3/components/core/buttons/AddOrRemoveButton.css';

  padding-left: 1px;
}

.button {
  composes: button from '~desktop/common/js/views/v3/components/core/buttons/AddOrRemoveButton.css';

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.container,
.button,
.icon {
  /* Transition color to maintain the transition style of the button import. */
  transition: color $defaultSpeed, opacity $defaultSpeed;
}

.isHidden {
  opacity: 0;
}

.isVisible {
  opacity: 1;
}
