$codeWidth: 160px;
$codesPadding: 16px;

.innerModalBody {
  composes: innerModalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  align-items: center;
  flex-direction: column;
}

.codes {
  display: flex;
  flex-wrap: wrap;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: $codesPadding;
  width: calc(2 * ($codeWidth + $codesPadding));
  border-radius: 4px;
  background-color: $lightTranslucentBackgroundColor;
}

.code {
  width: $codeWidth;
  text-align: center;
  font-size: $largeFontSize;
  font-family: 'Consolas', monospace;
}

.info {
  align-self: flex-start;
  margin-bottom: 15px;
}

.importantInfo {
  color: $primaryColor;
}

.strong {
  font-size: $largeFontSize;
}
