.rounded {
  border-radius: 50%;
}

.card {
  composes: card from '~desktop/common/js/v4/core/ui/cards/PosterCard.css';
  composes: rounded;
}

.link {
  composes: link from '~desktop/common/js/v4/core/ui/cards/PosterCardLink.css';
  composes: rounded;
}

.hoveredLink {
  composes: hoveredLink from '~desktop/common/js/v4/core/ui/cards/PosterCardLink.css';
  composes: rounded;

  /*
    Since there are no actions over the cast links override the hover
    background color from PosterCardLink
  */
  background-color: transparent;
}

.titleContainer {
  padding-top: $v4CardTitleGap;
  text-align: center;
}

.title {
  composes: singleLineTitle from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardTitle.css';
}

.role {
  composes: title;
  composes: isSecondary from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardTitle.css';
}
