.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.cardContainer {
  flex-shrink: 0;
  margin: 0 14px 0 0;
  width: 64px;
}

.card {
  composes: card from '~desktop/common/js/views/v3/components/search/posters/QuickSearchMetadataPosterCard.css';

  margin: 0 auto;
}

.titlesContainer {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
}

.sourceText {
  color: $dimColor;
}

.durationContainer {
  padding-left: 20px;
  color: $dimColor;
  white-space: nowrap;
}
