.line {
  opacity: 1;
  cursor: pointer;
  transition: opacity $chartOpacitySpeed;
}

.translucentLine {
  composes: line;

  opacity: $chartTranslucentOpacity;
}

.legendContainer {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.legendLabels {
  display: flex;
}

.legendSummary {
  color: $dimColor;
  font-size: 10px;
}

.legendLabel {
  composes: legendLabel from '~desktop/common/js/views/v3/components/core/charts/LegendLabel.css';

  margin-right: 20px;
  text-transform: uppercase;
  font-size: 10px;
  opacity: 1;
  cursor: pointer;
  transition: opacity $chartOpacitySpeed;
}

.translucentLegendLabel {
  composes: legendLabel;

  opacity: $chartTranslucentOpacity;
}
