.activityIcon {
  composes: iconContainer from './NavBarActivityIcon.css';

  transition: background $defaultSpeed;
}

.label {
  position: absolute;
  top: 30px;
  left: 0;
  font-size: 11px;
  font-family: $boldFontFamily;
}

.isHighlighted {
  .activityIcon {
    background-color: $primaryAlternateColor;
    color: #000;
  }

  .label {
    color: $primaryAlternateColor;
  }
}

.isHighlighted:hover {
  .activityIcon {
    background-color: lighten($primaryAlternateColor, 0.1);
  }

  .label {
    color: lighten($primaryAlternateColor, 0.1);
  }
}

.alertIcon {
  composes: alertIcon from './NavBarIconButton.css';

  top: 28px;
  width: $defaultIconFontSize;
  height: $defaultIconFontSize;
}
