.container {
  position: relative;
  height: 386px;
}

.listActions {
  display: inline-block;
  flex-shrink: 0;
  margin-left: 30px;
  font-size: 16px;
}

.listScrollButton {
  composes: iconButton from '~desktop/common/js/v4/core/ui/buttons/components/IconButton.css';

  &:not(:first-child) {
    margin-left: 14px;
  }

  &:global(.isDisabled) {
    opacity: 0.15;
  }
}
