.header {
  margin-top: 30px;
  font-size: 15px;
  font-family: $boldFontFamily;
}

.list {
  margin-top: 30px;
  margin-bottom: 40px;
  padding-left: 16px;
  font-size: 13px;
}

.item {
  margin-bottom: 24px;
  padding-left: 9px;

  &:last-child {
    margin-bottom: 0;
  }
}

@media (max-width: $breakpointSmall) {
  .list {
    width: 100%;
  }

  .item {
    margin-bottom: 12px;
  }
}
