.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 50px 10px 60px;
  border-bottom: 1px solid $dividerColor;
  background-color: $blackDarkTranslucentBackgroundColor;
  color: $dimColor;
}

.info {
  padding-right: 20px;
  max-width: 400px;
}

.action {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  display: flex;
  align-items: center;
}

.reset {
  display: flex;
  justify-content: center;
  min-width: 300px;
}

.resetIcon {
  top: 0;
  margin-right: 5px;
}
