.container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-shrink: 0;
  overflow: hidden;
  padding: 0 20px;
}

.text {
  @add-mixin truncate;
  padding-right: 10px;
}

.attribution {
  flex-shrink: 0;
  padding: 5px;
  width: $playlistBadgeSize;
  height: $playlistBadgeSize;
}

.avatar {
  composes: avatar from '~desktop/common/js/views/v3/components/core/images/AvatarImg.css';

  flex-shrink: 0;
}
