//
// Buttons
// --------------------------------------------------


// Base styles
// --------------------------------------------------

// Core styles
.btn {
  padding: @padding-base-vertical @btn-padding-base-horizontal;
  font-family: @btn-font-family;
  text-transform: uppercase;
  border: 0;

  &:focus {
    outline: 0;
  }

  &:active,
  &.active {
    .box-shadow(none);
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    .opacity(0.3);
  }
}


// Alternate buttons
// --------------------------------------------------

.btn-default {
  &:hover,
  &:focus {
    background-color: rgba(255, 255, 255, 0.3);
  }

  &:active,
  &.active,
  .open .dropdown-toggle& {
    background-color: rgba(255, 255, 255, 0.2);
  }
}


// Button Sizes
// --------------------------------------------------

.btn-lg {
  // line-height: ensure even-numbered height of button next to large input
  .button-size(@padding-large-vertical; @btn-padding-large-horizontal; @font-size-large; @line-height-large; @border-radius-large);
}
.btn-sm,
.btn-xs {
  // line-height: ensure proper height of button next to small input
  .button-size(@padding-small-vertical; @btn-padding-small-horizontal; @font-size-small; @line-height-small; @border-radius-small);
}


// Icon buttons
// -------------------------

.btn-icon {
  font-size: 1.2em;
  line-height: 1.2em;
  padding-top: 4px;
  padding-bottom: 0;
}

.btn-icon.btn-lg {
  font-size: 1.5em;
  line-height: 1.5em;
}

.btn-icon-label {
  i {
    margin-right: 5px;
  }
}


// Link buttons
// -------------------------

// Make a button look and behave like a link
.btn-link {
  text-shadow: none;
  border: 0;

  &:hover,
  &:focus {
    text-decoration: none;
    outline: 0;
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: default;
    .opacity(0.3);
  }
}

.btn-gray-link {
  .btn-link;

  color: #777;
  &:hover {
    color: @gray-lightest;
  }
}

// Gray button
// -------------------------

.btn-gray {
  padding: 0;
  color: @gray-light;
  text-shadow: none;
  background: none;
  border: 0;
  .box-shadow(none);

  &.btn-success,
  &.btn-danger {
    color: @brand-danger;

    &:hover,
    &:focus,
    &:active {
      background: none;
      border: 0;
      outline: 0;
      .box-shadow(none);
    }
  }

  &.btn-success {
    color: @brand-success;
  }

  &:hover,
  &:focus {
    color: @gray-lightest;
    outline: 0;
  }

  &.selected {
    color: @orange-light;
  }

  &.disabled {
    color: @gray-dark;
    cursor: default;
  }
}


// White button
// -------------------------

.btn-white {
  padding: 0;
  color: @white;
  text-shadow: none;
  background: none;
  border: 0;
  .box-shadow(none);

  &:hover {
    color: @link-hover-color;
    outline: 0;
  }

  &.disabled {
    color: @gray-dark;
    cursor: default;
  }
}
