$epgEpisodePadding: 24px;
$epgEpisodePaddingRight: 72px;

.item:hover {
  background-color: $stripeListHoverBackgroundColor;

  .recordButton {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

.alternateItem {
  composes: item;

  background-color: $stripeListAlternateBackgroundColor;
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: $epgEpisodePadding $epgEpisodePaddingRight $epgEpisodePadding
    $epgEpisodePadding;
}

.primaryTitle {
  @add-mixin truncate;
  font-size: $largeFontSize;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.secondaryTitle {
  @add-mixin truncate;
  color: $defaultLinkColor;
}

.subscriptionIcon {
  margin-right: 8px;
  color: $recordColor;
  font-size: $defaultIconFontSize;
}

.nextAiringDate {
  color: $dimColor;
}

.separator {
  margin: 0 4px;
  color: $dimColor;
}

.recordButton {
  composes: recordButton from '~desktop/common/js/views/v3/components/core/buttons/RecordButton.css';

  position: absolute;
  top: 36px;
  right: 36px;
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.9);
}

/*
 * Loading state
 */

.loadingItem {
  padding: $epgEpisodePadding;
  color: $dimColor;
}

.alternateLoadingItem {
  composes: loadingItem;

  background-color: $stripeListAlternateBackgroundColor;
}
