.card {
  position: relative;
  margin: 0 auto;
  background-color: $posterBackgroundColor;
}

.playButton {
  @add-mixin absoluteCenter;
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';

  position: absolute;
  z-index: 3;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.playCircle {
  @add-mixin absoluteCenter;
  composes: playCircle from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
}

.recordButton {
  @add-mixin absoluteCenter;
  composes: recordIconContainer from '~desktop/common/js/views/v3/components/core/icons/RecordIcon.css';

  position: absolute;
  z-index: 3;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.recordCircle {
  @add-mixin absoluteCenter;
  composes: recordCircle from '~desktop/common/js/views/v3/components/core/icons/RecordIcon.css';
}

.card:hover .playButton,
.card:hover .recordButton {
  opacity: 1;
}
