.topControls {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  height: 100%;
}

.topButton {
  composes: playerButton from '~desktop/common/js/views/v3/components/players/core/PlayerIconButton.css';

  text-shadow: 0 0 4px $playerShadowColor;
}

.castButton {
  composes: topButton;

  margin-right: 10px;
}

.remotePlayerName {
  margin-left: 10px;
  font-family: $boldFontFamily;
}

.remotePlayerProduct {
  margin-left: 4px;
  color: $dimColor;
  font-family: $boldFontFamily;
}
