.sidebarLink {
  composes: default from '~core/components/buttons/Link.css';

  display: block;
  flex-grow: 1;
  overflow: hidden;
  padding: 4px 0 2px $sidebarLeftGutterWidth;

  &:global(.isDisabled) {
    color: $defaultLinkDisabledLinkColor;
  }

  &:hover {
    .iconImg {
      opacity: 1;
    }
  }
}

.container {
  display: flex;
}

.icon {
  flex-shrink: 0;
  width: 25px;
  height: $defaultIconFontSize;
  font-size: $defaultIconFontSize;
}

.iconImg {
  position: relative;
  top: -2px;
  height: 100%;
  opacity: 0.7;
}

.title {
  flex-grow: 1;
  word-break: break-word;
}

.children {
  flex-shrink: 0;
  padding: 0 15px;
}

.spinner {
  position: relative;
  top: 2px;
  left: 0;
  margin-right: 10px;
  width: $defaultIconFontSize;
  height: $defaultIconFontSize;
  border-top-color: $defaultLinkColor;
  border-left-color: $defaultLinkColor;
}
