.container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  padding: $modalBodyPadding 0;
}

.modalBody {
  composes: modalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-grow: 1;
  padding: 0;
  width: 100%;
}

.innerModalBody {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 100%;
}

.scroller {
  composes: scroller from '~desktop/common/js/v4/core/ui/scrollers/components/VirtualListScroller.css';

  flex-grow: 1;
}

.buttonBar {
  composes: buttonBar from '~desktop/common/js/v4/core/ui/modals/components/ModalFooterButtonBar.css';

  display: flex;
  justify-content: space-between;
  width: 100%;
}
