.sidebarListHeaderContainer {
  display: flex;
}

.sidebarListHeader {
  @add-mixin truncate;
  align-content: center;
  padding-left: $sidebarLeftGutterWidth;
  height: 45px;
  color: $headerColor;
  text-transform: uppercase;
  font-size: $smallFontSize;
  font-family: $boldFontFamily;
  line-height: 45px;
}

.sidebarListHeaderIcon {
  flex-shrink: 0;
  padding: 0 $sidebarRightGutterWidth;
  height: 45px;
  font-size: $smallFontSize;
  line-height: 45px;
}
