.container {
  @add-mixin cover;
  display: flex;
  flex-direction: column;
  padding: 20px $playQueueSidePadding 0;
}

.topContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
}

.topLeft {
  text-align: left;
}

.topRight {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.count {
  margin-right: 20px;
  color: $dimColor;
  font-size: $largeFontSize;
}

.content {
  composes: vertical from '~core/components/base/Scroller.css';

  position: relative;
  flex-grow: 1;
  padding-right: 0;
}

.tooltipButton {
  text-align: center;
  font-size: 20px;
}
