.button {
  composes: disclosureArrowButton from '~core/components/buttons/DisclosureArrowButton.css';

  position: relative;
  /*
    disclosureArrowButton contains `overflow: hidden !important;` to support
    truncation. We use !important again here to override this and allow .badge
    to escape the container.
  */
  overflow: visible !important;
  height: 100%;
  /*
    This prevents the application flickering in safari when overflow: visible is
    set by forcing hardware acceleration.
    See: https://github.com/plexinc/plex-web-client/issues/11855
  */
  /* stylelint-disable property-no-vendor-prefix */
  -webkit-transform: translate3d(0, 0, 0);
  /* stylelint-enable */
}

.badge {
  composes: badge from '~core/components/base/Badge.css';

  position: absolute;
  top: 27px;
  left: -15px;
  font-size: 11px;
}

.tooltip {
  composes: bottomCenter from '~core/components/tooltips/Tooltip.css';

  margin-top: $tooltipMarginTop;
}

.tooltipTriggerHasFocus {
  composes: tooltip;

  margin-top: calc($tooltipMarginTop * -1);
}

.avatarContainer {
  display: flex;
}

.overlay {
  position: absolute;
  width: 30px;
  height: 30px;
  border: 1px solid #fff;
  border-radius: 50%;
  opacity: 0;
  transition: opacity $defaultSpeed;
}

.isMenuOpen {
  opacity: 1;
}
