.link {
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
  border-radius: 0;
  background: none;
  text-align: inherit;
  text-decoration: none;
  cursor: pointer;
  transition: color $defaultSpeed;
  user-select: none;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;

  &:hover {
    color: $defaultLinkHoverColor;
  }
}

.isHrefLink {
  user-select: text;
}

:global(.isDisabled) {
  pointer-events: none;
}

:global(.isFocused:not(.isDisabled)) {
  box-shadow: inset 0 0 0 2px $focusColor, inset 0 0 5px rgba(0, 0, 0, 0.5);
}

/*
 * Kinds
 */

.default {
  color: $defaultLinkColor;
}

.primary {
  color: $primaryColor;
}

.success {
  color: $successColor;
}

.danger {
  color: $dangerColor;
}

/*
 * Sizes
 */

.small {
  font-size: $smallFontSize;
}

.medium {
  font-size: $defaultFontSize;
}

.large {
  font-size: $largeFontSize;
}
