.innerModalBody {
  composes: innerModalBody from 'desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-direction: column;
  padding: 0;
  height: 100%;
}

.banner {
  width: 100%;
  height: 180px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.banner1 {
  composes: banner;

  background-image: url('~desktop/common/img/banners/banner-custom-home-sunset-screen-1.png');
}

.banner2 {
  composes: banner;

  background-image: url('~desktop/common/img/banners/banner-custom-home-sunset-screen-2.png');
  background-position: left;
}

.banner3 {
  composes: banner;

  background-image: url('~desktop/common/img/banners/banner-custom-home-sunset-screen-3.png');
}

.banner4 {
  composes: banner;

  background-image: url('~desktop/common/img/banners/banner-custom-home-sunset-screen-4.png');
  background-position: left;
}

.banner5 {
  composes: banner;

  background-image: url('~desktop/common/img/banners/banner-custom-home-sunset-screen-5.png');
}

.copyContainer {
  margin: auto 0;
  padding: 15px;
}

.title {
  margin-bottom: 20px;
  text-align: center;
  font-size: $headerFontSize;
  font-family: $boldFontFamily;
  line-height: 32px;
}

.copy {
  margin-bottom: 0;
  text-align: center;
  font-size: $defaultFontSize;
  font-family: $defaultFontFamily;
  line-height: 25px;
}

.copy strong {
  font-family: $boldFontFamily;
}

.copy .small {
  display: block;
  padding-top: 10px;
  font-size: 10px;
}
