.container {
  display: flex;
  align-items: center;
  margin: 20px;
  padding: 10px 20px;
  width: calc(100% - 40px);
  background-color: $warningColor;
  color: $v4BaseDark;
}

.messageContainer {
  display: flex;
  align-items: center;
  flex-grow: 1;
  margin-right: 10px;
}

.message {
  margin-left: 10px;
}

.icon {
  top: 0;
  flex-shrink: 0;
}

.button {
  composes: button from '~core/components/buttons/Button.css';

  flex-shrink: 0;
}

@media only screen and (max-width: $breakpointSmall) {
  .container {
    flex-direction: column;
  }

  .messageContainer {
    margin-bottom: 10px;
  }
}
