.selectButton {
  line-height: 0;
  cursor: default;

  /*
   * Use the not selector to fix white border color sticking on select without
   * mouse movement.
   */
  &:hover:not(.isSelected) .selectCircle {
    border-color: $defaultLinkHoverColor;
  }

  &:global(.isActive) .selectCircle {
    background-color: $selectedColor;
  }
}

.selectCircle {
  position: relative;
  display: inline-block;
  width: 14px;
  height: 14px;
  border: 2px solid $defaultLinkColor;
  border-radius: 50%;
  color: $defaultLinkColor;
  text-align: center;
  font-size: 9px;
  line-height: 14px;
  transition: all $defaultSpeed;
}

.selectedIcon {
  @add-mixin absoluteCenter;
  opacity: 0;
  transition: opacity $defaultSpeed;
}

.isSelected {
  .selectCircle {
    border-color: $selectedColor;
    background-color: $selectedColor;
    box-shadow: 0 0 4px $posterBadgeShadowColor;
    color: rgba(0, 0, 0, 0.75);
    transform: scale(1.4);
  }

  .selectedIcon {
    opacity: 1;
  }
}
