.statusContainer:not(:last-child) {
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid $menuSeparatorBackgroundColor;
}

.splitButton {
  composes: splitButton from './ActivityMenuStatusItem.css';

  display: inline-flex;
  align-content: center;
  align-items: center;
  justify-content: space-between;
}

.statusButtonIndicator {
  flex-basis: 18px;
  color: $primaryAlternateColor;
  text-align: center;
}

.alertIndicator {
  display: flex;
  width: $defaultIconFontSize;
  height: $defaultIconFontSize;
}

.statusTitle {
  @add-mixin truncate;
  margin: 0 0 5px;
  padding: 0 $menuItemSidePadding;
  color: $dimColor;
  text-transform: uppercase;
  font-size: 14px;
  font-family: $boldFontFamily;
}

.primaryLabel {
  color: $primaryColor;
}
