.downloadIcon {
  composes: plexIcon from '~core/components/base/PlexIcon.css';
}

.hasShadow {
  background-color: $shadowColor;
}

.arrowIcon {
  @add-mixin absoluteCenter;
}

.progressBarContainer {
  composes: absoluteCenterCircularProgressBarContainer from '~common/js/views/v3/components/core/CircularProgressBar.css';
}

.hasProgress,
.showIndeterminate {
  .arrowIcon {
    color: $dimColor;
  }
}
