.container {
  background: $lightTranslucentBackgroundColor;
}

.loadingContainer {
  composes: container;

  padding: 20px;
  color: $defaultColor;
  text-align: center;
}

.footer {
  overflow: hidden;
  padding: 7px $pagePadding;
}

.settings {
  display: flex;
  flex-direction: row;
  float: left;
}

.checkInput {
  composes: label from '~core/components/forms/InlineInputGroupFormLabel.css';
}

.numberInput {
  composes: input from '~core/components/forms/InlineNumberInputGroup.css';

  background: $altFilterBuilderInputBackgroundColor;
}

.buttons {
  float: right;
  text-align: right;
}

.error {
  margin-right: 10px;
  color: $dangerColor;
}

.button {
  composes: button from '~core/components/buttons/Button.css';

  margin-right: 10px;
  background-color: $filterBuilderInputBackgroundColor;

  &:hover {
    background-color: lighten($filterBuilderInputBackgroundColor, 0.2);
  }

  &:global(.isActive) {
    background-color: darken($filterBuilderInputBackgroundColor, 0.1);
  }

  &:global(.isDisabled) {
    background-color: darken($filterBuilderInputBackgroundColor, 0.3);
  }
}

.saveButton {
  composes: button from '~core/components/buttons/SplitButton.css';

  margin-left: 10px;
}
