.tagsInput {
  display: flex;
  flex-flow: row wrap;
  min-height: 26px;
  width: 100%;
  border-radius: $inputBorderRadius;
  background-color: $inputBackgroundColor;
  font-size: $defaultFontSize;
  font-family: $defaultFontFamily;
  line-height: $lineHeightComputed;
}

.input {
  align-self: flex-start;
  flex-grow: 1;
  flex-shrink: 1;
  margin: 0 5px;
  min-width: 20%;
  max-width: 100%;
  width: 0%;
  outline: none;
  border: 0;
  background: transparent;
  color: $defaultColor;
}

.hasError {
  composes: hasError from './input.css';
}
