.circularProgressBarContainer {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-align: center;
}

.absoluteCenterCircularProgressBarContainer {
  @add-mixin absoluteCenter;
  text-align: center;
}

.circularProgressBar {
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(-90deg);
  transform-origin: center center;
}

.circularProgressBarBackground {
  @add-mixin absoluteCenter;
  border-radius: 50%;
}

.circularProgressBarText {
  position: absolute;
  width: 100%;
  height: 100%;
}

.indeterminate {
  composes: spin from '~core/styles/animations/spin.css';
}
