.calendarHeaderContainer {
  display: flex;
  justify-content: center;
}

.calendarHeader {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-grow: 1;
  padding: 0 2px;
  font-family: $boldFontFamily;
}

.calendarNav {
  composes: link from '~core/components/buttons/Link.css';

  font-size: 20px;
}

.calendarNavDisabled {
  opacity: 0.15;
}
