.title {
  composes: title from './SourceSidebarLink.css';

  color: $primaryColor;
  text-transform: uppercase;
  font-size: $defaultFontSize;
  font-family: $boldFontFamily;
}

.isSelected {
  composes: isSelected from './SourceSidebarLink.css';

  .title {
    color: $primaryColor;
  }
}

.iconContainer {
  composes: iconImg from './SourceSidebarItem.css';

  top: 0;
  left: calc(
    ($sourceSidebarServerHeaderIconSize - $sourceSidebarIconSize) / -2
  );
  width: $sourceSidebarServerHeaderIconSize;
  height: $sourceSidebarServerHeaderIconSize;
  opacity: 1;
}

.actionContainer {
  display: flex;
  align-content: stretch;
  flex-shrink: 0;
  transition: opacity $defaultSpeed;

  &:empty {
    padding-right: 10px;
  }
}
