//
// Glyphicons
// --------------------------------------------------


// Fonts
// -------------------------

@font-face {
  font-family: 'Glyphicons Regular';
  src: url(~desktop/common/fonts/glyphicons/glyphicons-regular.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Glyphicons Social Regular';
  src: url(~desktop/common/fonts/glyphicons/glyphicons-social-regular.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}


// Base styles
// -------------------------

.glyphicon {
  position: relative;
  top: 1px;
  display: inline-block;
  font-family: 'Glyphicons Regular';
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.glyphicon-social {
  font-family: 'Glyphicons Social Regular';
}

// Makes the font 33% larger relative to the icon container.
.glyphicon-lg {
  font-size: 4/3em;
  line-height: 0.75em;
  vertical-align: -15%;
}

.glyphicon-2x {
  font-size: 2em;
}

.glyphicon-3x {
  font-size: 3em;
}

.glyphicon-4x {
  font-size: 4em;
}

.glyphicon-5x {
  font-size: 5em;
}


