.emptyPage {
  @add-mixin resetFont;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
  flex-shrink: 0;
  padding: $pagePadding;
  font-size: $largeFontSize;
}

.icon {
  margin-bottom: 42px;
  padding: 30px;
  border-radius: 50%;
  background-color: $blackDarkerTranslucentBackgroundColor;
  font-size: 40px;
  line-height: 0;

  .default {
    color: $secondaryContentColor;
  }

  .primary {
    color: $primaryColor;
  }

  .success {
    color: $successColor;
  }

  .danger {
    color: $dangerColor;
  }
}

.imgIcon {
  margin-bottom: 42px;
  width: 100px;
  height: 100px;
}

.title {
  max-width: 640px;
  text-align: center;
  font-size: 24px;
  font-family: $boldFontFamily;
  line-height: 1.5;
}

.footer {
  flex-shrink: 0;
  margin-bottom: 30px;
  color: $secondaryContentColor;
  text-align: center;
}
