.draggable {
  &:global(.isDraggingBefore) {
    &::before {
      position: absolute;
      top: 0;
      left: -1px;
      width: 2px;
      height: 100%;
      background-color: $dropLocationColor;
      content: '';
    }
  }

  &:global(.isDraggingAfter) {
    &::after {
      position: absolute;
      top: 0;
      right: -1px;
      width: 2px;
      height: 100%;
      background-color: $dropLocationColor;
      content: '';
    }
  }
}
