.playerButton {
  composes: button from '~core/components/buttons/IconButton.css';

  margin-left: $playerIconButtonSpacing;
  width: $playerIconButtonSize;
  height: $playerIconButtonSize;

  /* Round all borders so the focus styling is consistent for all normal and
  primary buttons */
  border-radius: calc($playerIconButtonSize / 2);
  text-align: center;
  font-size: 18px;

  &:global(.isDisabled) {
    color: $playerDisabledLinkColor;
  }
}

.playerButton:first-child {
  margin-left: 0;
}

.isPrimary {
  background-color: $playerPrimaryButtonBackgroundColor;
}
