.recordIconContainer {
  &:hover .recordCircle {
    border-color: $recordBackgroundColor;
    background-color: $recordBackgroundColor;
    color: $defaultLinkHoverColor;
  }

  &:hover .recordIcon {
    background-color: $defaultColor;
  }

  &:global(.isActive) .recordCircle {
    background-color: $recordActiveBackgroundColor;
  }
}

.recordCircle {
  composes: playCircle from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';
}

.recordIcon {
  @add-mixin absoluteCenter;

  display: block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: $defaultLinkColor;
}
