.modalBody {
  composes: modalScroller from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.innerModalBody {
  composes: innerModalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
}

.spinner {
  composes: spinner from '~app/desktop/common/js/v4/core/ui/spinners/components/Spinner.css';

  display: block;
  margin-bottom: 20px;
  width: $dvrSetupSpinnerSize;
  height: $dvrSetupSpinnerSize;
}

.steps {
  width: 180px;
}

.buttonBar {
  composes: buttonBar from '~desktop/common/js/v4/core/ui/modals/components/ModalFooterButtonBar.css';

  display: flex;
  justify-content: flex-start;
  width: 100%;
}

@media only screen and (max-height: 550px) {
  .spinner {
    display: none;
  }
}
