//
// Modals
// --------------------------------------------------

// .modal-open      - body class for killing the scroll
// .modal           - container to scroll within
// .modal-dialog    - positioning shell for the actual modal
// .modal-content   - actual modal w/ bg and corners and shit

// Container that the modal scrolls within
.modal {
  overflow-y: auto;

  // Fix "position: fixed" inside modal for Safari (and older Webkits)
  &.in .modal-dialog {
    .no-transform();
  }
}

// Modal background
.modal-backdrop {
  z-index: @zindex-modal-background;
}

.modal-alert {
  padding: 0 20px;
}

// Actual modal
.modal-content {
  border: 0;
}

// Modal header
// Top section of the modal w/ title and dismiss
.modal-header {
  padding: 15px 20px;
  background-color: @modal-header-bg;
  border-radius: @border-radius-base @border-radius-base 0 0;

  > h4 {
    font-size: 20px;
    vertical-align: bottom;
    .text-overflow();

    > .modal-icon {
      margin-right: 5px;
      font-size: 18px;
      color: @gray-light;
    }
  }
}

// Close icon
.modal-header .close {
  margin-top: 6px;
  font-size: @font-size-large;
  color: @gray-lightest;
  text-shadow: none;
  outline: 0;
}

.modal-body {
  .clearfix();
}

.modal-body-scroll {
  max-height: 400px;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

// Footer (for actions)
.modal-footer {
  padding: 15px 20px;
  margin-top: 0;
  background-color: @modal-header-bg;
  border-radius: 0 0 @border-radius-base @border-radius-base;
}

// Scale up the modal
@media screen and (min-width: @screen-tablet) {
  .modal-dialog {
    width: 600px;
    padding-top: 70px;
    padding-bottom: 70px;

    .modal-header > h4 {
      max-width: 500px;
    }
  }

  // Modal sizes
  .modal-sm > .modal-dialog {
    width: 450px;

    .modal-header > h4 {
      max-width: 350px;
    }
  }

  .modal-lg > .modal-dialog {
    width: 750px;

    .modal-header > h4 {
      max-width: 650px;
    }
  }
}

// Transparent Modal
// --------------------------------------------------

.modal-transparent {
  .modal-content,
  .modal-header,
  .modal-footer {
    background: none;
    border: none;
    box-shadow: none;
  }
}

// Paned Modal
// --------------------------------------------------

.modal-body-with-panes {
  position: relative;
  padding: 0;

  .modal-nav-pane {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: @zindex-sidebar;
    width: @modal-nav-pane-width;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    #gradient >
      .horizontal-three-colors(
        @start-color: rgba(26, 26, 26, 0.4) ; @mid-color: rgba(19, 19, 19, 0.4)
          ; @color-stop: 50%; @end-color: rgba(0, 0, 0, 0.4)
      );
  }

  .modal-nav-list a {
    display: block;
    padding: 10px 20px;

    &:hover,
    &:focus {
      background-color: rgba(255, 255, 255, 0.05);
    }

    &.disabled {
      color: @gray-light;
      cursor: default;
      background: none;
      .opacity(0.2);
    }

    .modal-nav-icon {
      margin-right: 5px;
    }
  }

  .modal-nav-list-header {
    padding: 10px 15px 2px;
    font-size: @font-size-small;
    color: @gray;
    text-transform: uppercase;

    &.disabled {
      .opacity(0.2);
    }
  }

  .modal-body-pane {
    height: 400px;
    padding: 20px;
    margin-left: @modal-nav-pane-width;
    overflow-x: hidden;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
  }
}

// Confirm Modal
// --------------------------------------------------

.confirm-modal {
  .modal-content {
    font-size: @font-size-large;
    line-height: 1.6em;
  }

  .confirm-message {
    word-wrap: break-word;
  }

  .modal-footer > .btn {
    min-width: 80px;
  }
}

// Channel Modal
// --------------------------------------------------

.channel-modal {
  .channel-details {
    float: right;
    width: 320px;
  }

  .channel-poster-container {
    @channel-poster-width: 200px;

    float: left;
    width: @channel-poster-width;
    margin-right: 20px;

    .channel-poster {
      height: @channel-poster-width;
      margin-bottom: 5px;
    }

    .btn {
      width: 100%;
      margin-top: 10px;
    }

    .btn-group {
      width: 100%;
      @channel-toggle-width: 40px;
      @channel-toggle-padding: 16px;

      > .btn {
        width: @channel-poster-width - @channel-toggle-width;
        padding-left: @channel-toggle-padding + @channel-toggle-width;

        .loading {
          margin-left: @channel-toggle-padding;
        }
      }

      > .btn.dropdown-toggle {
        width: @channel-toggle-width;
        padding: 6px @channel-toggle-padding;
      }
    }
  }
}

// Edit Metadata Modal
// --------------------------------------------------

.edit-lock-addon {
  color: @gray-darkest;
  background-color: @gray-dark;
  border-color: @gray-dark;

  &.locked {
    color: @white;
    background-color: @orange;
    border-color: @orange;
  }
}

// Artwork

.upload-artwork-container {
  @stripe-start-color: rgba(0, 0, 0, 0.05);
  @stripe-end-color: rgba(0, 0, 0, 0.15);
  @stripe-hover-start-color: rgba(255, 255, 255, 0.05);
  @stripe-hover-end-color: transparent;
  @stripe-success-start-color: darken(@brand-success, 38%);
  @stripe-success-end-color: darken(@brand-success, 40%);
  @stripe-error-start-color: darken(@brand-danger, 28%);
  @stripe-error-end-color: darken(@brand-danger, 30%);

  padding: 20px;
  margin: -20px -20px 20px;
  color: @gray-lighter;
  text-align: center;
  .stripes(@stripe-start-color, @stripe-end-color, 10px);
  border-bottom: 1px solid @gray-darkest;

  &.drag-hover {
    color: @gray-lightest;
    .stripes(@stripe-hover-start-color, @stripe-hover-end-color, 10px);
  }

  &.show-progress {
    .animation(barberpole-stripes 0.5s linear infinite);

    .upload-prompt {
      display: none;
    }

    .upload-progress {
      display: block;
    }
  }

  &.show-success {
    .stripes(@stripe-success-start-color, @stripe-success-end-color, 10px);

    .upload-prompt {
      display: none;
    }

    .upload-success {
      display: block;
    }
  }

  &.show-error {
    .stripes(@stripe-error-start-color, @stripe-error-end-color, 10px);

    .upload-prompt {
      display: none;
    }

    .upload-error {
      display: block;
    }
  }

  &.show-form {
    .upload-prompt {
      display: none;
    }

    .upload-form {
      display: block;
    }
  }

  .upload-progress,
  .upload-success,
  .upload-error,
  .upload-form {
    display: none;
  }
}

.upload-form-help {
  margin-top: 2px;
  .clearfix();
  font-size: @font-size-base - 1;
}

.upload-prompt-divider {
  margin: 0 5px;
  color: @gray-light;
}

.artwork-file-container {
  position: relative;
  display: inline-block;

  &:hover a {
    color: @link-hover-color;
  }
}

.artwork-file-control {
  position: absolute;
  left: -90px;
  width: 220px;
  height: 25px;
  cursor: pointer;
  .opacity(0.001);
}

.artwork-options-list {
  margin: 0 -26px -20px 0;
}

.artwork-option {
  position: relative;
  z-index: 0;
  display: inline-block;
  margin: 0 26px 20px 0;
  vertical-align: top;

  .media-poster {
    background-color: transparent;
  }

  .media-poster-image {
    position: relative;
    z-index: -1;
  }

  &.media-poster-container.selected {
    .media-poster {
      &:before {
        top: 0;
        right: 0;
        border-width: 0 50px 50px 0;
      }

      &:after {
        width: 30px;
        height: 30px;
        font-size: 18px;
        line-height: 30px;
      }
    }
  }
}

.poster > .artwork-option {
  .media-poster-image {
    width: 150px;
  }
}

.art > .artwork-option {
  .media-poster-image {
    width: 240px;
  }
}

.banner > .artwork-option {
  display: block;

  .media-poster-image {
    width: 100%;
  }
}

// Edit Section Modal
// --------------------------------------------------

.edit-section-modal {
  .edit-section-nav-list > li > a {
    display: block;
    padding: 10px 20px;
  }

  .visible-once-section-type-chosen {
    .transition(~'margin-top linear 0.2s, opacity linear 0.2s');
    opacity: 0;
    margin-top: 30px;
  }

  .section-type-chosen {
    .visible-once-section-type-chosen {
      opacity: 1;
      margin-top: 0;
    }
  }

  .section-type-title {
    .transition(color 0.8s);
    &.section-type-title-unstable {
      color: @orange-light;
      .transition(color 0s);
    }
  }

  .wizard-blocks-group {
    margin-top: 20px;
    text-align: center;

    > .wizard-block {
      display: inline-block;
      /* Reduce the padding just enough to fit the Games icon in single line
      with the other icon types */
      padding: 15px;
      color: @gray-dark;
      cursor: default;
      border-radius: @border-radius-base;

      &.selectable {
        color: @gray-lighter;
        cursor: pointer;

        &:hover {
          color: @gray-lightest;
          background-color: @gray-dark;
        }
      }

      &.selected,
      &.selected:hover {
        color: @orange-light;
      }

      > .wizard-block-icon {
        display: block;
        margin: 5px;
        font-size: 40px;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  .section-type-unsupported {
    display: none;
  }

  .section-type-supported {
    display: block;
  }

  .is-type-unsupported {  
    .section-type-unsupported {
      display: block;
    }

    .section-type-supported {
      display: none;
    }
  }

  .edit-section-folders-form {
    .form-group {
      margin-bottom: 10px;
    }

    .control-group {
      margin-top: 20px;
    }

    .form-control {
      max-width: none;

      &:focus ~ .path-actions > .btn-gray:hover {
        color: @gray;
      }
    }

    .control-error {
      margin: 10px;
      text-align: center;
    }

    .has-error .control-error {
      display: block;
    }

    .button-container {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-top: 20px;
    }

    .download-media-container {
      margin: 20px 0;
      padding-right: 100px;
      background-image: url(~desktop/common/img/vendor-logos/atari.svg);
      background-position: right;
      background-size: contain;
      background-repeat: no-repeat;

      /* stylelint-disable-next-line max-nesting-depth */
      input {
        margin-right: 10px;
      }
    }
  }

  .path-group {
    position: relative;

    .path-actions {
      position: absolute;
      top: 6px;
      right: 8px;
      line-height: 28px;
      font-size: 18px;
    }
  }

  .choose-library {
    cursor: pointer;
    border-radius: 2px;

    ul {
      padding-left: 1.5em;
    }

    input[type='radio'] {
      margin: 12px 10px 12px 0;
    }

    .vendor-logos {
      text-align: right;

      .vendor-logo-lyricfind {
        background-image: url(~common/img/vendor-logos/lyricfind.svg);
        height: 10px;
      }

      .vendor-logo-gracenote {
        background-image: url(~desktop/common/img/vendor-logos/gracenote.svg);
        height: 31px;
      }

      .vendor-logo-plex-pass {
        background-image: url(~common/img/plex-pass.svg);
        height: 44px;
      }

      .vendor-logo {
        margin: 8px 12px 16px 24px;
        background-size: contain;
        background-repeat: no-repeat;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .use-library-header {
      background-color: @gray-darkest;
      &:hover {
        background-color: @gray-dark;
      }
    }

    &.use-premium-library {
      .use-premium-library-body {
        padding: 16px 8px;
      }
    }

    &.use-basic-library {
      cursor: pointer;
    }

    > .row {
      margin-left: 0;
      margin-right: 0;
    }
  }
}

// Add Folder Modal
// --------------------------------------------------

.add-folder-modal {
  .directory-container-row {
    margin-top: 20px;
  }
}

// Media Info Modal
// --------------------------------------------------

.media-info-modal {
  .modal-body {
    padding: 5px 20px;
  }

  .position-ref {
    position: relative;
  }

  .media {
    overflow: auto;

    .props {
      position: relative;
      top: 0;
      float: left;
      width: 200px;
      padding-right: 20px;

      &.sticky {
        position: fixed;
        pointer-events: none; // Allow wheel & touch events bubble through fixed element
      }

      ul {
        padding-left: 0;
        font-size: 14px;
        color: @gray;

        li {
          overflow: hidden;
          text-overflow: ellipsis;
          list-style: none;
        }
      }

      .file {
        margin-bottom: 12px;
        overflow: hidden;
        color: @gray-lightest;
        text-overflow: ellipsis;
      }

      .detail-label {
        color: @gray-light;
      }

      .detail {
        margin-left: 5px;
        color: @gray-lightest;
      }
    }

    .part {
      margin-left: 200px;
      overflow-x: hidden;
      overflow-y: auto;
    }

    .streams {
      margin-left: 200px;
      overflow: auto;

      .props {
        float: none;
        width: auto;
      }
    }
  }

  .hide-streams-column {
    .props {
      width: 345px;
    }
    .part {
      margin-left: 345px;
    }
    .streams {
      display: none;
    }
  }

  .media,
  .part {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.files {
  .loading-container {
    position: relative;
    .loading {
      position: absolute;
    }
  }

  ul {
    padding-left: 0;

    li {
      list-style: none;
      color: @gray-light;

      .label {
        margin-left: 5px;
        padding: 0 10px;
      }
    }
  }
}

