  }
}

// Select Modal
// --------------------------------------------------

.select-modal {
  .empty-message {
    padding: 20px;
    font-size: 16px;
    color: @gray-light;
  }

  .message-container {
    height: 50px;
    margin: 20px -20px -20px;
    overflow: hidden;
  }

  .message {
    height: 100% !important;
    border-top: 1px solid @modal-footer-border-color;
    border-radius: 0;
    resize: none;

    &:focus {
      border-top: 1px solid @black;
    }
  }
}

// Recommend Modal
// --------------------------------------------------

.recommend-modal {
  .message-container {
    height: 80px;

    .transition(height 0.2s);

    &.invisible {
      visibility: visible;
      height: 0px;
    }
  }

  .characters-remaining {
    .pull-left();
    color: @gray-light;

    &.error {
      color: @brand-danger;
    }
  }
}

