  }

  .convert-warning-icon {
    float: left;
    padding: 10px 10px 10px 0;
    font-size: 24px;
    color: @gray;
  }

  .convert-warning-text {
    display: block;
    overflow: hidden;
  }

  .policy-label {
    margin-right: 10px;
  }

  .policy-value-control {
