// Keyboard Shortcuts Modal
// --------------------------------------------------

.keyboard-shortcuts-header {
  line-height: 2em;
  text-align: center;
  border-bottom: 1px solid @gray-dark;
}

.keyboard-shortcuts-list {
  // For vertical alignment control, the keyboard shortcut list
  // is displayed as a table, and its immediate children are
  // displayed as table cells with vertically-centered text.
  // We scope these rules to the specific children used.
  &.list li {
    display: table;
    margin-bottom: 5px;
    > .keyboard-shortcuts-keys,
    > .keyboard-shortcuts-label {
      display: table-cell;
      vertical-align: middle;
    }
  }
}

// Styles entries in the column displaying the
// keystrokes necessary to invoke a shortcut.
.keyboard-shortcuts-keys {
  min-width: 120px;
  text-align: center;

  // Styles descriptive text joining keystrokes
  // such as the `then` in `g then f`
  > .keyboard-shortcuts-keys-plain {
    font-size: 0.8em;
    .text-muted;
  }

  // Styles actual keyboard buttons such as the
  // `g` and `f` in `g then f`
  > .keyboard-shortcuts-keys-key {
    padding: 2px 8px;
    background-color: @gray-darkest;
    border-radius: @border-radius-base;
  }
}

// Release Notes
// --------------------------------------------------

.update-list-title {
  font-family: @font-family-bold;
}


// User Select Modal
// --------------------------------------------------

.user-select-modal-pin-container {
  .modal-footer {
