  }

  .close-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 15px;
    font-size: @font-size-large + 2;
  }

  .user-select-list {
    float: left;
    margin-right: 10px;
  }

  .pin-forgot-link {
    position: absolute;
    top: 8px;
    right: 20px;
  }

  .pin-digit-container {
    float: left;
    width: 390px;
    margin-top: 20px;

    &.shake {
      .animation(shake 0.4s);
    }
  }

  .pin-digit {
    float: left;
    margin-left: 4%;
    width: 22%;
    height: 115px;
    line-height: 115px;
    font-size: 72px;
    border-radius: 5px;
    cursor: text;
    background-color: @gray-dark;
    text-align: center;

    &:first-child {
      margin-left: 0;
    }

    &.current {
      background-color: white;
      color: @gray-dark;
    }
  }

  .pin-input {
    position: absolute;
    top: -1000px;
    width: 390px;
    font-size: 72px;
  }

  .help-block {
    margin: 20px 0 0;
    clear: both;
    font-size: @font-size-base;
    color: @gray-light;
  }
}

// Download Server Modal
// --------------------------------------------------

.server-download-modal {
  .download-server-pane {
    text-align: center;
    position: relative;
    min-height: 100%;
  }

  .more-info {
    padding-bottom: 40px;
  }

  p {
    color: @gray-light;
  }

  h2 {
    margin: 10px 0 10px;
  }

  .platform-logo {
    height: 110px;
    width: 100%;
    color: @gray-lighter;

    &.platform-asustor {
      background: url(~desktop/common/img/platform-logos/asustor.svg) center
        center no-repeat;
    }

    &.platform-drobo {
      background: url(~desktop/common/img/platform-logos/drobo.svg) center
        center no-repeat;
    }

    &.platform-freebsd {
      background: url(~desktop/common/img/platform-logos/freebsd.svg) center
        center no-repeat;
    }

    &.platform-linux {
      background: url(~desktop/common/img/platform-logos/linux.svg) center
        center no-repeat;
    }

    &.platform-macos {
      background: url(~desktop/common/img/platform-logos/mac.svg) center center
        no-repeat;
