.schedulePageContent {
  composes: pageContent from '~desktop/common/js/views/v3/components/pages/PageContent.css';

  display: flex;
  flex-direction: row;
  /*
  * overflow: hidden fixes an issue in Firefox that causes the height to not be
  * limited so inner content and the recording priority pane do not scroll.
  */
  overflow: hidden;
}

.schedulePageInnerContent {
  composes: innerPageContent from '~desktop/common/js/views/v3/components/pages/PageContent.css';

  padding-top: 20px;
}
