.link {
  /* composes: button from './source/SourceSidebarLink.css'; */

  display: inline-flex;
  align-items: center;
  justify-content: unset;
  padding: 0 0 0 $sourceSidebarLeftGutter;
  min-width: $sourceSidebarExpandedWidth;
  min-height: $sourceSidebarItemHeight;
  color: rgba(255, 255, 255, 0.75);
  text-transform: uppercase;
  font-size: $largeFontSize;
  font-family: $boldFontFamily;
}

.icon {
  top: 0;
}

.pinnedIcon {
  composes: icon;

  /* Need to align this narrower icon with other icons in sidebar */
  margin-left: 5px;
}

.unclaimedImage {
  margin-right: 6px;
  height: $defaultIconFontSize;
}

.topBorder {
  border-top: 2px solid rgba(255, 255, 255, 0.08);
}

.bottomBorder {
  border-bottom: 2px solid rgba(255, 255, 255, 0.08);
}
