.dotButton {
  composes: button from '~desktop/common/js/v4/core/ui/buttons/components/Button.css';

  padding: 4px;
  border-radius: 50%;
  background-color: transparent;
}

.dot {
  display: flex;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: $defaultLinkColor;
}

.dotIsSelected {
  composes: dot;

  background-color: $defaultColor;
}
