.externalMenuItemContainer {
  display: flex;

  &:hover {
    background-color: $menuItemHoverBackgroundColor;
  }
}

.externalMenuItem {
  composes: menuItemContainer from '~core/components/menus/SelectedMenuItem.css';

  flex-grow: 1;
  max-width: 100%;
  width: 0;

  &:hover {
    background-color: transparent;
  }
}

.menuLabelClassName {
  composes: menuLabel from '~core/components/menus/SelectedMenuItem.css';

  width: 100%;
}

.externalDeleteButton {
  composes: iconButton from '~desktop/common/js/v4/core/ui/buttons/components/IconButton.css';

  flex-shrink: 0;
  padding: 0 10px;

  &:hover {
    color: $dangerColor;
  }
}

.extendedTitle {
  @add-mixin truncate;
  display: block;
  color: $dimColor;
  font-size: $smallFontSize;
}
