$nextPrevButtonPaddingFromEdgeActive: 7px;
$nextPrevButtonPaddingFromEdgeInactive: 0;
$nextPrevButtonSize: 25px;
$nextPrevButtonNudgePadding: 3px;

.hub {
  position: relative;
  margin: $spotlightHubCellGap 0;
  height: $spotlightHubCellHeight;

  &:hover .iconButtonPrev {
    opacity: 1;
    transform: translate($nextPrevButtonPaddingFromEdgeActive, -50%);
  }

  &:hover .iconButtonNext {
    opacity: 1;
    transform: translate(calc($nextPrevButtonPaddingFromEdgeActive * -1), -50%);
  }
}

.cellItem {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  flex-shrink: 0;
  overflow: hidden;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity $defaultSpeed;
}

.cellItemFade {
  composes: cellItem;

  opacity: 0;
  pointer-events: none;
}

.dotsContainer {
  position: absolute;
  right: 50%;
  bottom: 8px;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: $defaultIconFontSize;
  transform: translateX(50%);
}

.iconButton {
  position: absolute;
  top: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: $nextPrevButtonNudgePadding;
  width: $nextPrevButtonSize;
  height: $nextPrevButtonSize;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.15);
  opacity: 0;
  transition: transform $defaultSpeed, opacity $defaultSpeed;
}

.iconButtonPrev {
  composes: button from '~core/components/buttons/IconButton.css';
  composes: iconButton;

  left: 0;
  padding-left: $nextPrevButtonNudgePadding;
  transform: translate($nextPrevButtonPaddingFromEdgeInactive, -50%);

  &:global(.isFocused) {
    opacity: 1;
    transform: translate($nextPrevButtonPaddingFromEdgeActive, -50%);
  }
}

.iconButtonNext {
  composes: button from '~core/components/buttons/IconButton.css';
  composes: iconButton;

  right: 0;
  padding-right: $nextPrevButtonNudgePadding;
  transform: translate(calc($nextPrevButtonPaddingFromEdgeInactive * -1), -50%);

  &:global(.isFocused) {
    opacity: 1;
    transform: translate(calc($nextPrevButtonPaddingFromEdgeActive * -1), -50%);
  }
}
