.textArea {
  composes: input from '~core/components/forms/input.css';

  overflow: hidden;
  background-color: $inputBackgroundColor;
  color: $inputColor;
  resize: none;
  transition: background-color $defaultSpeed;

  &:focus {
    outline: 0;
    background-color: $inputFocusBackgroundColor;
    color: $inputFocusColor;
  }
}

.small {
  padding: 2px 10px;
  font-size: $defaultFontSize;
}

.large {
  padding: 5px 10px;
  font-size: $largeFontSize;
}

.isDisabled {
  composes: isDisabled from '~core/components/forms/input.css';
}

.hasError {
  composes: hasError from '~core/components/forms/input.css';
}
