$thumbGap: 30px;

.container {
  display: flex;
  align-items: center;
  flex-direction: row;
  margin-bottom: 2px;
  padding-right: 20px;
  width: $liveTVSettingsChannelItemWidth;
  height: $liveTVSettingsChannelItemHeight;
  background-color: #2d2f31;
  color: $v4AltMedium;
  font-size: $largeFontSize;
}

.hdBadge {
  margin-left: 10px;
  height: 20px;
  background-color: $v4BaseLight;
  color: $v4AltMedium;
  font-size: 10px;
  line-height: 20px;
}

.thumb {
  margin-left: $thumbGap;
}

.vcn {
  padding-right: 10px;
  padding-left: 15px;
}

.vcnGap {
  margin-left: calc($thumbGap + $liveTVSettingsChannelThumbWidth);
}

.title {
  @add-mixin truncate;

  flex-grow: 1;
}

.channelSourceContainer {
  position: absolute;
  left: 0;
  display: flex;
  height: 100%;
}

.channelSource {
  margin-right: 2px;
  width: 4px;
  height: 100%;
}
