.form {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.manageChannelsBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.scanChannelsContainer {
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
  margin-bottom: 40px;
  min-height: 42px;
}

.scanChannelsMessage {
  margin-right: 70px;
  font-size: $smallFontSize;
}

.scroller {
  composes: vertical from '~core/components/base/Scroller.css';

  flex-grow: 1;
}

.centeredContainer {
  @add-mixin absoluteCenter;
}

.progressContainer {
  text-align: center;
}
