.channelMapping {
  display: flex;
  padding: 0 15px;
  height: 40px;
  color: rgba(255, 255, 255, 0.75);
  font-size: $largeFontSize;
  line-height: 40px;
}

.channelMapping:nth-of-type(odd) {
  background-color: $rowOddBackgroundColor;
}

.channelMapping:nth-of-type(even) {
  background-color: $rowEvenBackgroundColor;
}

.deviceInfo {
  display: flex;
  flex: 0 0 55%;
}

.channelEnabled {
  margin-top: 1px;
  width: 25px;
}

.deviceIdentifier {
  flex: 0 0 20%;
}

.mappingDisabled {
  color: #5c5c5c;
}

.channelName {
  flex: 1 0 auto;
}

.signalStrength {
  composes: signalStrength from '~desktop/common/js/views/v3/components/livetv/setup/SignalStrength.css';

  justify-content: center;
  flex: 0 0 100px;
}

.epgChannel {
  display: flex;
  align-items: center;
  flex: 0 0 45%;
}

.drmChannel {
  flex: 0 0 45%;
  text-align: right;
}

.drmChannelBadge {
  background-color: $blackDarkTranslucentBackgroundColor;
  color: rgba(255, 255, 255, 0.75);
  font-size: 11px;
  font-family: $boldFontFamily;
}

.resolutionContainer {
  margin-left: 15px;
  width: 30px;
  font-size: $smallFontSize;
  line-height: normal;
}
