.button {
  composes: button from '~core/components/buttons/Button.css';

  display: flex;
  align-items: center;
  padding: 0 15px;
  width: 100%;
  height: 40px;
  border-radius: initial;
  text-align: start;
  text-transform: none;

  &:nth-of-type(odd) {
    background-color: $rowOddBackgroundColor;
  }

  &:nth-of-type(even) {
    background-color: $rowEvenBackgroundColor;
  }

  &:hover {
    background-color: $quietHoverBackgroundColor;
  }
}

.selectedButton {
  composes: button;

  &:nth-of-type(odd),
  &:nth-of-type(even),
  &:hover {
    background-color: $menuItemActiveBackgroundColor;
  }
}

.avatar {
  composes: avatar from '~desktop/common/js/views/v3/components/core/images/AvatarImg.css';

  flex-shrink: 0;
  margin-right: 40px;
}

.titleContainer {
  @add-mixin truncate;

  flex-grow: 1;
  margin-right: 10px;
}

.title {
  color: rgba(255, 255, 255, 0.75);
  font-size: $largeFontSize;
}

.email {
  color: $dimColor;
  font-size: $smallFontSize;
}

.icon {
  top: 0;
  flex-shrink: 0;
}

.selectedIcon {
  composes: icon;

  margin: 0 15px;
  color: $successColor;
}

.homeIcon {
  composes: icon;

  visibility: hidden;
  color: $dimColor;
}

.visibleHomeIcon {
  visibility: visible;
}
