.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.settings {
  align-self: flex-start;
  flex-grow: 1;
}

.message {
  color: $dimColor;
  text-align: center;
  font-size: $largeFontSize;
}

.noChannelsIcon {
  @add-mixin absoluteCenter;
  font-size: 40px;
}

.clockContainer,
.noChannelsIconContainer {
  position: relative;
  width: 90px;
  height: 90px;
}

.clock {
  @add-mixin absoluteCenter;
  color: #3dc;
  font-size: 40px;
}

.clockTime {
  composes: clock;

  animation: blink steps(1, end) 1s infinite;
}

.clockBackground,
.noChannelsIconBackground {
  @add-mixin absoluteCenter;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.7);
}

@keyframes blink {
  0% {
    visibility: visible;
  }

  50% {
    visibility: hidden;
  }

  100% {
    visibility: visible;
  }
}
