.menuItemContainer {
  composes: menuItemContainer from '~core/components/menus/SelectedMenuItem.css';
}

.menuLabel {
  composes: menuLabel from '~core/components/menus/SelectedMenuItem.css';
}

.spinner {
  composes: spinner from '~desktop/common/js/v4/core/ui/spinners/components/Spinner.css';

  border-top-color: $selectedColor;
  border-left-color: $selectedColor;
}

.selectedIcon {
  composes: selectedIcon from '~core/components/menus/SelectedMenuItem.css';
}

.isSelected {
  color: $selectedColor;

  /* Keep the icon the selected color on hover. */
  .selectedIcon {
    color: $selectedColor;
  }
}
