.slideContainer {
  position: relative;
  width: 100%;
  height: 100%;
  filter: drop-shadow(0 0 5px $shadowColor);
}

.statusContainer {
  composes: fadeIn from '~desktop/common/js/views/v3/styles/animations/fadeIn.css';

  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
