.devicesContainer {
  display: flex;
  flex-direction: row;
  padding-bottom: $modalBodyPadding;
}

.modalBody {
  composes: modalScroller from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.innerModalBody {
  composes: innerModalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.spinnerContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  margin-top: -$modalBodyPadding;
  height: 100%;
}
