.card {
  composes: card from '~desktop/common/js/v4/core/ui/cards/PosterCard.css';

  padding: 20px;
  border-radius: 4px;
}

.details {
  display: flex;
}

.ratingImg {
  margin-right: $v4CardTitleGap;
  width: 40px;
  height: 40px;
}

/* stylelint-disable value-no-vendor-prefix, property-no-vendor-prefix */
.reviewContainer {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
}
/* stylelint-enable */

.titleContainer {
  padding-bottom: $v4CardTitleGap;
}

.title {
  composes: singleLineTitle from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardTitle.css';
}

.author {
  composes: title;
  composes: isSecondary from '~desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardTitle.css';
}
