.container {
  display: flex;
  align-items: center;

  a {
    color: $defaultColor;

    &:hover {
      color: $defaultColor;
      text-decoration: underline;
    }
  }
}

.card {
  composes: card from '~desktop/common/js/v4/core/ui/cards/PosterCard.css';

  flex-shrink: 0;
  margin-right: 20px;
}

.placeholderCard {
  composes: placeholder from './MetadataPosterCardPlaceholder.css';

  flex-shrink: 0;
  margin-right: 20px;
}

.placeholderTitle {
  margin-bottom: 5px;
  width: 100%;
  height: $largeFontSize;
  background: $v4CardBackgroundColor;
}

.placeholderSubtitle {
  composes: placeholderTitle;

  width: 75%;
  height: $defaultFontSize;
}

.titlesContainer {
  overflow: hidden;
  width: 100%;
}

.title {
  display: block;
  overflow: hidden;
  width: 100%;
}

.doubleTitle {
  @add-mixin doubleLineTruncate;
  composes: title;

  font-size: $largeFontSize;
}

.subtitle {
  @add-mixin truncate;
  composes: title;

  color: $dimColor;

  a {
    color: $dimColor;
  }
}

@keyframes cardShimmer {
  0% {
    background-color: $v4CardBackgroundColor;
  }

  100% {
    background-color: $v4CardShimmerBackgroundColor;
  }
}
