.alternate {
  background-color: $stripeListAlternateBackgroundColor;
}

.container {
  display: flex;
  align-items: center;
  flex: 1;
  flex-direction: row;
  overflow: hidden;
  padding: 5px 30px;

  &:hover {
    background: $stripeListHoverBackgroundColor;
  }
}

.card {
  composes: card from '~desktop/common/js/views/v3/components/core/images/PosterCard.css';

  flex-shrink: 0;
}

.title {
  @add-mixin truncate;
  padding-left: 15px;
}
