$sourceSidebarBackground: $darkOpaqueBackgroundColor;
$sourceSidebarExpandedBackground: $darkOpaqueBackgroundColor;
$sourceSidebarButtonPadding: 5px; /* give some padding so clickable target is bigger */
$toggleMenuButtonIconSize: 16px; /* these icons are largish, so using a custom size */
$transitionTiming: cubic-bezier(0.4, 0, 0.2, 1);
$transitionMaxWidth: max-width $defaultSpeed $transitionTiming;

.sidebarUnderlay {
  min-width: $sourceSidebarWidth;
}

.sidebar {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: $sourceSidebarBackground;
}

.collapsedSidebar {
  composes: sidebar;

  position: absolute;
  overflow: hidden;
  min-width: $sourceSidebarWidth;
  max-width: $sourceSidebarWidth;
  width: $sourceSidebarWidth;
  transition: $transitionMaxWidth, background-color 1s ease-out;
}

.expandedSidebar {
  composes: sidebar;

  position: absolute;
  z-index: $sidebarZIndex;
  overflow: hidden;
  max-width: $sourceSidebarExpandedWidth;
  width: $sourceSidebarExpandedWidth;
  background-color: $sourceSidebarExpandedBackground;
  transition: $transitionMaxWidth, background-color $defaultSpeed ease-in;
}

.openSidebar {
  composes: sidebar;

  position: relative;
  overflow: hidden;
  min-width: $sourceSidebarExpandedWidth;
  width: $sourceSidebarExpandedWidth;
  transition: min-width $defaultSpeed $transitionTiming;
}

.expandedSidebar,
.openSidebar {
  .pane {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    /* Prevent content from truncating during the expand animation. */
    width: $sourceSidebarExpandedWidth;
  }
}

.heading {
  flex-grow: 0;
  flex-shrink: 0;
  height: $sourceSidebarItemHeight;
}

.footer {
  background-color: rgba(0, 0, 0, 0.2);
}

.toggleMenuButton {
  /* Need to align with the navigation icons; so take into account the padding
   * and icon size difference */
  margin-left: 12px;
  padding: $sourceSidebarButtonPadding;
  height: $sourceSidebarItemHeight;
  font-size: $toggleMenuButtonIconSize;
}

.toggleMenuIcon {
  padding: 4px;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  background-color: $lightTranslucentBackgroundColor;
}
