.container {
  display: flex;
}

.panel {
  flex: 1;
  padding: 0 10px;
  text-align: center;
}

.panelIcon {
  color: $primaryColor;
  font-size: 100px;
}

.panelTitle {
  padding: 20px 0;
  font-size: $largeFontSize;
  font-family: $boldFontFamily;
}

.panelDescription {
  color: $dimColor;
  font-size: $defaultFontSize;
  font-family: $defaultFontFamily;
}
