.container {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.modalBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.content {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  margin-bottom: 20px;
  min-height: 42px;
  font-size: $smallFontSize;
}

.inviteLink {
  display: flex;
}

.input {
  composes: input from '~core/components/forms/TextInput.css';

  flex-grow: 1;
  max-width: none;

  &:focus {
    background-color: $inputBackgroundColor;
    color: $inputColor;
  }
}

.copyLink {
  composes: link from '~core/components/buttons/Link.css';

  flex-shrink: 0;
  margin-left: 10px;
  min-width: 60px;
  text-align: center;
}

.modalFooter {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  justify-content: space-between;
}
