.overlay {
  @add-mixin linkOverlay;
  display: flex;
  padding: $summaryCellPadding;

  a {
    color: $defaultColor;

    &:hover {
      text-decoration: underline;
    }
  }
}

.underlay {
  @add-mixin cover;
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';
}

.summaryContainer {
  display: flex;
  align-items: center;
  flex-grow: 1;
  width: 100%;
}

.posterContainer {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  margin-right: 20px;
  width: 144px;
  pointer-events: none;
}

.playPosterContainer {
  composes: posterContainer;

  pointer-events: all;
}

.playCircle {
  composes: playCircle from '~app/desktop/common/js/v4/core/metadata/components/cards/MetadataPosterCardActions.css';

  width: 26px;
  height: 26px;
}

.titlesContainer {
  flex-grow: 1;
  overflow: hidden;
  padding-right: 20px;
  width: 100%;
  font-size: $largeFontSize;
}

.titleContainer {
  display: block;
}

.title {
  overflow: hidden;
  width: 100%;
}

.doubleTitle {
  @add-mixin doubleLineTruncate;
  composes: title;
}

.subtitle {
  @add-mixin truncate;
  composes: titleContainer;
  composes: title;

  color: $dimColor;
  font-size: $defaultFontSize;

  a {
    color: $dimColor;
  }
}

.actions {
  flex-shrink: 0;
}

.actionsButton {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  padding: 10px;
}
