.completeIcon,
.recordingIcon,
.errorIcon {
  margin-right: 5px;
  font-size: $defaultIconFontSize;
}

.completeIcon {
  color: $successColor;
}

.recordingIcon {
  color: $recordColor;
}

.errorIcon {
  color: $warningColor;
}

.separator {
  composes: separator from '~core/components/base/DashSeparator.css';

  color: $dimColor;
}

.summary {
  font-size: $largeFontSize;
}
