.container {
  display: flex;
  overflow: hidden;
  margin-right: $breadcrumbMargin;
  height: 100%;
}

.tooltipButton {
  composes: toolbarButton from '~core/components/buttons/ToolbarButton.css';

  /* We want the button to take up the space of the page padding, so instead of
  using page padding make the button take the padding width */
  width: $v4PagePadding;
}

.noButton {
  width: $v4PagePadding;
}
