//
// Posters
// --------------------------------------------------


// Base styles
// --------------------------------------------------

.media-poster-container {
  position: relative;
  .backface-visibility(hidden); // Prevent flicker during transition.
}

.user-poster,
.media-poster,
.media-poster-fade {
  position: relative;
  overflow: hidden;
  background-color: @gray-dark;
  background-position: center;
  background-size: cover;
  .box-shadow(~"0 0 4px rgba(0, 0, 0, 0.3), inset 0 0 0 1px rgba(255, 255, 255, 0.1)");
}

.artist .media-poster:not(.placeholder-folder) {
  // Position the image to show the artist's face.
  background-position: center top;
}

.active > .media-poster-container .media-poster,
.dim > .media-poster-container .media-poster {
  .box-shadow(~"0 0 4px rgba(0, 0, 0, 0.3), inset 0 0 0 1px rgba(0, 0, 0, 0.5)");
}

.active > .media-poster-container .media-poster-overlay,
.dim > .media-poster-container .media-poster-overlay {
  display: block;
}


// Hover styles
// --------------------------------------------------

.force-hover .media-poster-container,
.media-poster-container:hover,
.media-poster-container:focus {
  .media-poster,
  .media-poster-fade {
    @box-shadow: 0 0 4px rgba(0, 0, 0, 0.3), inset 0 0 0 2px @orange;
    .box-shadow(@box-shadow);
  }

  .media-progress {
    height: 7px;
    .opacity(0.5);
  }

  .media-progress-track {
    display: none;
  }
}

.media-poster-container.selected {
  .media-poster {
    @box-shadow: 0 0 4px rgba(0, 0, 0, 0.3), inset 0 0 0 3px @orange-light;
    .box-shadow(@box-shadow);

    &:before {
      position: absolute;
      top: 1px;
      right: 1px;
      z-index: 1;
      display: block;
      width: 0;
      border-color: @orange-light @orange-light transparent transparent;
      border-style: solid;
      border-width: 0 80px 80px 0;
      content: '';
    }

    &:after {
      position: absolute;
      top: 0;
      right: 0;
      z-index: 1;
      display: block;
      width: 0;
      width: 50px;
      height: 50px;
      font-family: 'Glyphicons Regular';
      font-size: 24px;
      line-height: 50px;
      color: darken(@orange, 15%);
      text-align: center;
      content: '\E207';
    }
  }

  .media-poster-actions {
    display: none;
  }

  .media-progress {
    display: none;
  }
}

.force-hover .media-poster-container,
.media-poster-container:hover {
  .media-poster-overlay,
  .media-poster-top-overlay {
    top: 2px;
    right: 2px;
    bottom: 2px;
    left: 2px;
  }

  .media-poster-overlay {
    .opacity(1);
  }

  .media-poster-actions {
    .translate(0, -50px);
  }
}

.media-poster-container.selected {
  .media-poster-overlay {
    top: 2px;
    right: 2px;
    bottom: 2px;
    left: 2px;
  }
}

.hide-actions .media-poster-container {
  .media-poster-overlay {
    .opacity(0);
  }

  .media-poster-actions {
    display: none;
  }

  .media-progress {
    display: none;
  }
}

.media-poster-container.disabled {
  cursor: default;

  .media-poster {
    .box-shadow(none);
  }

  .media-poster-overlay {
    .opacity(0);
  }

  .media-poster-actions {
    .translate(0, 0);
  }
}


// Overlays
// --------------------------------------------------

.media-poster-overlay,
.media-poster-top-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  #gradient > .vertical(@start-color: rgba(0, 0, 0, 0), @end-color: @black, @start-percent: 30%);
  .opacity(0);
  .transition(opacity 0.2s);
}

.media-poster-top-overlay {
  display: block;
  #gradient > .vertical(@start-color: @black, @end-color: rgba(0, 0, 0, 0), @start-percent: 30%);
  .opacity(0.5);
}

.active > .media-poster-container .media-poster-overlay,
.dim > .media-poster-container .media-poster-overlay {
  #gradient > .vertical(@start-color: rgba(0, 0, 0, 0.6), @end-color: rgba(0, 0, 0, 0.8));
  .opacity(1);
}

.media-poster-container.hide-actions .media-poster-overlay {
  display: none;
}

.media-poster-overlay-icons {
  .centered();
}

.media-poster-overlay-icon {
  padding: 0 10px;
  font-size: 24px;
  color: @white;
  .opacity(0.7);

  &.warning {
    font-size: 28px;
    color: @orange-light;
    .opacity(1);
  }
}


// Loading
// --------------------------------------------------

.media-poster-container .loading {
  display: none;
}

.active > .media-poster-container .loading {
  display: block;
}



// Actions
// --------------------------------------------------

.media-poster-actions {
  position: absolute;
  right: 0;
  bottom: -50px;
  left: 0;
  font-size: 0; // Fixes the gap between inline-block elements.
  text-align: center;
  .transition(all 0.2s);
}

.media-poster-btn {
  padding: 10px 6px;
  font-size: @font-size-large + 2;
  color: @white;
  .transition(color 0.2s);
}

.active > .media-poster-container .media-poster-actions {
  display: none;
}


// Badges
// --------------------------------------------------

.media-poster > .badge {
  position: absolute;
  top: 10px;
  .box-shadow(0 0 10px rgba(0, 0, 0, 0.5));
}

.media-count-badge {
  left: 10px;
  background-color: #495a88;
}

.unwatched-count-badge {
  right: 10px;
}

.smart-badge {
  right: 10px;
  background-color: rgba(0, 0, 0, 0.8);
  > .icon {
    top: 4px;
  }
}

.video-badge {
  right: 10px;
  background-color: rgba(0, 0, 0, 0.8);
  > i {
    top: 4px;
  }
}


// Collection Poster
// --------------------------------------------------

.media-poster-container.collection-poster-container {
  .media-poster-overlay {
    display: block;
    #gradient > .vertical(@start-color: rgba(0, 0, 0, 0.7), @end-color: rgba(0, 0, 0, 0.9));
    .opacity(1);
  }

  .collection-title-container {
    position: absolute;
    top: 0;
    left: 0;
    display: table;
    width: 100%;
    height: 100%;
  }

  .collection-title-container .collection-title {
    display: table-cell;
    font-family: @font-family-bold;
    color: @white;
    text-align: center;
    text-transform: uppercase;
    vertical-align: middle;
  }
}


// Sync Status
// --------------------------------------------------

.sync-status {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 56px;
  height: 56px;
  margin: -28px 0 0 -28px;
  font-size: @font-size-large + 4;
  line-height: 56px;
  color: @white;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 50%;
  .transition(margin-top 0.2s);

  .sync-status-transcode {
    width: 100%;
    height: 100%;
  }

  .circle-progress {
    background-color: transparent;
  }

  .circle-progress-text {
    font-size: @font-size-small;
  }

  .sync-status-icon {
    width: 22px;
  }
}

.force-hover .sync-status,
.media-poster-container:hover .sync-status,
.media-poster-container:focus .sync-status {
  margin-top: -50px;
}


// Media queries
// --------------------------------------------------

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .media-poster-overlay-icons.with-actions {
    margin-top: -4px;
  }

  .media-poster-overlay-icon {
    font-size: 28px;

    &.warning {
      font-size: 32px;
    }
  }

  .media-poster-btn {
    padding: 12px;
  }
}
