.statusButton {
  composes: default from '~core/components/buttons/Button.css';

  margin-top: -1px;
  margin-right: $prePlayContentPadding;
  background-color: $blackDarkTranslucentBackgroundColor;
  color: $defaultLinkColor;

  &:hover {
    background-color: $blackDarkTranslucentBackgroundColor;
    color: $defaultLinkHoverColor;
  }

  &:global(.isActive) {
    background-color: $blackDarkerTranslucentBackgroundColor;
  }
}
