.summary {
  max-width: $prePlaySummaryMaxWidth;
  color: $v4DefaultColor;
  font-size: $largeFontSize;

  p {
    margin: 0 0 calc($largeFontSize * $lineHeightComputed);

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  a {
    color: $defaultLinkColor;
  }

  a:hover {
    color: $defaultLinkHoverColor;
    text-decoration: underline;
  }
}
