.container {
  display: inline-block;
  vertical-align: top;
}

.rating {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
}

.rt {
  display: flex;
  align-items: center;
  justify-content: flex-start;

  .rating:nth-of-type(2) {
    margin-left: $prePlayRatingBase;
  }
}

.ratingImage {
  margin-right: calc(0.3 * $prePlayRatingBase);
  width: calc(1.2 * $prePlayRatingBase);
  background-position: right center;
  background-size: contain;
  background-repeat: no-repeat;
}

.igdb {
  composes: ratingImage;

  width: calc(2.5 * $prePlayRatingBase);
  background-image: url('~common/img/ratings/igdb-logo.svg');
  background-size: cover;
}

.imdb {
  composes: ratingImage;

  width: calc(2.5 * $prePlayRatingBase);
  background-image: url('~common/img/ratings/imdb-logo-solid-560.svg');
}

.thetvdb {
  composes: ratingImage;

  width: calc(2.2 * $prePlayRatingBase);
  background-image: url('~common/img/ratings/tvdb-logo.svg');
  background-size: cover;
}

.tmdb {
  composes: ratingImage;

  width: calc(2.5 * $prePlayRatingBase);
  background-image: url('~common/img/ratings/tmdb-logo-short-560.svg');
}

.certified {
  composes: ratingImage;

  background-image: url('~common/img/ratings/rt-certified-fresh-560.svg');
}

.ripe {
  composes: ratingImage;

  background-image: url('~common/img/ratings/rt-fresh-tomato-560.svg');
}

.rotten {
  composes: ratingImage;

  background-image: url('~common/img/ratings/rt-rotten-splat-simple-560.svg');
}

.spilled {
  composes: ratingImage;

  width: calc(1.4 * $prePlayRatingBase);
  background-image: url('~common/img/ratings/rt-spilt-popcorn-simple-560.svg');
}

.upright {
  composes: ratingImage;

  background-image: url('~common/img/ratings/rt-fresh-popcorn-simple-560.svg');
}

.other {
  composes: ratingImage;

  background-image: url('~common/img/ratings/star-full-560.svg');
  opacity: 0.5;
}
