.listContainer {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
  /*
    When flex container has content, the minimum width is computed to the size
    of its contents. Set min-height to 0 so that the size of the container will
    take up the space of its parent
    See: https://ishadeed.com/article/min-max-css/#setting-min-width-to-zero-with-flexbox
  */
  min-height: 0;
}

.pageContentScroller {
  composes: pageContentScroller from '~desktop/common/js/v4/core/ui/pages/components/PageContent.css';
}
