.visibleContainer {
  position: absolute;
  z-index: $tooltipZIndex;
  opacity: 1;
  transition: opacity $defaultSpeed $motionEasing $defaultSpeed;
  pointer-events: none;
}

.hiddenContainer {
  composes: visibleContainer;

  opacity: 0;
  transition-delay: 0;
}

.thumbnail {
  background: $thumbnailBackgroundColor;
  box-shadow: 0 0 4px 0 $seekBarThumbnailShadowColor;
}

.tooltip {
  position: absolute;
  bottom: $seekBarTooltipMarginBottom;
  height: $seekBarTooltipHeight;
  border-radius: 4px;
  background-color: $seekBarTooltipBackgroundColor;
  box-shadow: 0 0 4px 0 $seekBarTooltipShadowColor;
  text-align: center;
}

.small {
  width: $seekBarTooltipSmallWidth;
}

.large {
  width: $seekBarTooltipLargeWidth;
}
