$playPauseButtonSize: 45px;

.container {
  position: relative;
  height: 100%;
}

.title {
  padding-bottom: 20px;
  height: $postPlayTitleHeight;
  text-transform: uppercase;
  font-size: $largeFontSize;
  font-family: $boldFontFamily;
}

.poster {
  position: relative;
}

.progress {
  @add-mixin absoluteCenter;
}

.playButton {
  @add-mixin cover;
  composes: playButton from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';

  opacity: 0;
  transition: opacity $defaultSpeed, transform $defaultSpeed;
}

.poster:hover .playButton {
  opacity: 1;
}

.playCircle {
  @add-mixin absoluteCenter;
  composes: playCircle from '~desktop/common/js/views/v3/components/core/buttons/PlayButton.css';

  width: $playPauseButtonSize;
  height: $playPauseButtonSize;
  border-color: $primaryBackgroundColor;
  background-color: $primaryBackgroundColor;
  box-shadow: 0 0 4px $playerShadowColor;
  color: $defaultLinkHoverColor;
  font-size: 20px;
  line-height: $playPauseButtonSize;
}

.timeLeft {
  @add-mixin absoluteCenter;
  font-size: 24px;
}

.autoPlay {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  height: $postPlayAutoPlayHeight;
  background: $playerPosterActionColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
}

.autoPlayOn {
  composes: autoPlay;

  color: $selectedColor;
}

.autoPlayOff {
  composes: autoPlay;
}
