.modalContent {
  composes: modalContent from '~desktop/common/js/v4/core/ui/modals/components/ModalContent.css';
}

.innerModalBody {
  composes: innerModalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.inputGroup {
  max-width: $inputMaxWidth;
  width: 100%;
}

.verifyAnotherWayLink {
  margin-top: 30px;
}

.sendCodeContainer {
  margin-bottom: 30px;
}

.sendCodeMessage {
  margin-bottom: 30px;
  color: $defaultColor;
}

.hidden {
  opacity: 0;
  transition: opacity $defaultSpeed;
}
