.form {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.modalBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.scroller {
  composes: vertical from '~core/components/base/Scroller.css';

  flex-grow: 1;
}

.message {
  margin-bottom: 15px;
  font-size: $smallFontSize;
}

.section {
  margin: 0 15px 15px;
}

.list {
  margin: 0 15px;
}

.modalFooter {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  justify-content: space-between;
}

.warningMessage {
  composes: message;

  padding: 10px;
  border-radius: 4px;
  background-color: $warningColor;
  color: $inverseColor;
}

.warningIcon {
  margin-right: 10px;
  font-size: $largeFontSize;
}
