.container {
  display: flex;
  align-items: center;
  height: 100%;
}

.button {
  composes: button from '~desktop/common/js/views/v3/components/header/PageHeaderTabButton.css';

  max-width: none;
}

.buttonContents {
  display: flex;
  align-items: center;
}

.title {
  @add-mixin truncate;

  max-width: 250px;
}

.disclosureArrow {
  composes: disclosureArrow from '~core/components/base/DisclosureArrow.css';

  margin: 0 0 0 5px;
}

.isSelected {
  border-color: $selectedColor;
}
