.controls {
  composes: controls from '~desktop/common/js/views/v3/components/players/core/PlayerControls.css';
}

.buttonGroup {
  composes: buttonGroup from '~desktop/common/js/views/v3/components/players/core/PlayerControls.css';
}

.buttonGroupLeft {
  composes: buttonGroupLeft from '~desktop/common/js/views/v3/components/players/core/PlayerControls.css';
}

.buttonGroupCenter {
  composes: buttonGroupCenter from '~desktop/common/js/views/v3/components/players/core/PlayerControls.css';
}

.buttonGroupRight {
  composes: buttonGroupRight from '~desktop/common/js/views/v3/components/players/core/PlayerControls.css';

  /*
  Autoprefix as of 9.7.6 does not handle vendor prefixing for line-clamp. This
  approach was adopted from: https://css-tricks.com/almanac/properties/l/line-clamp/
  */
  /* stylelint-disable value-no-vendor-prefix, property-no-vendor-prefix */
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  /* stylelint-enable value-no-vendor-prefix, property-no-vendor-prefix */

  overflow: hidden;
  padding-left: 20px;
}

.balanceLeft {
  composes: balanceLeft from '~desktop/common/js/views/v3/components/players/core/PlayerControls.css';
}

.button {
  composes: button from './WatchTogetherLobbyControlButton.css';

  margin-left: 15px;

  &:first-child {
    margin-left: 0;
  }
}

.hasLandscapePoster {
  composes: hasLandscapePoster from '~desktop/common/js/views/v3/components/players/core/PlayerControls.css';
}
