.bar {
  position: absolute;
  right: 0;
  left: 0;
  transition: transform $defaultSpeed, background $defaultSpeed;
}

.topBar {
  composes: bar;

  top: -$playerTopControlsHeight;
  height: $playerTopControlsHeight;
  transform: translateY($playerTopControlsHeight);
}

.bottomBar {
  composes: bar;
}

.content {
  position: absolute;
  top: $playerTopControlsHeight;
  right: 0;
  left: 0;
}

.spinnerContainer {
  @add-mixin cover;

  display: flex;
  align-items: center;
  justify-content: center;
}

.overlayButton {
  $buttonColor: #666;

  composes: button from '~core/components/buttons/Button.css';

  position: absolute;
  background-color: $buttonColor;
  box-shadow: 0 0 4px $playerShadowColor;

  &:hover {
    background-color: lighten($buttonColor, 0.1);
  }
}

/*
 * States
 */

.videoTopBar {
  background-color: $playerTopControlsBackgroundColor;
}

.playQueueTopBar {
  background-color: rgba(35, 36, 38, 0);
}

.topBarHidden {
  transform: translateY(0);
}

.bottomBarHidden {
  transform: translateY(0);
}
