.form {
  display: flex;
  flex-grow: 1;
  width: 100%;
}

.modalBody {
  composes: modalBody from '~core/components/modals/ModalBody.css';

  display: flex;
  flex-direction: column;
}

.message {
  margin-bottom: 20px;
  font-size: $smallFontSize;
}

.spinnerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.scroller {
  composes: vertical from '~core/components/base/Scroller.css';

  flex-grow: 1;
  height: 200px;
}

.modalFooter {
  composes: modalFooter from '~core/components/modals/ModalFooter.css';

  justify-content: space-between;
}
