.container {
  position: relative;
  display: flex;
  margin-top: -1px;
  margin-left: calc($channelGridTitleColumnWidth - 1px);
  padding: 10px 20px 10px 10px;
  width: calc(
    100% - $channelGridTitleColumnWidth - $channelGridChannelOverflowWidth
  );
  border-right: $channelGridBorderSize solid $channelGridBorderColor;
  border-bottom: $channelGridBorderSize solid $channelGridBorderColor;
  background-color: $channelGridSelectedItemDetailsBackgroundColor;
}

.preplayContainer {
  overflow: hidden;
  margin-left: 20px;
}

.poster {
  composes: card from '~desktop/common/js/views/v3/components/core/images/PosterCard.css';

  flex: 0 0 auto;
}

.navigationContainer {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 16px;
}
