.title {
  display: block;
  color: $defaultColor;
  line-height: $posterTitleHeight;
  cursor: default;
  user-select: none;
}

a.title,
.title a {
  cursor: pointer;

  &:hover {
    text-decoration: underline;
  }
}

.singleLineTitle {
  @add-mixin truncate;
  composes: title;

  height: $posterTitleHeight;
}

/* stylelint-disable value-no-vendor-prefix, property-no-vendor-prefix */
.doubleLineTitle {
  @add-mixin doubleLineTruncate;
  composes: title;

  max-height: calc($posterTitleHeight * 2);
}
/* stylelint-enable */

.isSecondary,
.isSecondary a {
  color: $dimColor;
}

.isSplitLine {
  max-width: 70%;
}
