.multiSelectPageHeader {
  composes: pageHeader from '~desktop/common/js/views/v3/components/header/PageHeader.css';

  padding-right: 5px;
  box-shadow: 0 2px 0 0 $selectedColor;
}

.selectedTitle {
  @add-mixin truncate;
  margin-right: $breadcrumbMargin;
  color: $selectedColor;
  font-family: $boldFontFamily;
}

.deselectLink {
  @add-mixin truncate;
}

.icon {
  top: -1px;
  margin-right: 6px;
  font-size: $defaultIconFontSize;
}
