.container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.channelInfoContainer {
  display: flex;
  align-items: center;
  align-self: stretch;
  flex: 0 0 200px;
  overflow: hidden;
  padding: 16px 20px;
  background-color: rgba(255, 255, 255, 0.01);
}

.channelTitle {
  @add-mixin truncate;
  color: $dimColor;
}

.cardContainer {
  flex-shrink: 0;
  margin: 0 14px 0 0;
  width: 64px;
}

.titlesContainer {
  display: flex;
  width: 100%;
}

.titles {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.airTimeContainer {
  padding-left: 20px;
  color: $dimColor;
  white-space: nowrap;
}

.posterPlayCircle {
  composes: playCircle from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterButtonCard.css';

  border-color: $primaryBackgroundColor;
  background-color: $primaryBackgroundColor;
  color: $defaultLinkHoverColor;
}
