.button {
  @add-mixin truncate;
  composes: button from '~core/components/buttons/Button.css';

  margin: 0 $pageHeaderTabButtonLeftRightMargin;
  padding: 5px 20px;
  max-width: 250px;
  background-color: transparent;
  color: $dimColor;
  line-height: 30px;

  &:hover {
    background-color: $blackDarkerTranslucentBackgroundColor;
  }
}

.isSelected {
  background-color: $blackDarkTranslucentBackgroundColor;
  color: $selectedColor;
}
