.unwatchedCircle {
  composes: unwatchedCircle from './MetadataTitleProgress.css';

  &.isPartiallyWatched {
    border-radius: 0.5em 0 0 0.5em;
  }
}

/*
 * Sizes
 */

.small {
  width: 0.8em;
  height: 0.8em;

  &.isPartiallyWatched {
    width: 0.4em;
  }
}

.medium {
  width: 1em;
  height: 1em;

  &.isPartiallyWatched {
    width: 0.5em;
  }
}
