.scroller {
  scrollbar-color: $scrollbarBackgroundColor transparent;
  -webkit-overflow-scrolling: touch;
}

.scroller::-webkit-scrollbar {
  width: 14px;
}

.scroller::-webkit-scrollbar-track {
  background-color: transparent;
}

.scroller::-webkit-scrollbar-thumb {
  min-height: 50px;
  border: 3px solid transparent;
  border-radius: 8px;
  background-color: $scrollbarBackgroundColor;
  background-clip: padding-box;
}

.scroller::-webkit-scrollbar-thumb:hover {
  background-color: $scrollbarHoverBackgroundColor;
}

.scroller::-webkit-scrollbar-thumb:window-inactive {
  background-color: $scrollbarInactiveBackgroundColor;
}

/*
 * Directions
 */

.none {
  overflow-x: hidden;
  overflow-y: hidden;
}

.vertical {
  overflow-x: hidden;
  overflow-y: scroll;
  padding-right: 5px;

  &.auto {
    overflow-y: auto;
  }
}

.horizontal {
  overflow-x: scroll;
  overflow-y: hidden;
  padding-bottom: 5px;

  &.auto {
    overflow-x: auto;
  }
}

.omni {
  overflow-x: scroll;
  overflow-y: scroll;
  padding-right: 5px;
  padding-bottom: 5px;

  &.auto {
    overflow-x: auto;
    overflow-y: auto;
  }
}

.compactScroller {
  padding: 0;
}
