.event {
  position: relative;
  margin: 4px 2px;
  background-color: $stripeListAlternateBackgroundColor;
  font-size: 11px;

  &:hover {
    background-color: $stripeListHoverBackgroundColor;
  }
}

.isComplete {
  background-color: $calendarEventSuccessBackgroundColor;

  &:hover {
    background-color: $calendarEventSuccessHoverBackgroundColor;
  }
}

.hasError {
  background-color: $calendarEventErrorBackgroundColor;

  &:hover {
    background-color: $calendarEventErrorHoverBackgroundColor;
  }
}

.underlay {
  @add-mixin cover;
}

.overlay {
  @add-mixin linkOverlay;
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 8px 13px;

  .errorIcon {
    pointer-events: all;
  }
}

.title {
  @add-mixin truncate;
  flex-shrink: 0;
  color: $defaultColor;
}

.secondaryInfo {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  flex-grow: 1;
}

.time {
  color: $dimColor;
  white-space: nowrap;
}

.status {
  display: flex;
  align-self: flex-end;
  justify-content: center;
  flex-shrink: 0;
  margin-top: 5px;
  min-width: 24px;
}

.recording {
  position: relative;
}

.recordingProgressBackground {
  composes: circularProgressBarBackground from '~desktop/common/js/views/v3/components/core/CircularProgressBar.css';

  background-color: rgba(0, 0, 0, 0.2);
}

.recordingIcon {
  @add-mixin absoluteCenter;
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: $recordingIconColor;
}
