.poster {
  overflow: hidden;
  border-radius: $v4CardBorderRadius;
  background-color: $v4CardBackgroundColor;
  box-shadow: 0 0 $v4CardShadowSize $v4CardShadowColor;
}

.face {
  @add-mixin cover;
  composes: poster;

  padding: inherit;
  backface-visibility: hidden;

  &:hover {
    box-shadow: 0 0 0 1px $v4CardHoverBorderColor,
      0 0 $v4CardShadowSize $v4CardShadowColor;
  }
}

.isSelected {
  box-shadow: 0 0 0 2px $v4CardHoverBorderColor,
    0 0 $v4CardShadowSize $v4CardShadowColor;
}

.faceFront {
  z-index: 2;
  transform: rotateY(0);
}

.faceBack {
  transform: rotateY(180deg);
}
