.statusContainer {
  @add-mixin cover;
  background-color: $v4CardHoverBackgroundColor;
}

.spinnerContainer {
  @add-mixin absoluteCenter;
  width: calc($v4CardCircleSize + 6px);
  height: calc($v4CardCircleSize + 6px);
}

.spinner {
  composes: spinner from '~desktop/common/js/v4/core/ui/spinners/components/Spinner.css';

  width: 100%;
  height: 100%;
}

.trashCircle {
  composes: cardCircle from './MetadataPosterCardCircle.css';

  background-color: $dangerBackgroundColor;
}
