.container {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: row;
  overflow: hidden;
}

.dayContainer {
  flex: 0 0 auto;
  width: $channelGridTitleColumnWidth;
}

.labelContainer {
  display: flex;
  justify-content: space-between;
  flex: 1 0 auto;
  margin-right: calc($channelGridChannelOverflowWidth * -1);
  padding: 20px 0 5px;
}

.label {
  display: inline-block;
  color: $channelGridTimelineLabelColor;
  text-align: left;
}

.navigationButtons {
  position: absolute;
  top: 15px;
  right: 5px;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex: 0 0 auto;
}

.navigationButton {
  width: 30px;
  text-align: center;
  text-shadow: $shadowColor 0 0 5px;
  font-size: 18px;

  &:global(.isDisabled) {
    color: $defaultLinkDisabledLinkColor;
  }
}
