//
// Background
// --------------------------------------------------

// Base Class

.background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: center;
  background-size: cover;
  .opacity(0.2);
  .transition(opacity 0.5s);

  &.dim {
    .opacity(0.1);
  }

  &.bright {
    .opacity(0.3);
  }

  &.transition-out {
    .opacity(0);
    .transition(none);
  }
}
