$padding: 80px;

.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.row {
  display: flex;
  flex-shrink: 0;
}

.poster {
  padding: calc($padding - $playerTopControlsHeight) 0 0 $padding;
}

.details {
  flex-grow: 1;
  padding: calc(
      $padding + $watchTogetherLobbyTitleHeight - $playerTopControlsHeight
    )
    $padding 0 60px;
}

.hubCell {
  composes: hubCell from './WatchTogetherParticipantsHubCell.css';

  padding: 0 $padding;
}
