.tooltipPortal {
  position: absolute;
  z-index: $tooltipZIndex;
  pointer-events: none;
}

.tooltip {
  @add-mixin resetFont;
  padding: 2px 10px;
  border-radius: 4px;
  background-color: $tooltipBackgroundColor;
  box-shadow: 0 4px 10px 0 $shadowColor;
  transform: translate(0, 0);
}

/*
 * Arrows
 */

.tooltipArrow::after {
  position: absolute;
  width: 0;
  height: 0;
  border: $tooltipArrowSize solid transparent;
  content: '';
}

.tooltipBottomArrow::after {
  bottom: calc($tooltipArrowSize * -2);
  border-top-color: $tooltipBackgroundColor;
}

.tooltipTopArrow::after {
  top: calc($tooltipArrowSize * -2);
  border-bottom-color: $tooltipBackgroundColor;
}

.tooltipRightArrow::after {
  right: calc($tooltipArrowSize * -2);
  border-left-color: $tooltipBackgroundColor;
}

.tooltipLeftArrow::after {
  left: calc($tooltipArrowSize * -2);
  border-right-color: $tooltipBackgroundColor;
}

.tooltipCenterAlignArrow::after {
  left: 50%;
  transform: translateX(-50%);
}

.tooltipLeftAlignArrow::after {
  left: calc($tooltipArrowSize * 2);
}

.tooltipRightAlignArrow::after {
  right: calc($tooltipArrowSize * 2);
}

.tooltipMiddleAlignArrow::after {
  top: 50%;
  transform: translateY(-50%);
}

.topLeft {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipBottomArrow;
  composes: tooltipLeftAlignArrow;

  margin-bottom: $tooltipArrowSize;
}

.topCenter {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipBottomArrow;
  composes: tooltipCenterAlignArrow;

  margin-bottom: $tooltipArrowSize;
}

.topRight {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipBottomArrow;
  composes: tooltipRightAlignArrow;

  margin-bottom: $tooltipArrowSize;
}

.bottomLeft {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipTopArrow;
  composes: tooltipLeftAlignArrow;

  margin-top: $tooltipArrowSize;
}

.bottomCenter {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipTopArrow;
  composes: tooltipCenterAlignArrow;

  margin-top: $tooltipArrowSize;
}

.bottomRight {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipTopArrow;
  composes: tooltipRightAlignArrow;

  margin-top: $tooltipArrowSize;
}

.middleLeft {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipRightArrow;
  composes: tooltipMiddleAlignArrow;

  margin-right: $tooltipArrowSize;
  transform-origin: center right;
}

.middleRight {
  composes: tooltip;
  composes: tooltipArrow;
  composes: tooltipLeftArrow;
  composes: tooltipMiddleAlignArrow;

  margin-left: $tooltipArrowSize;
  transform-origin: center left;
}

.topLeft,
.topCenter,
.topRight {
  transform-origin: center bottom;
}

.bottomLeft,
.bottomCenter,
.bottomRight {
  transform-origin: center top;
}
