.addDeviceButton {
  margin-left: 15px;
}

.manualDeviceButtonContainer {
  margin-bottom: 20px;
}

.manualDeviceContainer {
  margin-bottom: 25px;
}

a,
button {
  &:hover .manualAddressArrow {
    border-color: $defaultLinkHoverColor;
  }
}

.manualDeviceInput {
  composes: input from '~core/components/forms/TextInput.css';

  width: 300px;
  height: 41px;
}

.deviceCount {
  margin-bottom: 10px;
  color: $headerColor;
  text-transform: uppercase;
  font-family: $boldFontFamily;
}

.devices {
  display: flex;
}

.device {
  flex-shrink: 0;
  margin-right: 30px;
}

.spinnerContainer {
  padding: 60px;
  text-align: center;
}
