.titleContainer {
  padding-top: $posterTitlePadding;
  text-align: center;
}

.imageContainer {
  position: relative;
}

.imageLink {
  @add-mixin cover;
  z-index: 1;
  /* Hide the tag text from the link */
  text-indent: -9999px;

  &:hover {
    box-shadow: 0 0 0 2px $primaryColor;
  }
}

.isPerson {
  border-radius: 50%;
}
