.container {
  composes: bar from '~desktop/common/js/v4/core/ui/buttons/components/ActionButtonBar.css';

  position: relative;
  display: flex;
  align-items: center;
  margin-top: 40px;
  width: 100%;
  white-space: nowrap;

  &:not(:last-of-type) {
    margin-bottom: 40px;
  }
}

.addIcon {
  composes: icon from '~desktop/common/js/v4/core/ui/buttons/components/ActionButton.css';

  font-size: 14px;
}

.menu {
  composes: menu from '~core/components/menus/Menu.css';

  margin-top: 5px;
}

.disabledUserRating {
  composes: starRating from '~desktop/common/js/views/v3/components/core/StarRating.css';

  opacity: 0.45;
  pointer-events: none;
}
