.container {
  display: flex;
  justify-content: stretch;
}

.title {
  composes: container from './ChannelsGridRowTitle.css';

  flex: 0 0 auto;
  width: $channelGridTitleColumnWidth;
}

.programsContainer {
  position: relative;
  height: $channelGridRowHeight;
  white-space: nowrap;
}

.program {
  composes: container from './ChannelsGridMetadataItemCell.css';

  display: inline-block;
  height: 100%;
}

.progress {
  position: absolute;
  top: 1px;
  right: 0;
  bottom: 1px;
  left: 0;
}
