//
// Details
// --------------------------------------------------


// Containers
// --------------------------------------------------

.details-container {
  height: 100%;
}

.details-row {
  height: 100%;
}

.details-title-container {
  position: absolute;
  top: 20%;
  right: 0;
  left: 0;
  padding: 0 40px 0 (@poster-details-width + 80);
  margin-top: -50px;
  .clearfix();

  > h1,
  > h2,
  > h3 {
    margin: 0;
    text-shadow: 0 1px 2px @black;
    .text-overflow();
  }

  > h1 {
    float: left;
    max-width: 70%;

    a {
      color: @gray-lightest;
    }
  }

  > h2,
  > h3 {
    .h1;
    float: right;
    max-width: 30%;
    color: @gray-light;

    a {
      color: @gray-light;
    }
  }
}

.details-metadata-container {
  position: absolute;
  top: 20%;
  right: 0;
  left: 0;
  min-height: 80%;
  padding: 14px 40px 40px (@poster-details-width + 80);
  #gradient > .vertical-three-colors(@start-color: rgba(0, 0, 0, 0.4); @mid-color: rgba(19, 19, 19, 0.4); @color-stop: 50%; @end-color: rgba(26, 26, 26, 0.4));
}

.details-poster-container {
  position: absolute;
  top: 8%;
  left: 40px;
  width: @poster-details-width;

  > .btn {
    width: 100%;
    margin-top: 15px;
  }
}

.metadata-summary {
  @max-lines: 3;

  display: block;
  max-height: @line-height-computed * @max-lines;
  margin: 30px 0 12px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
  white-space: pre-wrap;
  .transition(max-height 0.2s ease-in-out);

  p {
    // This needs to be predictable for the summary component to compute its
    // collapsed height correctly.
    margin-bottom: @line-height-computed;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.summary-container {
  margin-bottom: 30px;
  clear: both;
}

.summary-divider {
  @summary-divider-bg: rgba(255, 255, 255, 0.08);
  @summary-divider-bg-hover: rgba(255, 255, 255, 0.1);

  text-align: right;
  border-top: 2px solid @summary-divider-bg;

  > .summary-divider-btn {
    min-width: 100px;
    padding: 4px 8px;
    color: @gray-light;
    background-color: @summary-divider-bg;
    border: 0;

    &:hover {
      color: @white;
      background-color: @summary-divider-bg-hover;
    }

    &:focus {
      outline: 0;
    }
  }
}

// Posters
// --------------------------------------------------

.details-poster-container {
  .media-poster {
    width: 100%;
  }

  .movie-poster,
  .show-poster {
    height: @poster-details-standard-height;
  }

  .artist-poster {
    background-position: center top; // Show the artist's eyes
    height: @poster-details-square-height;
  }

  .album-poster {
    height: @poster-details-square-height;
  }

  .playlist-poster {
    width: @poster-playlist-audio-width;
    height: @poster-playlist-audio-height;
    background-color: transparent;
    .box-shadow(none);

    &.video {
      width: @poster-playlist-video-width;
      height: @poster-playlist-video-height;
    }
  }
  .playlist-side-strip {
    position: absolute;
    width: @poster-playlist-audio-width;
    top: @poster-playlist-audio-height;
    bottom: 0;
    background-position: top left;
    background-repeat: no-repeat;
    background-size: 100%;
    background-color: transparent;
    .box-shadow(none);

    &.video {
      top: 0;
      width: @poster-playlist-video-width;
    }
  }

  .episode-poster,
  .clip-poster {
    height: @poster-details-widescreen-height;
  }
}

// Inline loading indicator
// --------------------------------------------------

.details-metadata-container .loading-container {
  position: relative;
  width: 100%;
  height: 50px;
}



// Progress Bar
// --------------------------------------------------

.details-progress-container {
  position: absolute;
  top: 0;
  right: 0;
  left: @poster-details-width + 40;
}

.details-progress {
  height: 2px;
  background-color: @orange;
}


// Rating
// --------------------------------------------------

.rating-container {
  margin-bottom: 30px;
}

.rating-container-inline {
  display: inline-block;
  margin: 0 20px 0 0;
}


// Flags
// --------------------------------------------------

.content-rating-flag-container {
  margin-bottom: 30px;

  .content-rating-flag {
    max-width: 70px;
    max-height: 30px;
  }
}

.content-rating-flag-container-inline {
  display: inline-block;
  margin: 0 20px 0 0;
  vertical-align: top;

  .content-rating-flag {
    max-width: 70px;
    max-height: 25px;
  }
}

.studio-flag-container {
  min-height: 40px;
  line-height: 0;
  text-align: right;

  .studio-flag {
    max-width: 200px;
    max-height: 40px;
  }
}

.video-audio-flags-container {
  min-height: 20px;
  margin-top: 30px;
  line-height: 0;
  text-align: center;

  .media-flag {
    max-width: 100px;
    max-height: 20px;
    margin: 0 10px;
  }
}


// Movie
// --------------------------------------------------

.movie-details-metadata-container {
  min-height: 340px;
  margin-bottom: 40px;
}

.extras-region {
  .clearfix();
  margin-left: -(@poster-details-width + 40);
}


// Show
// --------------------------------------------------

.show-details-metadata-container {
  min-height: 300px;
  margin-bottom: 40px;
}

.season-list-container {
  margin-left: -(@poster-details-width + 40);
}


// Season
// --------------------------------------------------

.season-details-row > .details-title-container {
  padding-left: 40px;
}

.season-details-row > .details-metadata-container {
  @container-padding: 40px;
  padding-left: @container-padding;

  .item-summary,
  .episode-list-container {
    margin-top: 10px;
  }

  .episode-list,
  .episode-details-list {
    padding-left: 0;
    margin-top: -10px;
    margin-left: -@container-padding;
    margin-right: -@container-padding;
  }

  .episode-list-item > a,
  .episode-list-item > .episode-list-inner-item {
    padding-left: @container-padding;
    padding-right: @container-padding;
  }
}


// Episode
// --------------------------------------------------

.episode-details-row > .details-title-container {
  margin-top: -95px;

  > h2 {
    float: left;
    max-width: 70%;
    clear: left;
  }
}

.episode-details-row .metadata-labels {
  margin-bottom: 30px;
}


// Artist
// --------------------------------------------------

.artist-details-metadata-container {
  min-height: 160px;
  margin-bottom: 20px;
}

.concert-region,
.popular-tracks-region,
.albums-region,
.extras-region,
.similar-artists-region {
  .clearfix();
  margin-left: -(@poster-details-width + 40);

  > h3 {
    margin-top: 0;
  }
}

.popular-tracks-region {
  margin-bottom: 40px;
}

.concert-region {
  margin-bottom: 40px;
}

.concert-api-logo {
  display: inline-block;
  margin-bottom: 3px;
  margin-left: 20px;
  width: 75px;
  height: 25px;
  background-image: url(~desktop/common/img/vendor-logos/songkick.svg);
  background-size: contain;
  background-repeat: no-repeat;
  vertical-align: bottom;
}

.concert-link {
  display: inline-block;
}

.concert-calendar {
  float: left;
  width: 50px;
  font-family: @font-family-bold;
  text-align: center;
}

.concert-calendar > .concert-month {
  font-size: @font-size-small;
  color: @white;
  text-transform: uppercase;
  background: @brand-danger;
}

.concert-calendar > .concert-day {
  padding: 0 8px;
  font-size: @font-size-large + 4;
  color: @gray-darker;
  background: @white;
}

.concert-details {
  float: left;
  margin-left: 20px;
}

.concert-name {
  margin-bottom: 0;
  font-size: @font-size-large;
  color: @gray-lightest;
}

.concert-location {
  color: @gray-light;
}


// Album
// --------------------------------------------------

.album-details-row > .details-title-container {
  margin-top: -95px;

  > h1,
  > h2 {
    float: left;
    max-width: 85%;
    clear: left;
  }
}

.album-details-row > .details-metadata-container {
  .track-list-container {
    clear: both;
  }
}


// Photo Album
// --------------------------------------------------

.photo-album-details-row > .details-title-container {
  padding-left: 40px;
}

.photo-album-details-row > .details-metadata-container {
  padding-left: 40px;

  .photo-album-summary,
  .photo-list-container {
    margin-top: 10px;
  }
}


// Playlist
// --------------------------------------------------

.playlist-details-metadata-container {
  margin-bottom: 40px;
}

.playlist-details-row > .details-title-container,
.playlist-details-row > .details-metadata-container{
  padding-left: @poster-playlist-audio-width + 80px;
  &.video {
    padding-left: @poster-playlist-video-width + 80px;
  }
}

.playlist-details-row > .details-poster-container {
  position: fixed;
  top: 150px;
  bottom: 0px;
  left: 100px;
  width: @poster-playlist-audio-width;
  &.video {
    width: @poster-playlist-video-width;
  }
}
