// Icons
// -------------------------

.dropdown > .cast-btn.active,
.dropdown > .dropdown-toggle.active {
  color: @orange;

  &:hover,
  &:focus {
    color: @link-hover-color;
  }
}

.dropdown.open > .dropdown-toggle.active {
  color: @orange-light;
}

.secure-connection-icon {
  margin: 3px 0 0 5px;
  vertical-align: top;
  color: @brand-success;
}

// Images
// -------------------------

.plex-image {
  display: inline-block;
  vertical-align: baseline;
  background-repeat: no-repeat;
  background-position: center;
}

.plex-logo {
  background-image: url(~common/img/plex-logo.svg);
}

.plex-image-preloader.plex-logo {
  background-size: 25%;
  background-position: center;
  background-repeat: no-repeat;
}

.plex-image.plex-logo {
  width: 200px;
  height: 65px;
  background-size: 200px 65px;
}

.plex-image.server {
  width: 250px;
  height: 250px;
  background-image: url(~common/img/pms-logo.svg);
  background-size: 250px 250px;

  &.server-sm {
    width: 100px;
    height: 100px;
    background-size: 100px 100px;
  }

  &.server-xs {
    width: 60px;
    height: 60px;
    background-size: 60px 60px;
  }
}

.plex-image.plex-pass {
  width: 250px;
  height: 114px;
  background-image: url(~common/img/plex-pass.svg);
  background-size: 250px 114px;

  &.plex-pass-sm {
    width: 114px;
    height: 52px;
    background-size: 114px 52px;
  }
}

// Devices
// -------------------------

@device-icon-size: 100px;

.device-icon {
  width: @device-icon-size;
  height: @device-icon-size;
  background-color: rgba(0, 0, 0, 0.3);
}
