.listItem {
  @add-mixin resetFont;

  display: flex;
  align-items: center;
  justify-content: space-between;
  height: $homeServerListItemHeight;
  background-color: $sortableHomeHubsRowBackgroundColor;
  color: $dimColor;
  /* Prevent drag-and-drop missed clicks from selecting text */
  user-select: none;
}

.label {
  @add-mixin truncate;
}

.title {
  color: $defaultColor;
  text-transform: uppercase;
  font-weight: bold;
}

.isRemoved .label {
  text-decoration: line-through;
  opacity: $dragPreviewLowOpacity;
  text-decoration-color: $quietColor;
}

.iconReorder {
  /* The top: -2px; causes vertical icon misalignment. */
  top: 0 !important;
  flex-shrink: 0;
  margin: 10px 20px;
  cursor: grab;
  transition: color $defaultSpeed;
}

.iconReorder:hover {
  color: $defaultColor;
}

.toggleButton {
  composes: button from '~desktop/common/js/views/v3/components/core/buttons/AddOrRemoveButton.css';

  flex-shrink: 0;
  margin-left: auto;
  padding: 10px 15px;
  opacity: 0;
}

.listItem:hover {
  .toggleButton {
    opacity: 1;
  }
}
