//
// Animations
// --------------------------------------------------


// Loading Spinner
// ------------------------

// Webkit
@-webkit-keyframes loading {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

// Firefox
@-moz-keyframes loading {
  from {
    -moz-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -moz-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

// Opera
@-o-keyframes loading {
  from {
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

// Spec and IE10+
@keyframes loading {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


// Barberpole Stripes
// ------------------------

// Webkit
@-webkit-keyframes barberpole-stripes {
  from { background-position: 0 0; }
  to { background-position: 30px 0; }
}

// Firefox
@-moz-keyframes barberpole-stripes {
  from { background-position: 0 0; }
  to { background-position: 30px 0; }
}

// Opera
@-o-keyframes barberpole-stripes {
  from { background-position: 0 0; }
  to { background-position: 30px 0; }
}

// Spec and IE10+
@keyframes barberpole-stripes {
  from { background-position: 0 0; }
  to { background-position: 30px 0; }
}


// Fade Out
// ------------------------

// Webkit
@-webkit-keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

// Firefox
@-moz-keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

// Opera
@-o-keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

// Spec and IE10+
@keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}


// Fade In
// ------------------------

// Webkit
@-webkit-keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

// Firefox
@-moz-keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

// Opera
@-o-keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

// Spec and IE10+
@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}


// Shake
// ------------------------

// Webkit
@-webkit-keyframes shake {
  0%, 100% {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  20%, 60% {
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  40%, 80% {
    -webkit-transform: translateX(10px);
    transform: translateX(10px);
  }
}

// Firefox
@-moz-keyframes shake {
  0%, 100% {
    -moz-transform: translateX(0);
    transform: translateX(0);
  }
  20%, 60% {
    -moz-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  40%, 80% {
    -moz-transform: translateX(10px);
    transform: translateX(10px);
  }
}

// Opera
@-o-keyframes loading {
  0%, 100% {
    -p-transform: translateX(0);
    transform: translateX(0);
  }
  20%, 60% {
    -p-transform: translateX(-10px);
    transform: translateX(-10px);
  }
  40%, 80% {
    -p-transform: translateX(10px);
    transform: translateX(10px);
  }
}

// Spec and IE10+
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  20%, 60% {
    transform: translateX(-10px);
  }
  40%, 80% {
    transform: translateX(10px);
  }
}
