$disclosureArrowButtonPadding: 5px;

.disclosureArrowButton {
  @add-mixin truncate;
  composes: link from '~core/components/buttons/Link.css';

  position: relative;
  padding-right: 13px;
  min-width: 30px;

  &.isSelected {
    font-family: $boldFontFamily;
  }

  &.small {
    padding-right: calc($disclosureArrowButtonPadding + $smallArrowWidth);
  }

  &.medium {
    padding-right: calc($disclosureArrowButtonPadding + $mediumArrowWidth);
  }

  &.large {
    padding-right: calc($disclosureArrowButtonPadding + $largeArrowWidth);
  }
}

.disclosureArrow {
  composes: disclosureArrow from '~core/components/base/DisclosureArrow.css';

  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);

  &.down {
    transform: translate(0, -50%) rotateX(180deg);
  }

  &.left {
    transform: translate(0, -50%) rotateY(180deg);
  }

  /* Add specifity to override the DisclosureArrow style. */
  &.small,
  &.medium,
  &.large {
    &.up,
    &.right,
    &.left,
    &.down {
      margin: 0;
    }
  }
}
