.button {
  composes: button from '~core/components/buttons/IconButton.css';

  /* Transition the kind-specific Link colors. */
  transition: color $defaultSpeed;
}

.icon {
  top: 0;
  transition: transform $defaultSpeed ease-out, opacity $defaultSpeed;
  transform: rotate(0);
}

.iconRemovable {
  composes: icon;

  transform: rotate(-135deg);
}
