//
// Lists
// --------------------------------------------------

// Base Styles
// --------------------------------------------------

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.tile-list {
  margin: 0;
  padding: 0;
  list-style: none;
  .clearfix();

  > li {
    float: left;
  }
}

// Striped List
// --------------------------------------------------

.striped-list-item > a,
.striped-list-item > .striped-list-inner-item,
.striped-list-item .striped-list-inner-item > a {
  position: relative;
  display: block;
  padding: 5px 20px;
  color: @gray-lighter;
}

.striped-list-item .striped-list-inner-item > a {
  padding: 5px 20px;
}

.striped-list-item:nth-child(even) {
  > a,
  > .striped-list-inner-item {
    background-color: rgba(0, 0, 0, 0.1);
  }
}

.striped-list-item > a:hover,
.striped-list-item > a:active,
.striped-list-item > a.selected,
.striped-list-item.force-hover > a,
.striped-list-item.force-hover > .striped-list-inner-item > a,
.striped-list-item > .striped-list-inner-item > a:hover,
.striped-list-item > .striped-list-inner-item > a:active,
.striped-list-item > .striped-list-inner-item > .selected {
  background-color: rgba(255, 255, 255, 0.052);
  color: @gray-lightest;
}

.striped-list-item > a.selected,
.striped-list-item > .striped-list-inner-item > .selected {
  padding-left: 17px;
  border-left: 3px solid @orange-light;
}

// Label List
// --------------------------------------------------

.label-list {
  margin: 8px 0 -4px;
  .disabled-list();
}

.label-list-item {
  margin-bottom: 4px;

  .label-truncate {
    display: inline-block;
    margin-top: 5px;
    max-width: 148px;
    vertical-align: top;
    .text-overflow();
  }
}

// Side Bar List
// --------------------------------------------------

.side-bar-list {
  .disabled-list();

  a {
    display: block;
    padding: 2px 10px;
    border-radius: @border-radius-base;

    &:hover,
    &:focus {
      background-color: rgba(255, 255, 255, 0.05);
    }

    .selected-icon {
      margin-left: 5px;
    }
  }
}

// Sortable
// ------------------------

.sortable-ghost {
  visibility: hidden;
}

// Alphanumeric Bar List
// --------------------------------------------------

.alpha-bar-list-item > a {
  display: block;
  width: 100%;
  color: #666;
  text-align: center;
}

// Select List
// --------------------------------------------------

.select-list {
  margin: -20px;
}

.select-list-item {
  &:last-child a {
    border-radius: 0 0 @border-radius-base @border-radius-base;
  }

  a {
    position: relative;
    display: block;
    padding: 0 20px 0 0;
    height: 50px;
    color: @gray-light;
    text-indent: 70px;
    font-size: @font-size-large;
    line-height: 50px;
    .text-overflow();

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 50px;
      height: 50px;
      border-right: 1px solid rgba(255, 255, 255, 0.1);
      background-color: @black;
      background-color: rgba(0, 0, 0, 0.2);
      color: rgba(0, 0, 0, 0.2);
      content: '';
      text-align: center;
      text-indent: 0;
      font-family: 'Glyphicons Regular';
    }

    &:hover,
    &:focus {
      background-color: @orange;
      color: @white;

      &::before {
        border-right: 1px solid rgba(0, 0, 0, 0.1);
        content: '\E207';
      }
    }

    &:active,
    &.selected {
      padding: 0;
      border: 0;
      background: spin(darken(@orange, 2%), -5);
      color: @white;

      &::before {
        border-right: 1px solid rgba(0, 0, 0, 0.1);
        color: @white;
        content: '\E207';
      }
    }

    .title-separator {
      margin: 0 5px;
      color: rgba(255, 255, 255, 0.4);
    }
  }
}

.download-list-item {
  a {
    &:hover,
    &:focus {
      &::before {
        content: '\E182';
      }
    }

    &:active,
    &.selected {
      &::before {
        content: '\E182';
      }
    }
  }
}

.select-collections-list,
.select-playlist-list,
.select-friends-list,
.select-networks-list {
  overflow-y: auto;
  max-height: 250px;

  > li {
    a {
      .text-overflow();
    }
    &:last-child a {
      border-radius: 0;
    }

    .media-poster {
      display: inline-block;
      margin-right: 5px;
      margin-bottom: 4px;
      vertical-align: middle;
    }
  }
}

.select-friends-list > li,
.select-networks-list > li {
  .user-poster {
    width: 30px;
    height: 30px;
  }

  .network-icon,
  .network-icon::before {
    display: inline;
    width: auto;
  }
}

.select-playlist-list > li {
  .playlist-poster {
    width: 40px;
    height: 40px;
  }
}

// Browse Directory List
// --------------------------------------------------

.browse-directory-list-item {
  .disabled-list();

  .folder-icon {
    display: inline;
    margin-right: 5px;
  }

  .loading {
    right: 10px;
    left: auto;
    border-top-color: @white;
    border-left-color: @white;
  }

  > a {
    display: -webkit-box;
    overflow: hidden;
    padding: 5px 20px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
}

.css-file-list-truncated-message {
  margin-left: 20px;
  opacity: 0.5;
}

// Alert List
// --------------------------------------------------

.alert-list-item {
  .server-alert-time {
    display: inline-block;
    width: 100px;
    color: @gray-light;
    text-align: center;
  }

  .server-alert-icon {
    top: 5px;
    float: left;
    color: @gray-light;
  }

  .alert-list-inner-item {
    padding: 5px 20px;
  }
}

// Modal List
// --------------------------------------------------

.modal-list {
  margin: -20px -20px 20px;
  padding: 5px 20px 6px;
  background-color: #222;
  .clearfix();
}

.modal-list-item {
  float: left;
  padding-right: 20px;
}

// Match Result List
// --------------------------------------------------

.match-result-list-item {
  > .match-result {
    position: relative;
    padding: 10px;
    font-size: @font-size-large;

    > .row {
      margin: 0;
    }

    .striped-list-inner-item {
      padding: 5px 0;
    }

    .match-index {
      padding-right: 4px;
      padding-left: 0;
      color: @gray-alt;
      text-align: right;
      font-size: 12px;
      &.missing {
        color: @gray;
      }
    }

    .match-title-info {
      color: @gray-alt;
      .text-overflow();
    }

    .match-score-info {
      color: @gray-alt;
      text-align: right;
    }

    .match-extended-info {
      margin-top: 12px;
    }

    .match-name {
      color: @gray-lightest;
      font-weight: bold;
    }

    .match-separator {
      margin: 0 5px;
    }

    .match-disambiguation-info {
      color: @gray-alt;
      font-size: @font-size-small;
    }

    .match-score-icon {
      margin-right: 2px;
    }

    .match-track-name {
      font-size: @font-size-small;

      &.missing {
        color: @gray-alt;
      }
    }

    .match-art {
      float: right;
      max-width: 100%;
      max-height: 150px;
    }

    .match-summary {
      overflow: hidden;
      max-height: 150px;
      font-size: @font-size-base;
    }
  }
}

// Sync Status List
// --------------------------------------------------

.sync-status-list {
  margin: 30px 0 0 15px;
}

.sync-status-list-item {
  margin-bottom: 10px;
  .clearfix();

  &.movie,
  &.show,
  &.season {
    .media-poster {
      width: @poster-sync-status-list-standard-width;
    }
  }

  &.artist,
  &.album,
  &.track,
  &.photo,
  &.photoalbum,
  &.channel,
  &.channelDirectory,
  &.device {
    .media-poster {
      width: @poster-sync-status-list-square-width;
    }
  }

  &.episode,
  &.clip {
    .media-poster {
      width: @poster-sync-status-list-widescreen-width;
    }
  }

  .media-poster {
    float: left;
    margin-right: 15px;
    height: @poster-sync-status-list-height;
  }

  p {
    margin: 2px 0 0;
    max-width: 80%;
    .text-overflow();
  }

  .subtitle {
    color: @gray-light;
  }
}

// Play Queue List
// --------------------------------------------------

.play-queue-list {
  .play-queue-header {
    max-width: 350px;

    .btn-gray {
      float: right;
    }
  }

  .play-queue-header,
  > li > a {
    padding: 4px 10px;
  }

  &.readonly {
    button {
      display: none;
    }
  }

  &.sortable-list > .play-queue-list-item {
    button {
      visibility: hidden;
    }

    > a:hover {
      background-color: transparent;
    }
  }
}

.play-queue-list-item {
  position: relative;
  font-size: @font-size-small;

  .media-poster {
    display: inline-block;
    margin-right: 5px;
    width: 40px;
    height: 40px;
    vertical-align: middle;
    &.episode,
    &.clip,
    &.movie {
      width: 70px;
    }
  }

  .media-item {
    display: inline-block;
    padding-right: 20px;
    width: 230px;
    vertical-align: middle;

    > .media-title {
      color: @gray-lighter;
      font-family: @font-family-bold;
      .text-overflow();
    }

    > .media-subtitle-1 {
      color: @gray-light;
      .text-overflow();
    }
  }

  .btn-link {
    position: absolute;
    right: 0;
    visibility: hidden;
    padding: 10px;
  }

  &:hover .btn-link {
    visibility: visible;
  }
}

.play-queue-empty-message {
  padding: 14px;
  color: @gray-alt;
  text-align: center;
}

// delete file list on info modal
ul.media-info-file-list li {
  padding-left: 10px;
}

// User Select List
// --------------------------------------------------

.user-select-list {
  margin-right: -20px;
}

.user-select-list-item {
  @poster-user-select-list-width: 80px;
  @poster-user-select-list-height: @poster-user-select-list-width *
    @poster-square-ratio;
  @user-select-top-padding: 20px;

  margin-right: 20px;
  margin-bottom: 20px;

  > .user-select-container {
    border-radius: @border-radius-base;
    background-color: @gray-darker;
    background-position: center;
    background-size: cover;
  }

  > a {
    display: block;

    &:hover {
      .user-poster {
        box-shadow: 0 0 0 4px @white;
      }

      .username {
        color: @white;
      }
    }
  }

  .user-select-top {
    position: relative;
    padding: @user-select-top-padding;

    .user-poster {
      display: flex;
      align-items: center;
      justify-content: center;
      width: @poster-user-select-list-width;
      height: @poster-user-select-list-height;
      box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.1);
    }

    .user-icon {
      position: absolute;
      top: 94px;
      padding: 5px 5px 5px 6px; // Nudge the icon over 1px.
      border-radius: 50%;
      background-color: rgba(0, 0, 0, 0.5);
      font-size: @font-size-small;

      &.protected-icon {
        left: 5px;
        color: @brand-success;
      }

      &.admin-icon {
        right: 5px;
        color: @orange-light;
      }
    }

    .add-icon {
      top: 0;
      display: flex;
      font-size: 50px;
    }
  }

  .caption {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 5px;
    width: @poster-user-select-list-width + @user-select-top-padding * 2;
    height: 50px;
    background: rgba(0, 0, 0, 0.5);
    text-align: center;
  }

  .username {
    color: @gray-light;
    .text-overflow();
  }

  .managed-title {
    color: @gray;
    font-size: @font-size-small;
    .text-overflow();
  }
}
