.container {
  @add-mixin stripeListItem;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px 0 0;
  height: $playQueueItemHeight;

  &:hover {
    .metadataPosterPlayButton {
      opacity: 1;
    }
  }
}

.underlay {
  @add-mixin cover;
}

.metadataContainer {
  composes: container from './GridAlsoAiringItemMetadata.css';
}

.metadataPosterPlayButton {
  composes: playButton from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterButtonCard.css';

  transition: opacity $defaultSpeed;
}
