.badge {
  padding: 0 8px;
  border-radius: 4px;
  white-space: nowrap;
  transition: all $defaultSpeed;
}

.isHidden {
  opacity: 0;
  transform: scale(0.5);
}

.default {
  background-color: $badgeBackgroundColor;
}

.primary {
  background-color: $primaryBackgroundColor;
}

.success {
  background-color: $successBackgroundColor;
}

.danger {
  background-color: $dangerBackgroundColor;
}
