//
// Metadata labels and text
// --------------------------------------------------

.metadata-labels {
  font-size: @font-size-large;

  .metadata-duration,
  .label {
    margin-right: 20px;
  }
}

.btn-metadata-link {
  &:hover,
  &:focus {
    color: @white;
    #gradient > .vertical(transparent, rgba(255, 255, 255, 0.4), 49%, 50%);
    background-position: 0 1.2em;
    background-repeat: repeat-x;
    background-size: 1px 2px;
  }
}

.metadata-tagline {
  margin: 30px 0;
  font-family: @font-family-bold;
  font-size: @font-size-large + 2;
  color: @gray-light;
}

.metadata-tags {
  font-size: @font-size-large;
}

.metadata-tags > a:not(.dropdown-toggle),
.metadata-tag-list > a:not(.dropdown-toggle) {
  .btn-metadata-link;
  color: @gray-lightest;
}

.metadata-right {
  text-align: right;
}

.metadata-right > .metadata-tags {
  margin-bottom: 30px;
}

.metadata-tags-separator {
  margin-top: 15px;
}

.metadata-label {
  margin-right: 10px;
  color: @gray-light;
  text-transform: uppercase;
}
