.item {
  @add-mixin stripeListItem;
  display: flex;
  align-items: center;
  padding: 10px 20px;
  width: 100%;

  &:hover {
    .downloadIcon {
      visibility: visible;
    }
  }
}

.titles {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  margin-right: 10px;
  min-width: 0;
}

.title {
  @add-mixin truncate;
}

.secondaryTitle {
  composes: title;

  color: $dimColor;
}

.score {
  flex: 1 0 auto;
  text-align: right;
}

.perfectMatch {
  top: -1px;
  margin-right: 5px;
  color: $primaryColor;
}

.spinnerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-left: 20px;
  width: 20px;
  height: 20px;
}

.icon {
  top: 0;
  margin-left: 20px;
  width: 20px;
  height: 20px;
}

.downloadedIcon {
  composes: icon;

  color: $selectedColor;
}

.downloadIcon {
  composes: icon;

  visibility: hidden;
}
