.modalContent {
  composes: modalContent from '~desktop/common/js/v4/core/ui/modals/components/ModalContent.css';
}

.innerModalBody {
  composes: innerModalBody from '~desktop/common/js/v4/core/ui/modals/components/ModalBody.css';

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.inputGroup {
  margin-bottom: 10px;
  max-width: $inputMaxWidth;
  width: 100%;
}

.secretContent {
  display: flex;

  margin-bottom: 30px;
}

.verificationCodeContent {
  display: flex;
  flex-direction: column;
  padding-left: 30px;
}

.signOutContent {
  margin-top: auto;
}

.qrCodeContent {
  display: flex;
  flex-direction: column;
}

.textCode {
  color: $primaryColor;
}

.verificationCodeInput {
  margin-top: auto;
}

.qrCodePlaceholder {
  width: 200px;
  height: 200px;
}

.checkbox {
  composes: input from '~desktop/common/js/v4/core/ui/forms/components/Checkbox.css';

  margin: 10px 10px 10px 0 !important;
}

.help {
  composes: help from '~core/components/forms/FormHelp.css';

  width: 100%;
}
