.container {
  position: relative;
  flex-grow: 1;
  padding: $pagePadding;
  background-color: #222;
}

.containerWithTimeFilter {
  top: $pageHeaderHeight * 2;
}

.closeButton {
  composes: link from '~core/components/buttons/Link.css';

  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  width: 60px;
  height: 60px;
  text-align: center;
  font-size: 18px;
  line-height: 60px;
}

.action {
  margin-top: 30px;
}

.label {
  display: block;
  color: $dimColor;
}

/*
 * Levels
 */

.containerError {
  composes: container;

  box-shadow: 0 0 0 2px rgba(255, 85, 51, 0.75);
}

.containerWarning {
  composes: container;

  box-shadow: 0 0 0 2px rgba(249, 190, 3, 0.75);
}

.containerInfo {
  composes: container;

  box-shadow: 0 0 0 2px rgba(25, 160, 215, 0.75);
}

.containerDebug {
  composes: container;
}

.containerVerbose {
  composes: container;
}

.containerUnknown {
  composes: container;
}

.levelError {
  color: rgba(255, 85, 51, 0.75);
}

.levelWarning {
  color: rgba(249, 190, 3, 0.75);
}

.levelInfo {
  color: rgba(25, 160, 215, 0.75);
}
