.input {
  max-width: $inputMaxWidth;
  width: 100%;
  border: 0;
  border-radius: $inputBorderRadius;
  background-color: $inputBackgroundColor;
  color: $inputColor;
  vertical-align: middle;
  font-family: $defaultFontFamily;
  line-height: $lineHeightComputed;
  transition: background-color $defaultSpeed;

  &:focus {
    outline: 0;
    background-color: $inputFocusBackgroundColor;
    color: $inputFocusColor;
  }

  &::placeholder {
    color: $inputPlaceholderColor;
  }
}

.small {
  padding: 2px 10px;
  height: $smallSize;
  font-size: $defaultFontSize;
}

.medium {
  padding: 2px 10px;
  height: $mediumSize;
  font-size: $defaultFontSize;
}

.large {
  padding: 5px 10px;
  height: $largeSize;
  font-size: $largeFontSize;
}

.isDisabled {
  opacity: $inputDisabledOpacity;
  cursor: not-allowed;
}

.hasError {
  border: 2px solid $dangerColor;
  background-color: $errorBackgroundColor;
  color: $defaultColor;
}
