.progress {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 10px;
  border-radius: 10px;
}

.emptyBar {
  @add-mixin cover;
  background-color: rgba(0, 0, 0, 0.75);
}

.bar {
  @add-mixin cover;
  width: 100%;
  background-color: $primaryColor;
  transition: transform $fastSpeed ease-in-out;
}
