.upsell {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1 0 auto;
  flex-direction: column;
  padding: 30px;
  width: 100%;
}

.iconContainer {
  margin-bottom: 20px;
  padding: 25px;
  border-radius: 50%;
  background-color: $blackDarkerTranslucentBackgroundColor;
}

.icon {
  top: 0;
  width: 40px;
  height: 40px;
  color: $primaryColor;
}

.title {
  margin-bottom: 10px;
  text-align: center;
  font-size: 24px;
}

.text {
  max-width: 100%;
  color: rgba(255, 255, 255, 0.5);
  text-align: center;
  font-size: 18px;
}
