.gridContainer {
  display: flex;
  justify-content: stretch;
  flex: 1 0 auto;
  flex-direction: column;
}

.gridRowCell {
  composes: gridContainer;
}

.gridRow {
  composes: container from './ChannelsGridRow.css';

  flex: 0 0 auto;
}

.gridContent {
  position: relative;
  overflow: hidden;
  height: 100%;
}

.gridInnerContent {
  composes: scroller from '~core/components/lists/VirtualListScroller.css';

  height: 100%;
}

.timeline {
  composes: container from './ChannelsGridTimeline.css';

  flex: 0 0 auto;
}

.innerEmptyPage {
  composes: innerEmptyPage from '~desktop/common/js/views/v3/components/pages/empty/InnerEmptyPage.css';

  top: 40px;
}

.banner {
  composes: container from '~desktop/common/js/views/v3/components/livetv/guide/ProgramGuideGracenoteMigrationBanner.css';

  margin: 20px 0;
  width: calc(100% - 15px);
}
