.hub {
  padding: 10px;
  width: 50%;
  color: $defaultColor;
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  background: $playerSecondaryOverlayColor;
}

.body {
  top: 0;
  width: 100%;
  height: 100%;
}

.overlay {
  position: absolute;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.title {
  @add-mixin truncate;
  padding-top: 5px;
  text-transform: uppercase;
}

.count {
  padding-bottom: 5px;
}

.title,
.count {
  width: 100%;
  background: $playerPanelColor;
  text-align: center;
}

.icon {
  display: inline-block;
}
