.container {
  padding: 10px;
  width: 200px;
  background-color: $v4BaseMediumLight;
}

.action {
  composes: link from '~desktop/common/js/v4/core/ui/buttons/components/Link.css';

  margin-bottom: 10px;
}

.actionIcon {
  margin-right: 8px;
  margin-bottom: -1px;
  font-size: $defaultIconFontSize;
}

@media only screen and (max-width: $breakpointLarge) {
  .container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    width: 100%;
  }

  .action {
    margin-bottom: 0;
  }
}
