$lyricsSpacing: 20px;
$timerFontSize: 16px;
$borderRadius: 4px;

.measure {
  @add-mixin cover;
}

.container {
  display: flex;
  padding: $lyricsSpacing $pagePadding 0;
  width: 100%;
  height: 100%;
}

.card {
  composes: card from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCard.css';

  margin: 0 auto;
}

.content {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin-left: $pagePadding;
  width: 100%;
}

.titlesContainer {
  padding: 0 $lyricsSpacing $lyricsSpacing;
  border-bottom: 2px solid $dividerColor;
}

.rightInfo {
  margin-left: $lyricsSpacing;
}

.scroller {
  composes: vertical from '~core/components/base/Scroller.css';

  padding: $lyricsSpacing 0 40px 0;
  width: 100%;
  height: 100%;
}

.spinner {
  @add-mixin absoluteCenter;
  composes: spinner from '~core/components/base/Spinner.css';
}

.upsellContainer {
  @add-mixin absoluteCenter;
  max-width: 660px;
  text-align: center;
}

.innerContent {
  position: relative;
  flex-grow: 1;
  width: 100%;
  height: 100%;
}

.line {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 7px 0 $lyricsSpacing;
  min-height: $lyricsLineHeight;
  max-width: $lyricsLineWidth;
  font-size: $largeFontSize;

  .lineTimedIcon {
    visibility: hidden;
  }

  &.isLineSelected {
    border-radius: $borderRadius;
    background-color: $lightTranslucentBackgroundColor;

    .lineTimedIcon {
      visibility: visible;
    }
  }
}

.lineTimedIcon {
  top: 0;
  color: rgba(0, 0, 0, 0.3);
  font-size: $timerFontSize;
}

.timedLyricsButton {
  composes: button from '~core/components/buttons/IconButton.css';

  padding: 0 5px;
  border-radius: $borderRadius;
  font-size: $timerFontSize;
  transition: background $defaultSpeed, color $defaultSpeed;

  &.isTimedLyricsActive {
    background-color: $selectedColor;
    color: rgba(0, 0, 0, 0.75);

    &:hover {
      background-color: rgba(255, 255, 255, 0.75);
      color: rgba(0, 0, 0, 0.75);
    }
  }
}
