.container {
  display: flex;
  flex-direction: row;
}

.left {
  flex-basis: 130px;
  margin-right: 30px;
  min-width: 130px;
}

.right {
  flex-grow: 1;
  min-width: 0;
}

.subscriptionBadge {
  composes: badge from '~desktop/common/js/views/v3/components/metadata/posters/MetadataPosterCardOverlay.css';

  top: 0;
  right: 0;
  background-color: $recordBackgroundColor;
}

.subscriptionBadgeIcon {
  margin-right: 2px;
  font-size: 14px;
}

.titleContainer {
  margin-bottom: 24px;
}

.staticHeightTitleContainer {
  composes: titleContainer;

  height: 55px;
}
